const seriesList = {
    namespaced: true,
    state:{
        filterBrands:[],
        seriesList:[],
        listOffset:0,
        filterCategorySpecs:[]
    },
    getters:{
        listCurrpage:state=>{
            return state.listOffset + 1;
        }
    },
    mutations:{
        updateFilterBrands(state,data) {
            state.filterBrands = data;
        },
        updateSeriesList(state,data) {
            state.seriesList = data;
        },
        updatelistOffset(state, data) {
            state.listOffset = data;
        },
        updatefilterCategorySpecs(state, data) {
            // var verifyData = [{
            //     "spec_values":[
            //         "5",
            //         "6",
            //         "7",
            //         "8"
            //     ],
            //     "category_id":3,
            //     "category_level":0,
            //     "spec_name":"电流",
            //     "spec_unit":"A"
            // },{
            //     "spec_values":[
            //         "1",
            //         "2",
            //         "4",
            //         "6"
            //     ],
            //     "category_id":3,
            //     "category_level":0,
            //     "spec_name":"电功率",
            //     "spec_unit":"W"
            // },{
            //     "spec_values":[
            //         "3",
            //         "6",
            //         "9",
            //         "12"
            //     ],
            //     "category_id":3,
            //     "category_level":0,
            //     "spec_name":"电压",
            //     "spec_unit":"V"
            // }]
            state.filterCategorySpecs = data;
        }
    },
    actions:{
        updateFilterBrandsAsync(store,param) {// 更新品牌搜索
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + "/brands/all";
            that.$http({
                method:"get",
                url:url
            }).then((res)=>{
                var resCode = parseInt(res.data.rescode);
                if(resCode <= 10000) {
                    store.commit("updateFilterBrands", res.data.data);
                    param.success(res)
                } else {
                    param.fail(res)
                }
            }).catch((error) => {
                param.error(error)
            })
        },
        updateSeriesListAsync(store,param) {// 更新产品列表
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + "/products?" + param.params;
            that.$http({
                method:"get",
                url:url
            }).then((res)=>{
                var resCode = parseInt(res.data.rescode);
                if(resCode <= 10000) {
                    store.commit("updateSeriesList", res.data.data);
                    param.success(res);
                } else {
                    param.fail(res);
                }
            }).catch((error) => {
                param.error(error);
            })
        },
        updatefilterCategorySpecsAsync(store, param) { // 根据URL的分类id，请求对应ID的产品规格参数
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + "/categories/" + param.params + "/specs";
            that.$http.get(url).then((res) => {
                var resCode = parseInt(res.data.rescode);
                if(resCode <= 10000) {
                    store.commit('updatefilterCategorySpecs', res.data.data);
                    param.success(res);
                } else {
                    param.fail(res);
                }
            }).catch((error) => {
                param.error(error);
            })

        }
    }
}

export default seriesList;


