<template>
<div>
    <el-dialog
  :visible.sync="dialogVisible"
  width="60%"
  >
  <p class="payContent"><img src="../../assets/images/tu.png" alt="">确认支付 <strong>&yen;&nbsp;{{orderInfo.total_money}}</strong></p>
  <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="surePayNow" class="surePay">确 定</el-button>
  </span>
</el-dialog>
</div>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  props: {
    surePay: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      dialogVisible: false,
    };
  },
  methods: {
    ...mapActions({
      payMoneyNow: "payment/payMoneyNow"
    }),
    createForm(url, params, target) {
        // 创建form元素并设置其相关属性。参数url：表单提交去向；参数target：链接打开方式。
        const tempForm = document.createElement("form");
        tempForm.action = url;
        tempForm.method = "post";
        // tempForm.style.display = 'none';
        if (target) {
          tempForm.target = target;
        }
        // 创建input元素并设置提交的数据。参数params：提交的数据（JSON结构）。
        Object.keys(params).map((key, index) => {
          const ipt = document.createElement("input");
          ipt.name = Object.keys(params)[index];
          ipt.value = params[key];
          tempForm.appendChild(ipt);
          return null;
        });
        // 创建提交按钮元素
        const ipt = document.createElement("input");
        ipt.type = "submit";
        tempForm.appendChild(ipt);
        // 创建->提交->删除
        document.body.appendChild(tempForm);
        tempForm.submit();
        loader.close();
        document.body.removeChild(tempForm);
    },
    surePayNow() {
        var loader = this.$loading({ fullscreen: true });
      var redirectUrl = encodeURI(
        `${location.origin}/#/paymentSuccess`
      );
      var params = {
        order_no: this.orderInfo.order_sn,
        amount: this.orderInfo.total_money,
        redirect_url: redirectUrl,
        pay_method: "b2b",
        user_id: this.userInfo.id
      };
      this.payMoneyNow({
        params,
        success: function(res) {
          this.createForm(res.data.data.req_url,res.data.data.req_params);
        }.bind(this),
        fail: function(res) {
            var tipMsg = res.data.msg;
           const h = this.$createElement;
            this.$message({
              message: h('p', null, [
                h('span', null, tipMsg),
                h('i', { style: 'color: teal' })
              ])
            });
          loader.close();
        }.bind(this),
        error: function(error) {
          console.log(error);
          loader.close();
        }.bind(this)
      });
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.loginRegister.userInfo,
      orderInfo: state => state.order.orderInfo
    })
  },
  watch: {
    surePay: function(newvalue, oldvalue) {
      console.log(newvalue);
      if (newvalue) {
        this.dialogVisible = newvalue;
      }
    },
    dialogVisible: function(newvalue, oldvalue) {
      if (!newvalue) {
        this.$emit("changeSurePay", false);
      }
    }
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.payContent {
  height: 300px;
  line-height: 300px;
  text-align: center;
  font-size: 17*@fontSize;
  img {
    width: 100px;
    height: 100px;
    display: inline-block;
    vertical-align: middle;
    margin-right: 30*@fontSize;
  }
  strong {
    font-size: 23*@fontSize;
    margin-left: 8*@fontSize;
    color: @baseColor;
  }
  .surePay {
    background-color: @baseColor;
    border-color: @baseColor;
  }
}
</style>



