<template>
  <div class="container orderDetail">
      <div class="orderDetailTitle">
          <Title title="订单详情" englishTitle="" iconClass="icon-caidan"/>
          <span class="totalOrderNum">总订单号 : {{orderInfo.order_sn}}</span>
      </div>
      <div class="totalOrder">
          <ul>
              <li>
                  <span>下单时间 :</span> <span>{{orderTime}}</span>
              </li>
              <li>
                  <span>支付方式 :</span> <span>银联支付</span>
              </li>
              <li>
                  <span>收货信息 :</span> 
                  <span>{{orderInfo.receiver + " " + orderInfo.mobile + " " + orderInfo.province+orderInfo.city+orderInfo.district+" " + orderInfo.address}} </span>
              </li>
              <li>
                  <span>发票信息 :</span> <span>{{orderInfo.receipt_title||"您选择不开发票"}}</span>
              </li>
              <li>
                  <span>备注 :</span> <span>{{orderInfo.remarks}}</span>
              </li>
          </ul>
          <div class="totalMoney">
                <span>总金额 : </span><strong>&yen; {{orderInfo.total_money}}</strong> (含运费: &yen; 0.00)
                <el-button type="primary" class="paynow" @click="payNow">立即支付</el-button>
          </div>
          <div class="payStatus">
              支付状态 : <strong v-if="orderInfo.status == 1">待支付</strong>
          </div>
      </div>
      <div class="subOrder">
          <SubOrderDetail />
      </div>
      <SurePayMoney :surePay="surePay" @changeSurePay="changeSurePay"/>
  </div>
</template>
<script>
import Title from "../components/title/title.vue";
import SubOrderDetail from "../components/OrderDetail/SubOrderDetail.vue";
import SurePayMoney from '../components/SurePayMoney/SurePayMoney.vue';
import {mapState,mapActions} from 'vuex';
import moment from 'moment';
export default {
    components:{
        Title,
        SubOrderDetail,
        SurePayMoney
    },
    props:{
        id:{
            type:String,
            required:true
        }
    },
    data(){
        return {
            orderTime:"",
            receiptType:'',
            surePay:false
        }
    },
    mounted(){
        var loader = this.$loading({ fullscreen: true });
        this.updateorderInfoAsync({
            orderId: this.id,
            success:function(res){
                this.orderTime = this.formatTime(res.data.data.add_time*1000);
                if(res.data.data.receipt_type === 2) {
                    this.receiptType = "增值税发票";
                } else if (res.data.data.receipt_type === 3) {
                    this.receiptType = "您选择不开发票"
                }
                loader.close();
            }.bind(this),
            fail:function (res){
                const h = this.$createElement;
                this.$message({
                    message: h('p', null, [
                        h('span', null, '获取订单信息失败，请重试'),
                        h('i', { style: 'color: teal' },)
                    ]),
                    center: true
                })
                loader.close();
            },
            error:function (error) {
                const h = this.$createElement;
                this.$message({
                    message: h('p', null, [
                        h('span', null, '请求服务失败,请重试'),
                        h('i', { style: 'color: teal' },)
                    ]),
                    center: true
                })
                loader.close();
            }
        })
    },
    methods:{
        ...mapActions({
            updateorderInfoAsync:"order/updateorderInfoAsync"
        }),
        formatTime:function (time) {
			return moment(time).format("YYYY/MM/DD HH:mm:ss"); 
        },
        payNow(){
            this.surePay = true;
        },
        changeSurePay(){
            this.surePay = false;
        }
    },
    computed:{
        ...mapState({
            orderInfo:state=>state.order.orderInfo
        })
    }
    
}
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.orderDetailTitle {
    margin-top: 30*@fontSize;
    position: relative;
    .totalOrderNum {
        position: absolute;
        bottom: 0;
        right: 0;
        font-size: 16*@fontSize;
    }
}
.totalOrder {
    position: relative;
    // border-bottom: 1px solid #ddd;
    padding: 15*@fontSize 0;
    ul {
        border-bottom: 1px solid #ddd;
        padding-bottom: 20*@fontSize;
        li {
            height: 50*@fontSize;
            line-height: 50*@fontSize;
            font-size: 14*@fontSize;
            span {
                &:first-child {
                    margin-right: 10*@fontSize;
                }
            }
            strong {
                color: @baseColor;
                font-weight: normal;
                margin-right: 5*@fontSize;
            }
        }
    }
    .payStatus {
        position: absolute;
        top: 20*@fontSize;
        right: 0;
        font-size: 24*@fontSize;
        strong {
            font-weight: normal;
            color: @baseColor;
        }
    }
    .totalMoney {
        text-align: left;
        margin-bottom: 60*@fontSize;
        margin-top: 30*@fontSize;
        height: 40*@fontSize;
        line-height: 40*@fontSize;
        font-size: 18*@fontSize;
        strong {
            color: @baseColor;
            margin: 0 8*@fontSize ;
        }
        .paynow {
            background-color: @baseColor;
            border-color: @baseColor;
            float: right;
            border-radius: 0;
            font-size: 16*@fontSize;
            letter-spacing: 2px;
        }
    }
}
</style>



