<template>
    <div class="wrap">
        <h4 class="title">{{title}}</h4>
        <div class="brandLeft wrapper" v-cloak ref="wrapper">
            <div class="wrapper-box">
                <ul class="bscroll-container">
                    <li v-for='(item,idx) in brands' :key=idx @click='changeBrands(idx)' :class="[active==idx?'active':'']">
                        <img :src="require('../../../assets/images/' + item.img)"  alt="">
                    </li>
                </ul>
            </div>
            
        </div>
    </div>
</template>
<script>
import BScroll from "better-scroll";
export default {
  props: ["title"],
  created() {
    this.$nextTick(() => {
      const options = {
        scrollY: true,
        mouseWheel: {
            speed: 20,
            invert: false,
            easeTime: 300
        },
        scrollbar:{
            fade:false,
            interactive:true
        }
      };
     
      console.log('refs',this.$refs.wrapper)
      this.scroll = new BScroll(this.$refs.wrapper, options);
    });
  },
  mounted() {
    setTimeout(() => {
      this.scroll.refresh();
      console.log('5s')        
    }, 5000);
  },
  data(){
      return {
          brands:[
              {img:'ruking.png'},
              {img:'ama.png'},
              {img:'sanyo.png'},
              {img:'panasonic.png'},
          ],
          active:0,
      }
  },
  methods:{
      changeBrands(idx){
          this.active=idx;
      }
  }
};
</script>
<style lang="less" scoped>
.wrap {
  width: 100%;
  height: 100%;
  .title {
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-weight: 600;
    text-align: center;
    font-size: 17px;
    margin: 0;
  }
  .brandLeft {
    box-shadow: 0px 1px 4px 1px rgba(0, 0, 0, 0.08);
    height: 200px;
    position: relative;
    top: 20px;
    overflow: hidden;
    ul {
      background-color: #fff;
      li {
        height: 100px;
        line-height: 100px;
        padding-left: 12px;
        cursor: pointer;
        display: flex;
        align-items: center;
        img {
          width: 170px;
          height: 70px;
          max-width: 100%;
          margin: 0;
        }
      }
       li.active{
           background-color: rgba(2,117,216,0.03)!important;
       }
    }
  }
}
</style>



