<template>
  <div class="subOrderInfo">
        <div class="subOrder" v-for="(item, index) in orderInfo.sub_order" :key="index">
            <p>
                子订单号: <strong>{{item.order_sn}}</strong>
            </p>
            <el-row class="suborderTitle">
                <el-col :span="9">
                    商品详情
                </el-col>
                <el-col :span="3">
                    数量
                </el-col>
                <el-col :span="3">
                    单位
                </el-col>
                <el-col :span="3">
                    单价
                </el-col>
                <el-col :span="3">
                    发货日
                </el-col>
                <el-col :span="3">
                    发货状态
                </el-col>
            </el-row>
            <el-row class="suborderContent">
                <el-col :span="9">
                    <div class="subOrderDetail">
                        <h5>{{item.goods_name}}</h5>
                        <div><span>魔方订货号 :</span> {{item.goods_sn}}</div>
                    </div>
                </el-col>
                <el-col :span="3" class="lineHeight_150">
                    {{item.number}}
                </el-col>
                <el-col :span="3" class="lineHeight_150">
                    {{item.goods_unit}}
                </el-col>
                <el-col :span="3" class="lineHeight_150">
                    &yen; {{item.univalent.toFixed(2)}}
                </el-col>
                <el-col :span="3" class="lineHeight_150">
                    {{item.max_delivery_time}} 天
                </el-col>
                <el-col :span="3" class="lineHeight_150">
                    <span v-if="item.status==1">待支付</span>
                    <span v-if="item.status==2">已取消</span>
                    <span v-if="item.status==3">待接单</span>
                </el-col>
            </el-row>
        </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
export default {
  mounted() {
    console.log(this.orderInfo);
  },
  computed: {
    ...mapState({
      orderInfo: state => state.order.orderInfo
    })
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.subOrderInfo {
  border: 1px solid #ddd;
  border-bottom: none;
  border-top: none;
  position: relative;
   &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: #ddd;
      z-index: 1;
    }
}
.subOrder {
  p {
    // border-bottom: 1px solid #ddd;
    border-top: 1px solid #ddd;
    margin: 0;
    padding: 15*@fontSize 0;
    padding-left: 10*@fontSize;
    font-size: 14*@fontSize;
    position: relative;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: #ddd;
      z-index: 1;
    }
    strong {
      margin-left: 5*@fontSize;
      font-weight: normal;
      color: @baseColor;
    }
  }
  .suborderTitle {
    text-align: center;
    font-size: 14*@fontSize;
    font-weight: 600;
    > div {
      height: 40*@fontSize;
      line-height: 40*@fontSize;
    }
  }
  .suborderContent {
    text-align: center;
    > div {
      position: relative;
      height: 150*@fontSize;
      font-size: 14*@fontSize;
    }
    .subOrderDetail {
      text-align: left;
      position: absolute;
      top: 50%;
      transform: translate(-50%, -50%);
      left: 50%;
      width: 80%;
      h5 {
        margin: 0;
        padding: 0;
        font-size: 14*@fontSize;
      }
      > div {
        font-size: 14*@fontSize;
        margin-top: 10*@fontSize;
        > span {
          display: inline-block;
          width: 78*@fontSize;
          text-align: right;
          margin-right: 8*@fontSize;
        }
      }
    }
  }
}
.lineHeight_150 {
  line-height: 150*@fontSize;
}
</style>



