<template>
  <div class="productRank">
        <Title title="产品销售排行榜" englishTitle="" iconClass="icon-xingyepaixing"/>
        <div class="swiper-rank" id="rankSwiper">
            <swiper :options="swiperOption">
                <swiper-slide>
                    <ul class="cat-list">
                        <li v-for="(item, index) in indexProduct" :key="index">
                            <router-link :to="'/seriesInfo/'+item.pno" class="clearfix" :title="item.product_name">
                                <img src="../../../assets/images/top1.png" alt="" class="imgLeft" v-if="index === 0">
                                <img src="../../../assets/images/top2.png" alt="" class="imgLeft" v-if="index === 1">
                                <img src="../../../assets/images/top3.png" alt="" class="imgLeft" v-if="index === 2">
                                <img src="../../../assets/images/top4.png" alt="" class="imgLeft" v-if="index === 3">
                                <p class="descInfo" :title="item.product_name">  
                                    {{item.product_name}}           
                                </p>                    
                                <img :src="item.cover_url" alt="" class="imgRight">   
                            </router-link>
                        </li>
                    </ul>
                </swiper-slide>
                <swiper-slide>
                    <ul class="cat-list">
                        <li v-for="(item, index) in indexProduct" :key="index+100">
                            <router-link :to="'/seriesInfo/'+item.pno" class="clearfix" :title="item.product_name">
                                <img src="../../../assets/images/top5.png" alt="" class="imgLeft" v-if="index === 0">
                                <img src="../../../assets/images/top6.png" alt="" class="imgLeft" v-if="index === 1">
                                <img src="../../../assets/images/top7.png" alt="" class="imgLeft" v-if="index === 2">
                                <img src="../../../assets/images/top8.png" alt="" class="imgLeft" v-if="index === 3">
                                <p class="descInfo" :title="item.product_name">  
                                    {{item.product_name}}               
                                </p>
                                <img :src="item.cover_url" alt="" class="imgRight">      
                            </router-link>
                        </li>
                    </ul>
                </swiper-slide>
                
            </swiper>
            <div class="swiper-button-prev rankPrev" slot="button-prev"></div>
            <div class="swiper-button-next rankNext" slot="button-next"></div>
        </div>
      
  </div>
</template>
<script>
import Title from "../../title/title";
import {mapState, mapActions} from 'vuex'
export default {
    components:{
        Title,
    },
    props:{
        indexProduct:{
          type:Array,
          required:true
        }
    },
    data(){
        return {
            rankObj:{},
            swiperOption:{
                navigation: {
                     nextEl: '.rankPrev',
                     prevEl: '.rankNext',
                },
            }
        }
    },
   
}
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.productRank {
    position: relative;
}
.cat-list li {
    border-bottom: 1px dotted #ddd;
    a {
        display: block;
	    width: 100%;
	    height: 100%;
        position: relative;
        height: 130*@fontSize;
        .imgLeft {
            position: absolute;
            width: 50*@fontSize;
            height: 50*@fontSize;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
        }
        p {
            position: absolute;
            top: 50%;
            left: 46%;
            transform: translate(-50%, -50%);
            margin: 0;
            width: 48%;
            color: #333;
             display: -webkit-box;
             -webkit-box-orient: vertical;
             -webkit-line-clamp: 2;
             overflow: hidden;
             font-weight: 700;
             font-size: 12*@fontSize;

        }
        .imgRight {
            position: absolute;
            width: 80*@fontSize;
            height: 80*@fontSize;
            top: 50%;
            right: 0;
            transform: translateY(-50%);
        }
        
    }
}
.swiper-button-next, .swiper-button-prev {
            background-image : none;
            background-color: @baseColor;
            width: 20px;
            height: 20px;
            border-radius: 50%;
            margin-top: 0;
            position: absolute;
            top: 5px;
            cursor: pointer;
        }
        .swiper-button-next {
            right: 0px;
        }
        .swiper-button-prev {
            left: 80%;
        }
        :focus {
            outline: none;
        }
</style>



