<template>
    <div class="loginRegister">
        <a href="javascript:;" :class='{"displayNone" : userInfoShow}' @click="login(0)">
            请登录
        </a>
        <span :class='[{"displayNone" : userInfoShow},"verticalLine"]'>|</span>
        <a href="javascript:;" :class='{"displayNone" : userInfoShow}' @click="register">免费注册</a>
        <span :class='{"displayNone" : !userInfoShow}'>您好, {{userInfo.username}}</span>
        <span :class='[{"displayNone" : !userInfoShow},"verticalLine"]'>|</span>
        <a href="javascript:;" :class='{"displayNone" : !userInfoShow}' @click="loginOut">退出</a>
    </div>
</template>
<script>
import {mapState,mapGetters,mapActions,mapMutations} from "vuex";
import Cookies from "js-cookie";
export default {
    name:"LoginRegister",
    created(){
       this.getUserInfo();
    },
    data(){
        return {
            // isShow:false,
        }
    },
    mounted(){
       
    },
    methods:{
        ...mapActions({
            login:"loginRegister/login",
            register:"loginRegister/register",
            loginOut:"loginRegister/loginOut",
            getUserInfo:"loginRegister/getUserInfo",
        })
    },
    computed:{
        ...mapState({
            userInfo:state=>state.loginRegister.userInfo,
            userInfoShow:state=>state.loginRegister.userInfoShow
        })

    },
    watch:{
        userInfo:(newvalue,oldvalue)=>{
            Cookies.set('userInfo', newvalue, { expires: 365 });
        }
    }
}
</script>
<style lang="less" scoped>
    @baseColor:#0275d8;
    a, span {
        font-size: 14/16rem;
        color: #666;
        text-decoration: none;
    }
    .displayNone {
        display: none;
    }
    .displayInlineBlock {
        display: inline-block;
    }
    .verticalLine {
        font-size: 12/16rem;
        vertical-align: 1/16rem;
        margin: 0 5px;
        color: #999;
    }
</style>





