<template>
    <el-button-group class="pagination clearfix">
        <el-button type="primary" icon="el-icon-arrow-left" class="arrow arrowPrev" @click="previousPage">上一页</el-button>
        <div class="pageNum">
            {{listCurrpage}} / {{totalPage}}
            <!-- {{this.$route.query.page?this.$route.query.page-0+1:1}} / {{totalPage}}        -->
        </div>
        <el-button type="primary" class="arrow arrowNext" @click="nextPage">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
    </el-button-group>
</template>
<script>
import { mapState, mapGetters, mapMutations } from 'vuex';
export default {
  props:['limit'],
  created(){
  },
  data(){
      return {
        offset:0,
      }
  },
  methods:{
      ...mapMutations({
          updatelistOffset:"productList/updatelistOffset"
      }),
      previousPage: function() {
            if(this.listOffset<= 0) {
              this.updatelistOffset(0);
              return;
            }
            var count = this.listOffset - 1;
            this.updatelistOffset(count);

            var paginationParams = {
                offset:this.listOffset*this.limit,
                limit:this.limit,
                order:this.$route.query.order,
                has_stock: this.$route.query.has_stock,
                sort:this.$route.query.sort,
                keywords:this.$route.query.keywords
            }
            this.$router.push({query:paginationParams});
            
            // this.$emit('updateProductList',paginationParams);
    	},
    	nextPage: function () {
             var count = this.listOffset + 1;
             console.log('count',count)
            this.updatelistOffset(count);
            if(this.listOffset >= this.totalPage) {
                this.currpage = this.totalPage;
                this.updatelistOffset(this.totalPage-1);
                return;
            }
            var paginationParams = {
                offset:this.listOffset*this.limit,
                limit:this.limit,
                order:this.$route.query.order,
                has_stock: this.$route.query.has_stock,
                sort:this.$route.query.sort,
                keywords:this.$route.query.keywords
            }
            this.$router.push({query:paginationParams});
            
            // this.$emit('updateProductList',paginationParams);
            
    	},
  },
  computed:{
      ...mapState({
          totalCount:state=>state.productList.totalCount,
          listOffset:state=>state.productList.listOffset,
      }),
     
      ...mapGetters({
          listCurrpage:'productList/listCurrpage'
      }),
      totalPage:function () {
          return Math.ceil(parseInt(this.totalCount)/this.limit)
      }
  },
  watch:{
      
  }
}
</script>
<style lang="less" scoped>
@baseColor: #0275d8;
@fontSize: 1/16rem;
.pagination {
    display: block;
    position: relative;
    margin: 30*@fontSize 0;
    .pageNum {
        text-align: center;
        height: 40*@fontSize;
        line-height: 40*@fontSize;
    }
    .arrow {
        background-color: @baseColor;
        border-color:@baseColor;
        border-radius: 0;
    }
    .arrowPrev {
        float: left;
    }
    .arrowNext {
        position: absolute;
        top: 0;
        right: 0;
    }
}

</style>



