<template>
    <div class="solutionInfo">
        <div class="container">
            <div class="weldingSolution">
                <div class="breadCrumbTitle">
                <!-- <BreadCrumb :breadCrumbData = "seriesInfoData"/> -->
                </div>
                <h3>
                    焊接方案
                    <router-link to = "/">Robo2025.com</router-link>
                </h3>
                <div class="weldingDesc clearfix">
                    <ul class="weldingCoding">
                        <li>方案编号</li>
                        <li>{{solutionCode}}</li>
                    </ul>
                    <ul class="weldingDate">
                        <li>方案生成日期</li>
                        <li>{{date}}</li>
                    </ul>
                    <ul class="weldingDetail" v-if="stepTableParam.weldingTechnologyData">
                        <li><span>行业:</span>{{stepTableParam.industry}}</li>
                        <li><span>母材成分:</span>{{stepTableParam.metalData}}</li>
                        <li><span>生产效率:</span>{{stepTableParam.weldingTechnologyData.speed}}</li>
                        <li><span>焊接方法:</span>{{stepTableParam.weldingTechnologyData.method}}</li>
                        <li><span>最大板厚值:</span>{{stepTableParam.plankVal}} mm</li>
                        <li><span>场景:</span>{{stepTableParam.scene || '无'}}</li>
                        <li><span>气体成本:</span>{{stepTableParam.weldingTechnologyData.cost}}</li>
                        <li><span>飞溅/成型:</span>{{stepTableParam.weldingTechnologyData.splash}} / {{stepTableParam.weldingTechnologyData.model}}</li>
                        <li><span>保护气体:</span>{{stepTableParam.weldingTechnologyData.gas}}</li>
                        <li><span>最大作业半径:</span>{{stepTableParam.radiuVal}} mm</li>
                    </ul>
                   <div class="weldingTable" id="weldingTable">
                        <el-table
                            :data="solutionInfoTableData"
                            :span-method="objectSpanMethod"
                            style="width: 100%; margin-top: 20px">
                            <el-table-column
                              prop="idNum"
                              label="#"
                              width="30">
                            </el-table-column>
                            <el-table-column
                            align="center"
                              prop="name"
                              label="组成部分"
                              width="120">
                            </el-table-column>
                            <el-table-column
                            align="center"
                              prop="madeOf"
                              label="组件">
                            </el-table-column>
                            <el-table-column
                            align="center"
                              label="产品型号">
                              <template slot-scope="scope">
                                  <span>{{scope.row.product_model?scope.row.product_model:"- -"}}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                            align="center"
                              label="品牌">
                              <template slot-scope="scope">
                                  <span>{{scope.row.branding?scope.row.branding:"- -"}}</span>
                              </template>
                            </el-table-column>
                            <el-table-column
                            align="center"
                              label="单位">
                              <template slot-scope="scope">
                                    <span>套</span>
                                </template>
                            </el-table-column>
                            <el-table-column
                            align="center"
                              label="数量">
                              <template slot-scope="scope">
                                    <span>1</span>
                                </template>
                            </el-table-column>
                            <el-table-column
                            align="center"
                              label="小计">
                              <template slot-scope="scope">
                                    <span>{{scope.row.price?scope.row.price+".00":"- -"}}</span>
                                </template>
                            </el-table-column>
                            <el-table-column
                            align="center"
                              label="备注">
                              <template slot-scope="scope">
                                    <span>- -</span>
                                </template>
                            </el-table-column>
                            
                        </el-table>
                        <p>注： 此价格仅为核心设备的价格</p>
                        <div class="numChangeContainer">
                            <div class="totalPrice"><span>合计： <strong>&yen; {{totalPrice[0]*num1?(totalPrice[0]*num1).toFixed(2):'0.00'}}</strong></span></div>
                            <div class="numChange">
                                <el-input-number v-model="num1" :min="1" :max="10000" @change="changeTotalPrice"></el-input-number>
                                <div class="warning" v-if="tipDisplay"><span class="el-icon-warning" />请输入数字</div>
                            </div>
                        </div>
                   </div>
                </div>
            </div>
            <SolutionAddInfo />
        </div>
    </div>
</template>

<script>
import BreadCrumb from "../components/breadCrumb/main/BreadCrumb";
import SolutionAddInfo from "../components/solutionInfo/main/SolutionAddInfo";
import moment from "moment"; //引入日期格式化文件
import qs from "qs";
import { mapActions, mapState } from "vuex";
export default {
  components: {
    SolutionAddInfo
  },
  mounted() {
    this.stepTableParam = JSON.parse(sessionStorage.getItem("stepTableParam"));
    this.date = moment().format("YYYY-MM-DD"); // 获取当前时间
    let tableParam = qs.parse(sessionStorage.getItem("solutionInfotableParam"));
    var allPrice = 0;
    this.solutionInfoTableData.forEach(ele => {
      // 获取table表格数据
      if (ele.madeOf === "弧焊机器人") {
        for (var key in tableParam.robot) {
          ele[key] = tableParam.robot[key];
        }
      } else if (ele.madeOf === "焊接电源") {
        for (var key in tableParam.power) {
          ele[key] = tableParam.power[key];
        }
      } else if (ele.madeOf === "焊枪") {
        for (var key in tableParam.torch) {
          ele[key] = tableParam.torch[key];
        }
      }
      if (ele.price) {
        allPrice += parseFloat(ele.price);
      }
    });
    this.$set(this.totalPrice, 0, allPrice); // 获取总钱数
    var solutionInfoParam = this.$route.params.id;
    if(solutionInfoParam === '111') {
        this.updateSolutionCodeAsync({
      // 获取方案编码
      success: res => {
          this.solutionCode = res.data.data.plan_sn;
      },
      fail: res => {},
      error: res => {}
    });
    } else {
        this.solutionCode = solutionInfoParam;
    }
    
  },
  data() {
    return {
      stepTableParam: {},
      solutionCode:"",
      date: "",
      totalPrice: [],
      num1: 1,
      tipDisplay: false,
      solutionInfoTableData: [
        {
          idNum: "1",
          name: "机器人部分",
          madeOf: "弧焊机器人"
        },
        {
          idNum: "1",
          name: "机器人部分",
          madeOf: "控制柜"
        },
        {
          idNum: "1",
          name: "机器人部分",
          madeOf: "示教器"
        },
        {
          idNum: "2",
          name: "焊机部分",
          madeOf: "焊接电源"
        },
        {
          idNum: "2",
          name: "焊机部分",
          madeOf: "送丝机"
        },
        {
          idNum: "2",
          name: "焊机部分",
          madeOf: "焊枪"
        }
      ]
    };
  },
  methods: {
    ...mapActions({
      updateSolutionCodeAsync: "solutionInfo/updateSolutionCodeAsync"
    }),
    objectSpanMethod({ row, column, rowIndex, columnIndex }) {
      if (columnIndex === 0 || columnIndex === 1) {
        if (rowIndex % 3 === 0) {
          return {
            rowspan: 3,
            colspan: 1
          };
        } else {
          return {
            rowspan: 0,
            colspan: 0
          };
        }
      }
    },
    changeTotalPrice(val) {
      if (!val) {
        this.tipDisplay = true;
      } else {
        this.tipDisplay = false;
      }
    }
  },
  computed: {
    ...mapState({
    })
  }
};
</script>

<style lang="less" scoped>
@baseColor: #0275d8;
@fontSize: 1/16rem;
.solutionInfo {
  .weldingSolution {
    h3 {
      font-size: 40px;
      color: #333;
      font-weight: 500;
      letter-spacing: 2px;
      position: relative;
      padding-bottom: 10px;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 5px;
        background-color: #4a90e2;
        z-index: 1;
      }
      a {
        display: block;
        font-size: 14px;
        color: #ccc;
        margin-top: 15px;
      }
    }
    .weldingDesc {
      ul {
        margin-bottom: 40px;
        float: left;
        &.weldingCoding {
          border-right: 1px solid #aaa;
        }
        &.weldingCoding,
        &.weldingDate {
          font-size: 16px;
          color: #333;
          width: 10%;
          margin-right: 2%;
          li {
            &:first-of-type {
              color: #bbb;
              margin-bottom: 20px;
            }
          }
        }
        &.weldingDetail {
          width: 75%;
          font-size: 14px;
          li {
            float: left;
            width: 20%;
            height: 30px;
            line-height: 30px;
            &:nth-child(5n) {
              width: 16%;
              float: right;
            }
            text-align: left;
            span {
              margin-right: 5px;
            }
          }
        }
      }
      .weldingTable {
        p {
          font-size: 12px;
          border-bottom: 1px solid #ebeef5;
          margin: 0;
          padding: 15px 0;
          color: #000;
        }
        .numChangeContainer {
          position: relative;
          height: 40px;
          line-height: 40px;
          margin: 30px 0;
          .numChange {
            width: 150px;
            float: right;
            text-align: center;
            position: relative;
            .warning {
              position: absolute;
              width: 180px;
              text-align: center;
              color: #e6a23c;
              font-size: 14px;
              margin-top: 5px;
              span {
                background-color: rgba(230, 162, 60, 0.1);
                color: #e6a23c;
                margin-right: 8px;
              }
            }
          }
          .totalPrice {
            float: right;
            font-size: 16px;
            span {
              width: 205px;
              display: inline-block;
              text-align: right;
              strong {
                color: @baseColor;
              }
            }
          }
        }
      }
    }
  }
}
</style>



