<template>
    <div class="solutionFilter">
        <el-row :gutter="25">
            <el-col :span="6">
                <el-tabs type="border-card" v-model="activeName" id="solutionFilterContent">
                    <el-tab-pane label="焊接电源" name="first">
                        <el-row class="filter">
                            <el-col class="filterItem">
                                <div v-for="(item, index) in filterResult.power_filter" :key="index">
                                    <p v-if="index === 'power_branding'">品牌</p>
                                    <p v-if="index === 'power_load'">额定持续负载率</p>
                                    <p v-if="index === 'power_model'">型号</p>
                                    <div class="wrapper" ref="wrapper">
                                         <div class="wrapper-box">
                                             <ul class="bscroll-container">
                                                 <li  @click="changeFilter" data-value="-1" :data-key="index"> 
                                                    <label>
                                                        <input type="radio" 
                                                            :name="index"
                                                            ref="inputAll"
                                                            checked
                                                        > 
                                                        <span>全部</span>
                                                    </label>
                                                </li>
                                                <li v-for="(filterItem, idx) in item" :key="idx" @click="changeFilter" :data-value="filterItem" :data-key="index"> 
                                                    <label>
                                                        <input type="radio" 
                                                            :name="index"
                                                            ref="input"
                                                        > 
                                                        <span>{{filterItem}}</span>
                                                    </label>
                                                </li>
                                             </ul>
                                         </div>
                                     </div>
                                </div> 
                            </el-col>
                        </el-row>
                    </el-tab-pane>
                    <el-tab-pane label="焊枪" name="second">
                         <el-row class="filter">
                            <el-col class="filterItem">
                                <div v-for="(item, index) in filterResult.torch_filter" :key="index">
                                    <p v-if="index === 'torch_branding'">品牌</p>
                                    <p v-if="index === 'torch_load'">额定持续负载率</p>
                                    <p v-if="index === 'torch_model'">型号</p>
                                    <p v-if="index === 'torch_current'">电流</p>
                                    <p v-if="index === 'torch_cooling'">冷却方式</p>
                                    <div class="wrapper" ref="wrapper">
                                         <div class="wrapper-box">
                                             <ul class="bscroll-container">
                                                 <li  @click="changeFilter" data-value="-1" :data-key="index" > 
                                                    <label>
                                                        <input type="radio" 
                                                            :name="index"
                                                            ref="inputAll"
                                                            checked
                                                        > 
                                                        <span>全部</span>
                                                    </label>
                                                </li>
                                                <li v-for="(filterItem, idx) in item" :key="idx" @click="changeFilter"  :data-value="filterItem" :data-key="index"> 
                                                    <label>
                                                        <input type="radio" 
                                                            :name="index"
                                                            ref="input"
                                                        > 
                                                        <span>{{filterItem}}</span>
                                                    </label>
                                                </li>
                                             </ul>
                                         </div>
                                     </div>
                                </div> 
                            </el-col>
                        </el-row>
                    </el-tab-pane>
                    <el-tab-pane label="弧焊机器人" name="third">
                        <el-row class="filter">
                            <el-col class="filterItem">
                                <div v-for="(item, index) in filterResult.robot_filter" :key="index">
                                    <p v-if="index === 'robot_branding'">品牌</p>
                                    <p v-if="index === 'robot_accuracy'">重复精确率</p>
                                    <p v-if="index === 'robot_model'">型号</p>
                                    <div class="wrapper" ref="wrapper">
                                         <div class="wrapper-box">
                                             <ul class="bscroll-container">
                                                 <li  @click="changeFilter" data-value="-1" :data-key="index" > 
                                                    <label>
                                                        <input type="radio" 
                                                            :name="index"
                                                            ref="inputAll"
                                                            checked
                                                        > 
                                                        <span>全部</span>
                                                    </label>
                                                </li>
                                                <li v-for="(filterItem, idx) in item" :key="idx" @click="changeFilter" :data-value="filterItem" :data-key="index" > 
                                                    <label>
                                                        <input type="radio" 
                                                            :name="index"
                                                            ref="input"
                                                        > 
                                                        <span>{{filterItem}}</span>
                                                    </label>
                                                </li>
                                             </ul>
                                         </div>
                                     </div>
                                </div> 
                            </el-col>
                        </el-row>
                    </el-tab-pane>
                </el-tabs>
                <el-button type="primary" @click="resetSolutionList" class="resetButton">重置</el-button>
            </el-col>
            <el-col :span="18">
                <div class="noTipInfo" v-if="filterSolution.length <= 0">
                    暂无相关方案, <router-link to="/"><el-button type="primary">去逛逛</el-button></router-link>
                </div>
                <el-row class="filterContent" v-for="(item, index) in filterSolution" :key="index" @click.native="goSolutionInfo(item,paginationIndex*limit+index+1)">
                    <el-col :span="20" :offset="4">
                        <div class="filterContentTitle clearfix">
                            <span>核心设备组合{{paginationIndex*limit+index+1}}</span>
                            <strong>&yen; {{totalPrice[index]}}</strong>
                        </div>
                    </el-col>
                    <el-col :span="4" class="imgContainer">
                        <img src="../../../assets/images/solutionListImg.png" alt="">
                    </el-col>
                    <el-col :span="20">
                        <table cellspacing=0 cellpadding=0>
                            <thead>
                                <th width="20%"></th>
                                <th width="20%">品牌</th>
                                <th width="30%">型号</th>
                                <th width="15%">数量</th>
                                <th width="15%">单价</th>
                            </thead>
                            <tbody>
                                <tr v-for="(itemSolution, key) in item" :key="key">
                                    <td v-if="key === 'power'">电源</td>
                                    <td v-if="key === 'torch'">焊枪</td>
                                    <td v-if="key === 'robot'">机器人</td>
                                    <td>{{itemSolution.branding}}</td>
                                    <td>{{itemSolution.product_model}}</td>
                                    <td>1</td>
                                    <td>{{itemSolution.price.toFixed(2)}}</td>
                                </tr>
                            </tbody>
                        </table>
                    </el-col>  
                </el-row>
                <el-pagination
                class="pagination"
                v-if="totalCount > 0"
                    :page-size='limit'
                    layout="prev, pager, next"
                    @current-change = "changePagination"
                    :total="totalCount">
                </el-pagination>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import { mapActions, mapState } from "vuex";
import BScroll from "better-scroll";
import qs from 'qs';
const scrollBarArr = [];
export default {
  created() {},
  updated: function() {
    this.$nextTick(function() {
      const options = {
        scrollY: true,
        mouseWheel: {
          speed: 20
        },
        scrollbar: {
          fade: false,
          interactive: true
        }
      };
      if (this.$refs.wrapper) {
        this.$refs.wrapper.forEach((element, index) => {
          const isExistScrollBar = element.querySelector(
            ".bscroll-vertical-scrollbar"
          );
          if (element.children[0].children[0].children.length > 4) {
            if (!isExistScrollBar) {
              let scrollBar = new BScroll(this.$refs.wrapper[index], options);
              scrollBarArr.push(scrollBar);
            } else {
              scrollBarArr.forEach(bar => {
                bar.refresh();
              });
            }
          }
        });
      }
    });
  },
  mounted() {
    this.stepTableParam = JSON.parse(sessionStorage.getItem("stepTableParam"));
    this.solutionListParam = {
      metal: this.stepTableParam.metalData,
      welding: this.stepTableParam.weldingTechnologyData.method,
      height: this.stepTableParam.plankVal,
      radius: this.stepTableParam.radiuVal
    };
    var loader = this.$loading();
    this.updateFilterListAsync({
      params: this.solutionListParam,
      success: res => {
        // console.log(res)
        loader.close();
      },
      fail: res => {
        // console.log(res)
        loader.close();
      },
      error: error => {
        // console.log(error)
        loader.close();
      }
    });
    this.updateFilterResultAsync({
      params: this.solutionListParam,
      success: res => {
        loader.close();
      },
      fail: res => {
        // console.log(res)
        loader.close();
      },
      error: error => {
        // console.log(error)
        loader.close();
      }
    });
  },
  data() {
    return {
      activeName: "first",
      stepTableParam: {},
      totalPrice: [],
      paginationIndex: 0,
      limit: 4,
      totalCount: 0,
      solutionListParam: {},
      filterSolution: []
    };
  },
  methods: {
    ...mapActions({
      updateFilterListAsync: "solutionList/updateFilterListAsync",
      updateFilterResultAsync: "solutionList/updateFilterResultAsync"
    }),
    changePagination(val) {
      // 改变页数时改变filterSolution的值
      this.paginationIndex = val - 1;
      this.filterSolution = [];
      var endTotal;
      if (this.totalCount < this.limit * val) {
        endTotal = this.totalCount;
      } else {
        endTotal = this.limit * val;
      }
      for (var i = this.limit * (val - 1); i < endTotal; i++) {
        this.filterSolution.push(this.filterList.solution[i]);
      }
    },
    changeFilter(e) {
      this.totalCount = -1;
      var loader = this.$loading();
      var param = this.solutionListParam;
      var itemKey = e.currentTarget.dataset.key;
      var itemValue = e.currentTarget.dataset.value;
      if (itemValue === "-1") {
        delete param[itemKey];
      } else {
        param[itemKey] = itemValue;
      }
      //   console.log(param)
      this.updateFilterListAsync({
        params: param,
        success: res => {
          //   console.log(res);
          loader.close();
        },
        fail: res => {
          //   console.log(res);
          loader.close();
        },
        error: error => {
          //   console.log(error);
          loader.close();
        }
      });
    },
    resetSolutionList() {
      // 重置
      this.totalCount = -1;
      var loader = this.$loading();
      this.solutionListParam = {
        metal: this.stepTableParam.metalData,
        welding: this.stepTableParam.weldingTechnologyData.method,
        height: this.stepTableParam.plankVal,
        radius: this.stepTableParam.radiuVal
      };
      this.updateFilterListAsync({
        params: this.solutionListParam,
        success: res => {
          // console.log(res)
          this.activeName = "first";
          this.$refs.input.forEach(item => {
            item.checked = false;
          });
          this.$refs.inputAll.forEach(item => {
            item.checked = true;
          });
          loader.close();
        },
        fail: res => {
          // console.log(res)
          loader.close();
        },
        error: error => {
          // console.log(error)
          loader.close();
        }
      });
    },
    goSolutionInfo(item,index){
        
        sessionStorage.setItem("solutionInfotableParam",qs.stringify({...item,index}));
        this.$router.push({path:"/solutionInfo/111"})
    }
  },
  computed: {
    ...mapState({
      filterList: state => state.solutionList.filterList,
      filterResult: state => state.solutionList.filterResult
    })
  },
  watch: {
    filterList(newvalue, oldvalue) {
      // filterList改变
    //   console.log(newvalue);
      this.filterSolution = [];
      this.paginationIndex = 0;
      if (newvalue.solution && newvalue.solution.length > 0) {
        this.totalCount = newvalue.solution.length;
        var endTotal;
        if (this.totalCount <= this.limit) {
          endTotal = this.totalCount;
        } else {
          endTotal = this.limit;
        }
        for (var i = 0; i < endTotal; i++) {
          this.filterSolution.push(newvalue.solution[i]);
        }
      }
    },
    filterSolution(newvalue, oldvalue) {
      this.totalPrice = [];
      newvalue.forEach(element => {
        var allPrice = 0;
        for (var key in element) {
          allPrice += element[key]["price"];
        }
        this.totalPrice.push(allPrice.toFixed(2));
      });
    },
    filterResult(newvalue, oldvalue) {}
  }
};
</script>

<style lang="less" scoped>
@baseColor: #0275d8;
.solutionFilter {
  text-align: center;
  .resetButton {
    width: 50%;
    margin-top: 30px;
    border-radius: 0;
    letter-spacing: 2px;
    background-color: #3284d8;
    border-color: #3284d8;
  }
  .filter {
    margin: 0 -15px -20px;
    text-align: left;
    .wrapper {
      position: relative;
      overflow: hidden;
      max-height: 160px;
      margin: 20px;
      border: 1px solid #ddd;
    }
    p {
      background-color: #3284d8;
      box-shadow: 0 16px 26px -27px rgba(2, 117, 216, 0.56),
        0 4px 25px 0 rgba(0, 0, 0, 0.12), 0 8px 10px -8px rgba(2, 117, 216, 0.2);
      height: 40px;
      line-height: 40px;
      margin-bottom: 0;
      color: #fff;
      font-weight: 600;
      margin-left: -8px;
      padding-left: 20px;
      font-size: 14px;
      letter-spacing: 1px;
    }
    ul {
      background-color: #fff;
      li {
        font-size: 13px;
        color: #333;
        line-height: 40px;
        padding-left: 12px;
        height: 40px;
        cursor: pointer;
        &:hover {
          background-color: rgba(2, 117, 216, 0.03);
        }
        label {
          width: 100%;
          height: 100%;
          display: block;
          span {
            letter-spacing: 1px;
          }
        }
      }
    }
  }
  .pagination {
    text-align: right;
  }
  .noTipInfo {
      height: 300px;
      line-height: 300px;
      text-align: center;
  }
  .filterContent {
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
    padding: 30px;
    margin-bottom: 40px;
    cursor: pointer;
    .filterContentTitle {
      padding-bottom: 15px;
      span {
        float: left;
        font-weight: 500;
        font-size: 16px;
      }
      strong {
        color: @baseColor;
        float: right;
        margin-right: 2px;
      }
    }
    .imgContainer {
      height: 165px;
      position: relative;
      img {
        position: absolute;
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
      }
    }
    table {
      width: 100%;
      border-collapse: collapse;
      text-align: center;
      font-size: 14px;
      thead {
        width: 100%;
        th {
          color: #fff;
          height: 40px;
          line-height: 40px;
          font-size: 14px;
          font-weight: 400;
          letter-spacing: 1px;
          background-color: #3284d8;
          border: 1px solid #ddd;
        }
      }
      tbody {
        tr {
          td {
            border: 1px solid #ddd;
            height: 40px;
            line-height: 40px;
          }
        }
      }
    }
  }
}
</style>



