<template>
    <div class="container">
        <div class="solutionListDesc">
            <h3>需求汇总</h3>
            <el-row class="solutionListContent">
                <el-col :span="5" :offset="1">
                    <h4>行业 & 场景</h4>
                    <ul>
                        <li><span>行业 :</span> {{stepTableParam.industry}}</li>
                        <li><span>场景 :</span> {{stepTableParam.scene || '无'}}</li>
                    </ul>
                </el-col>
                <el-col :span="10" :offset="1">
                    <h4>焊接方法</h4>
                    <ul class="weldingMethod clearfix" v-if="stepTableParam.weldingTechnologyData">
                        <li><span>母材成分 :</span>{{stepTableParam.metalData}}</li>
                        <li><span>焊接方法 :</span>{{stepTableParam.weldingTechnologyData.method}}</li>
                        <li><span>保护气体 :</span>{{stepTableParam.weldingTechnologyData.gas}}</li>
                        <li><span>气体成本 :</span>{{stepTableParam.weldingTechnologyData.cost}}</li>
                        <li><span>生产效率 :</span>{{stepTableParam.weldingTechnologyData.speed}}</li>
                        <li><span>飞溅/成型 :</span>{{stepTableParam.weldingTechnologyData.splash}} / {{stepTableParam.weldingTechnologyData.model}}</li>
                    </ul>
                </el-col>
                <el-col :span="5" :offset="1">
                    <h4>板厚 & 作业范围</h4>
                    <ul>
                        <li><span>最大板厚值 :</span>{{stepTableParam.plankVal}} mm</li>
                        <li><span>最大作业半径 :</span>{{stepTableParam.radiuVal}} mm</li>
                    </ul>
                </el-col>
            </el-row>
        </div>
       <SolutionListFilter />
    </div>
</template>

<script>
import SolutionListFilter from "../components/solutionList/main/SolutionListFilter"
export default {
    components:{
        SolutionListFilter
    },
    data(){
        return {
            stepTableParam:{}
        }
    },
    created(){
        
    },
    mounted(){
        this.stepTableParam = JSON.parse(sessionStorage.getItem("stepTableParam"));
    }
}
</script>

<style lang="less" scoped>
.solutionListDesc {
    >h3 {
        margin: 30px 0;
        font-weight: 500;
        font-size: 20px;
        letter-spacing: 1px;
    }
    .solutionListContent {
        box-shadow: 0 2px 4px 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
        background-color: #fff;
        padding: 40px 0;
        margin-bottom: 60px;
        h4 {
            font-size: 20px;
            font-weight: 500;
            color: #000;
            border-bottom: 2px solid #a8aaac;
            padding-bottom: 20px;
            margin-top: 0;
        }
        ul {
            li {
                height: 40px;
                line-height: 40px;
                font-size: 14px;
                span {
                    font-weight: 600;
                    margin-right: 15px;
                }
            }
            &.weldingMethod {
            li {
                &:nth-child(2n) {
                    width: 40%;
                    float: left;
                }
                &:nth-child(2n+1) {
                    width: 60%;
                    float: left;
                }
            }
        }
        }
        
    }
}

</style>



