<template>
  <div>
        <div class="title">
            <div class="container">
                  <el-row :gutter="20">
                      <el-col :span="4"><CategoryList :styleObj="categoryStyleObj" :cateWidth="categoryWidth"/></el-col>
                      <el-col :span="20">
                          <!-- <BreadCrumb /> -->
                      </el-col>
                  </el-row>
            </div>
        </div>
        <div class="container">
            <el-row class="filter">
                <el-col :span="5" class="filterItem">
                    <div>
                        <p>品牌</p>
                        <div class="wrapper" ref="wrapper">
                             <div class="wrapper-box">
                                 <ul class="bscroll-container">
                                    <li @click="changeSelected" key="-1" data-bno="-1"> 
                                        <label>
                                            <input type="radio" 
                                                name="brand"
                                                :checked="inputIndex == -1"
                                            > 
                                            <span>全部</span>
                                        </label>
                                    </li>
                                    <li 
                                        v-for="(filterItem, filterIndex) in searchList.brands" :key="filterIndex"
                                        @click="changeSelected"
                                        :data-bno="filterItem"
                                    > 
                                        <label>
                                            <input type="radio" 
                                                name="brand"
                                                :checked="inputIndex === filterItem"
                                            > 
                                            <span>{{filterItem}}</span>
                                        </label>
                                    </li>
                                 </ul>
                             </div>
                         </div>
                    </div> 
                </el-col>
                <el-col :span="18" :offset="1">
                    <SearchListRight :limit="limit" :totalCount = "totalCount" @updateSearchList="updateSearchList"/>
                </el-col>
            </el-row>
        </div>
  </div>
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
import CategoryList from "../components/header/main/CategoryList"; //引入分类组件
import BreadCrumb from "../components/breadCrumb/main/BreadCrumb"; //引入面包屑导航组件
import SearchListRight from "../components/searchList/main/SearchListRight";
import BScroll from "better-scroll";
export default {
  components: {
    CategoryList,
    BreadCrumb,
    SearchListRight
  },
  created() {},
  mounted() {
    this.updateCategoryDisplay(false); // Header里面的分类不显示
    // 获取排序参数
    var param = this.$route.query;
    this.searchListParam = {};
    for (var key in param) {
      if (key === "page") {
        this.searchListParam.offset = (param.page - 1) * this.limit;
      } else {
        this.searchListParam[key] = param[key];
      }
    }
    this.searchListParam.limit = this.limit;
    this.updateSearchListAsync({
      params: this.searchListParam,
      success: res => {
        if (res.data.data.brands.length > 6) {
          const options = {
            scrollY: true,
            mouseWheel: {
              speed: 20,
              invert: false,
              easeTime: 300
            },
            scrollbar: {
              fade: false,
              interactive: true
            }
          };
          new BScroll(this.$refs.wrapper, options);
        }
        this.totalCount = res.headers["x-content-total"];
        // loader.close();
      },
      fail: res => {
        // console.log(res);
        // loader.close();
      },
      error: error => {
        // console.log(error);
        // loader.close();
      }
    }); // 初始化页面数据
  },
  destroyed() {
    this.updateCategoryDisplay(true); // Header里面的分类显示
  },
  data() {
    return {
      categoryStyleObj: {
        backgroundColor: "#0275d8",
        color: "#fff"
      },
      categoryWidth: {
        width: "1000px",
        top: "104%"
      },
      limit: 3,
      searchListParam: {},
      totalCount: "0",
      inputIndex: -1
    };
  },
  methods: {
    ...mapMutations({
      updateCategoryDisplay: "updateCategoryDisplay",
      updatelistOffset: "searchList/updatelistOffset"
    }),
    ...mapActions({
      updateSearchListAsync: "searchList/updateSearchListAsync"
    }),
    updateSearchList(param) {
      this.searchListParam = {};
      for (var key in param) {
        if (key === "page") {
          this.searchListParam.offset = (param.page - 1) * this.limit;
        } else {
          this.searchListParam[key] = param[key];
        }
      }
      this.searchListParam.limit = this.limit;
      this.updateSearchListAsync({
        params: this.searchListParam,
        success: res => {
          this.totalCount = res.headers["x-content-total"];
        },
        fail: res => {
          // console.log(res);
        },
        error: error => {
          // console.log(error);
        }
      }); // 初始化页面数据
    },
    changeSelected(e) {
      this.searchListParam = {};
      var bno = e.currentTarget.dataset.bno;
      var param = this.$route.query;
      this.inputIndex = bno;
      if (bno === "-1") {
        // 证明点击的是全部
        this.searchListParam = { page: 1, keywords: param.keywords };
      } else {
        this.searchListParam = {
          page: 1,
          keywords: param.keywords,
          brands: bno
        };
      }
      this.$router.replace({ query: this.searchListParam });
    }
  },
  computed: {
    ...mapGetters({
      listCurrpage: "seriesList/listCurrpage"
    }),
    ...mapState({
      searchList: state => state.searchList.searchList
    })
  },
  watch: {
    $route(newvalue, oldvalue) {
      var rankParams = this.$route.query; // 当后退时，改变选择状态
      if (this.$route.query.brands) {
        this.inputIndex = this.$route.query.brands;
      } else {
        this.inputIndex = -1;
      }
    }
  }
};
</script>
<style lang="less" scoped>
@baseColor: #0275d8;
@fontSize: 1/16rem;
.title {
  height: 40px;
  line-height: 40px;
  margin: 30 * @fontSize 0;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background-color: #0275d8;
    z-index: 1;
  }
}
.filter {
  min-height: 518px;
  .filterItem {
    .wrapper {
      position: relative;
      overflow: hidden;
      max-height: 240px;
      border: 1px solid #ddd;
    }
    p {
      background-color: rgb(41, 147, 240);
      margin: 0;
      height: 40px;
      line-height: 40px;
      color: #fff;
      font-weight: 600;
      padding-left: 10px;
      font-size: 14px;
    }
    ul {
      background-color: #fff;
      border-top: none;
      li {
        padding-left: 3px;
        height: 40px;
        line-height: 40px;
        cursor: pointer;
        label {
          width: 100%;
          display: block;
          height: 100%;
          span {
            font-size: 12px;
          }
        }
      }
      li:hover {
        background-color: rgba(2, 117, 216, 0.03);
      }
    }
  }
}
</style>



