const productList = {
    namespaced: true,
    state: {
        productList: [], // 商品列表页数据，用于商品列表
        totalCount: 0, // 商品总件数
        listOffset: 0, // 商品列表商品偏移数，默认为0
        isBigImgList: true, // 商品列表排序是否有大图选项，默认有
        listForProductList: true, // 列表是否在产品列表中显示，默认是
    },
    getters: {
        listCurrpage: (state) => {
            return state.listOffset + 1;
        },
        productListLength: (state) => {
            return state.productList.length;
        },
    },
    mutations: {
        updataIsBigImgList(state, bool) { // 商品列表是否显示大图选项
            state.isBigImgList = bool;
        },
        updataListForProductList(state, bool) { // 列表是否在productList中显示
            state.listForProductList = bool;
        },
        updateproductList(state, data) { // 更新列表数据
            state.productList = data;
        },
        updateTotalCount(state, data) {
            state.totalCount = data;
        },
        updatelistOffset(state, count) { // 点击翻页是改变偏移量，实现列表页翻页功能
            state.listOffset = count;
        },
    },
    actions: {
        updateProductListAsync({ commit }, param) {
            let that = this._vm;
            let qs = require('qs');
            let data = qs.stringify(param.params);
            const url = that.$urlConfig.config.productUrl + '/api/goods' + '?' + data;
            that.$http({
                method: 'get',
                url,
            }).then((res) => {
                let rescode = parseInt(res.data.rescode);
                if (rescode <= 10000) {
                    commit('updateTotalCount', res.headers['x-content-total']);
                    commit('updatePrice', res.data.data, { root: true });
                    commit('updateproductList', res.data.data);
                    param.success(res);
                } else {
                    param.fail(res);
                }
            }).catch((error) => {
                param.error(error);
            });
        },
    },
};

export default productList;



