<template>
  <div>
        <div class="title">
            <div class="container">
                  <el-row :gutter="20">
                      <el-col :span="4"><CategoryList :styleObj="categoryStyleObj" :cateWidth="categoryWidth"/></el-col>
                      <el-col :span="20">
                            <el-breadcrumb separator-class="el-icon-arrow-right">
                                <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
                                <el-breadcrumb-item v-if="categoryName.length === 1">
                                    {{categoryName[0]}}
                                </el-breadcrumb-item>
                                <el-breadcrumb-item v-if="categoryName.length > 1"
                                    :to="{path:'/seriesList',query:{category_id:[categoryId[0]],category_name:[categoryName[0]]}}">
                                    {{categoryName[0]}}
                                </el-breadcrumb-item>
                                <el-breadcrumb-item v-if="categoryName.length === 2">
                                    {{categoryName[1]}}
                                </el-breadcrumb-item>
                                <el-breadcrumb-item v-if="categoryName.length > 2"
                                    :to="{path:'/seriesList',query:{category_id:[categoryId[0],categoryId[1]],category_name:[categoryName[0],categoryName[1]]}}">
                                    {{categoryName[1]}}
                                </el-breadcrumb-item>
                                <el-breadcrumb-item v-if="categoryName.length === 3">
                                    {{categoryName[2]}}
                                </el-breadcrumb-item>
                                 <el-breadcrumb-item v-if="categoryName.length > 3"
                                    :to="{path:'/seriesList',query:{category_id:[categoryId[0],categoryId[1],categoryId[2]],category_name:[categoryName[0],categoryName[1],categoryName[2]]}}">
                                    {{categoryName[2]}}
                                </el-breadcrumb-item>
                                <el-breadcrumb-item v-if="categoryName.length > 3">
                                    {{categoryName[3]}}
                                </el-breadcrumb-item>
                            </el-breadcrumb>
                      </el-col>
                  </el-row>
            </div>
        </div>
        <div class="container">
            <el-row class="filter">
                <el-col :span="5" class="filterItem">
                    <div>
                        <p>品牌</p>
                        <div class="wrapper" ref="wrapperBrand">
                             <div class="wrapper-box">
                                 <ul class="bscroll-container">
                                    <li @click="changeBrands('bno',-1)"> 
                                        <label>
                                            <input type="radio" 
                                                name="brand"
                                               checked
                                               ref="checkBrand"
                                            > 
                                            <span>全部</span>
                                        </label>
                                    </li>
                                    <li 
                                        v-for="(filterItem, filterIndex) in filterBrands" :key="filterIndex"
                                        @click="changeBrands('bno',filterItem.bno)"
                                    > 
                                        <label>
                                            <input type="radio" 
                                                name="brand"
                                            > 
                                            <span>{{filterItem.brand_name}}</span>
                                        </label>
                                    </li>
                                 </ul>
                             </div>
                         </div>
                    </div> 
                    <div v-for="(item, index) in filterCategorySpecs" :key="index" v-if="filterCategorySpecs.length > 0">
                        <p>{{item.spec_name}}<span>{{item.spec_unit?' ('+ item.spec_unit + ')':''}}</span></p>
                        <div class="wrapper" ref="wrapper">
                             <div class="wrapper-box">
                                 <ul class="bscroll-container">
                                    <li @click="changeSelected(item.spec_name,-1)" key="-1"> 
                                        <label>
                                            <input type="radio" 
                                                :name="item.spec_name"
                                                checked
                                                ref="checkAll"
                                            > 
                                            <span>全部</span>
                                        </label>
                                    </li>
                                    <li 
                                        v-for="(specItem, specIndex) in item.spec_values" :key="specIndex"
                                        @click="changeSelected(item.spec_name,specItem)"
                                    > 
                                        <label>
                                            <input type="radio" 
                                                :name="item.spec_name"
                                            > 
                                            <span>{{specItem}}</span>
                                        </label>
                                    </li>
                                 </ul>
                             </div>
                         </div>
                    </div> 
                </el-col>
                <el-col :span="18" :offset="1">
                    <SeriesListRight :limit="limit" :totalCount = "totalCount" :sort="sort" :filterSpecs="specValue" @updateSeriesList="updateSeriesList"/>
                </el-col>
            </el-row>
        </div>
  </div>
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
import CategoryList from "../components/header/main/CategoryList"; //引入分类组件
import BreadCrumb from "../components/breadCrumb/main/BreadCrumb"; //引入面包屑导航组件
import SeriesListRight from "../components/seriesList/main/SeriesListRight";
import BScroll from "better-scroll";
export default {
  components: {
    CategoryList,
    BreadCrumb,
    SeriesListRight
  },
  created() {},
  mounted() {
    this.updateFilterBrandsAsync({
      success: res => {
        this.$nextTick(() => {
          if (res.data.data.length > 6) {
            const options = {
              scrollY: true,
              mouseWheel: {
                speed: 20,
                invert: false,
                easeTime: 300
              },
              scrollbar: {
                fade: false,
                interactive: true
              }
            };
            new BScroll(this.$refs.wrapperBrand, options);
          }
        });
      },
      fail: res => {},
      error: res => {}
    });
    this.updateCategoryDisplay(false); // Header里面的分类不显示
    // 获取排序参数
    this.categoryName = [];
    this.categoryId = [];
    if (typeof this.$route.query.category_name === "string") {
      // 后退只剩一级分类时，会默认为字符串而不是数组
      this.categoryName.push(this.$route.query.category_name);
      this.categoryId.push(this.$route.query.category_id);
    } else {
      this.categoryName = this.$route.query.category_name;
      this.categoryId = this.$route.query.category_id;
    }
    this.seriesListParam = {};
    if (this.$route.query.category_id.length === 1) {
      this.seriesListParam = {
        category_id_1: this.$route.query.category_id[0]
      };
    } else if (this.$route.query.category_id.length === 2) {
      this.seriesListParam = {
        category_id_2: this.$route.query.category_id[1]
      };
    } else if (this.$route.query.category_id.length === 3) {
      this.seriesListParam = {
        category_id_3: this.$route.query.category_id[2]
      };
    }else if (this.$route.query.category_id.length === 4) {
      this.seriesListParam = {
        category_id_4: this.$route.query.category_id[3]
      };
    }
    this.seriesListParam.offset = 0;
    this.seriesListParam.limit = this.limit;
    this.updateSeriesList({});
    // 初始化页面数据
    var cateParams =
      this.seriesListParam.category_id_4 ||
      this.seriesListParam.category_id_3 ||
      this.seriesListParam.category_id_2 ||
      this.seriesListParam.category_id_1;
    if (!cateParams) {
      return false;
    } else {
      this.updatefilterCategorySpecsAsync({
        params: cateParams,
        success: res => {},
        fail: res => {},
        error: error => {}
      });
    }
  },
  destroyed() {
    this.updateCategoryDisplay(true); // Header里面的分类显示
  },
  data() {
    return {
      categoryStyleObj: {
        backgroundColor: "#0275d8",
        color: "#fff"
      },
      categoryWidth: {
        width: "1000px",
        top: "104%"
      },
      limit: 1,
      seriesListParam: {},
      totalCount: "0",
      specValue: {},
      sort: 1,
      breadCrumbList: [],
      categoryName: [],
      categoryId: []
    };
  },
  methods: {
    ...mapMutations({
      updateCategoryDisplay: "updateCategoryDisplay",
      updatelistOffset: "seriesList/updatelistOffset"
    }),
    ...mapActions({
      updateFilterBrandsAsync: "seriesList/updateFilterBrandsAsync",
      updateSeriesListAsync: "seriesList/updateSeriesListAsync",
      updatefilterCategorySpecsAsync:
        "seriesList/updatefilterCategorySpecsAsync" // 获取对应的规格参数筛选框
    }),
    updateSeriesList(param) {
      // 在本组件内使用this.seriesListParam,从子组件传过来的参数使用param
      let qs = require("qs");
      var seriesParam = qs.stringify(
        { ...this.seriesListParam, ...param },
        { indices: false }
      );
      var loader = this.$loading({ fullScreen: true });
      // console.log(seriesParam);
      this.updateSeriesListAsync({
        params: seriesParam,
        success: res => {
          this.totalCount = res.headers["x-content-total"];
          loader.close();
        },
        fail: res => {
          // console.log(res);
          loader.close();
        },
        error: error => {
          // console.log(error);
          loader.close();
        }
      }); // 初始化页面数据
    },
    changeSelected(key, value) {
      // 规格参数需要以数组的形式传给后台
      this.sort++;
      if (this.sort >= 1000) {
        this.sort = 0;
      }
      this.updatelistOffset(0);
      if (value === -1) {
        this.$set(this.specValue, key, "");
        delete this.specValue[key];
      } else {
        delete this.specValue[key];
        this.$set(this.specValue, key, value);
        // this.specValue[key] = value;
      }
      var keyArr = Object.keys(this.specValue);
      var valueArr = [];
      keyArr.forEach(item => {
        valueArr.push(this.specValue[item]);
      });
      this.seriesListParam = {
        ...this.seriesListParam,
        spec_names: keyArr,
        spec_values: valueArr
      };
      this.updateSeriesList({});
    },
    changeBrands(key, value) {
      this.sort++;
      if (this.sort >= 1000) {
        this.sort = 0;
      }
      this.updatelistOffset(0);
      if (value === -1) {
        delete this.seriesListParam[key];
      } else {
        this.seriesListParam[key] = value;
      }
      this.updateSeriesList({});
    }
  },
  computed: {
    ...mapState({
      filterBrands: state => state.seriesList.filterBrands,
      filterCategorySpecs: state => state.seriesList.filterCategorySpecs
    }),
    ...mapGetters({
      listCurrpage: "seriesList/listCurrpage"
    })
  },
  watch: {
    filterCategorySpecs(newvalue, oldvalue) {
      if (newvalue.length > 0) {
        this.$nextTick(() => {
          newvalue.forEach((element, index) => {
            if (element.spec_values && element.spec_values.length > 6) {
              const options = {
                scrollY: true,
                mouseWheel: {
                  speed: 20,
                  invert: false,
                  easeTime: 300
                },
                scrollbar: {
                  fade: false,
                  interactive: true
                }
              };
              new BScroll(this.$refs.wrapper[index], options);
            }
          });
        });
      }
    },
    $route(newvalue, oldvalue) {
      if (newvalue.path === oldvalue.path) {
        // 证明只是路由参数发生改变
        // 获取排序参数
        this.sort++;
        if (this.sort >= 1000) {
          this.sort = 0;
        }
        this.categoryName = [];
        this.categoryId = [];
        if (typeof this.$route.query.category_name === "string") {
          // 后退只剩一级分类时，会默认为字符串而不是数组
          this.categoryName.push(this.$route.query.category_name);
          this.categoryId.push(this.$route.query.category_id);
        } else {
          this.categoryName = this.$route.query.category_name;
          this.categoryId = this.$route.query.category_id;
        }
        this.seriesListParam = {};
        if (this.$route.query.category_id.length === 1) {
          this.seriesListParam = {
            category_id_1: this.$route.query.category_id[0]
          };
        } else if (this.$route.query.category_id.length === 2) {
          this.seriesListParam = {
            category_id_2: this.$route.query.category_id[1]
          };
        } else if (this.$route.query.category_id.length === 3) {
          this.seriesListParam = {
            category_id_3: this.$route.query.category_id[2]
          };
        }else if (this.$route.query.category_id.length === 4) {
          this.seriesListParam = {
            category_id_4: this.$route.query.category_id[3]
          };
        }
        this.seriesListParam.offset = 0;
        this.seriesListParam.limit = this.limit;
        this.updateSeriesList({});
        // 初始化页面数据
        var cateParams =
          this.seriesListParam.category_id_4 ||
          this.seriesListParam.category_id_3 ||
          this.seriesListParam.category_id_2 ||
          this.seriesListParam.category_id_1;
        if (!cateParams) {
          return false;
        } else {
          this.updatefilterCategorySpecsAsync({
            params: cateParams,
            success: res => {},
            fail: res => {},
            error: error => {}
          });
        }
        if (this.$refs.checkAll) {
          this.$refs.checkAll.forEach(element => {
            element.checked = true;
          });
        }
        this.$refs.checkBrand.checked = true;
        this.updatelistOffset(0);
      }
    }
  }
};
</script>
<style lang="less" scoped>
@baseColor: #0275d8;
@fontSize: 1/16rem;
.title {
  height: 40px;
  line-height: 40px;
  margin: 30 * @fontSize 0;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 2px;
    background-color: #0275d8;
    z-index: 1;
  }
  .el-breadcrumb {
    height: 40px;
    line-height: 40px;
  }
}

.filter {
  min-height: 518px;
  .filterItem {
    .wrapper {
      position: relative;
      overflow: hidden;
      max-height: 240px;
      border: 1px solid #ddd;
      margin-bottom: 30px;
    }
    p {
      background-color: rgb(41, 147, 240);
      margin: 0;
      height: 40px;
      line-height: 40px;
      color: #fff;
      font-weight: 600;
      padding-left: 10px;
      font-size: 14px;
    }
    ul {
      background-color: #fff;
      border-top: none;
      li {
        padding-left: 3px;
        height: 40px;
        line-height: 40px;
        cursor: pointer;
        label {
          width: 100%;
          display: block;
          height: 100%;
          span {
            font-size: 12px;
          }
        }
      }
      li:hover {
        background-color: rgba(2, 117, 216, 0.03);
      }
    }
  }
}
</style>



