<template>
    <div class="likeBest">
        <Title title="猜你喜欢" englishTitle="" iconClass="icon-iconfonthuangguan"/>
        <ul class="likeContent">
            <li v-for="(item, index) in indexProduct" :key="index">
                <router-link :to="'/seriesInfo/'+item.pno">
                    <img :src="item.cover_url" alt="" />
                    <div class="likeDesc">
                        <p>{{item.product_name}}</p>
                        <p>
                            <span class="price">&yen; {{item.price}}+</span>
                        </p>
                    </div>
                </router-link>
            </li>
        </ul>
        
    </div>
  
</template>
<script>
import Title from "../../title/title";

export default {
    components:{
        Title,
    },
    props:{
        indexProduct:{
          type:Array,
          required:true
        }
    },
    methods:{
    },
    computed:{
       
    }
  
}
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
    .likeBest {
        margin-top: 40px;
        .likeContent li {
            position: relative;
            border-bottom: 1px dashed #ccc;
            width: 100%;
            height: 157px;
            a {
            display: block;
            width: 100%;
            
            img {
                width: 100px;
                height: 100px;
                position: absolute;
                top: 50%;
                left: 0;
                transform: translateY(-50%);
            }
            .likeDesc {
                position: absolute;
                top: 50%;
                right: 0;
                transform: translateY(-50%);
                width: 57%;
                p:first-of-type {
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 2;
                    overflow: hidden;
                    font-size: 14*@fontSize;
                    color: #333;
                }
                .price {
                    color: #0275d8;
                    font-weight: 700;
                    font-size: 16px;
                }
            }
            
        }
        }
         
    }
</style>



