<template>
    <div class="wrap">
        <h4 class="title">{{title}}</h4>
        <div class="type wrapper">
            <ul>
                <li v-for='(item,idx) in types' :key=idx @click='changeTypes(idx)' :class="[active==idx?'active':'']">                   
                    <label>
                        <input type="radio" name="type" id="item.id" :checked="active==idx">
                        <span>{{item.text}}</span>       
                    </label>      
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
export default {
  props: ["title"],
  data(){
      return {
          types:[
              {id:1,text:'全部'},
              {id:2,text:'HQ4M40-10S30A3'},
              {id:3,text:'HQ5M130-200D20B1'},
          ],
          active:0,
      }
  },
  methods:{
      changeTypes(idx){
          this.active=idx;
      }
  }
};
</script>
<style lang="less" scoped>
.wrap {
  width: 100%;
  height: 100%;
  .title {
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-weight: 600;
    text-align: center;
    font-size: 17px;
    margin: 0;
  }
  .type {
    box-shadow: 0px 1px 4px 1px rgba(0, 0, 0, 0.08);
    height: 200px;
    position: relative;
    overflow: hidden;
    top: 20px;
    ul {
      background-color: #fff;
      padding-top: 20px;
      li {
        height: 50px;
        line-height: 50px;
        padding-left: 20px;
        cursor: pointer;
        display: flex;
        align-items: center;
        font-size: 14px;
        label{
            width:100%;
            input{
                vertical-align: -1px;
                margin-right: 8px;
                cursor: pointer;
            }
        }
      }
       li.active{
           background-color: rgba(2,117,216,0.03)!important;
       }
    }
  }
}
</style>



