const solutionInfo = {
    namespaced: true,
    state:{
        
    },
    getters:{

    },
    mutations:{
        
    },
    actions:{
        updateSolutionCodeAsync(store,param) {
            var that = this._vm;
            let url = that.$urlConfig.config.IDGeneratorUrl;
            that.$http.post(url).then((res) => {
                var resCode = parseInt(res.data.rescode);
                if(resCode <= 10000) {
                    param.success(res);
                } else {
                    param.fail(res);
                }
            }).catch((error) => {
                param.error(error);
            })
        }
    }
}

export default solutionInfo;


