const searchList = {
    namespaced: true,
    state: {
        searchList: {},
        listOffset:0,
    },
    getters: {
        listCurrpage:state=>{
            return state.listOffset + 1;
        }
    },
    mutations: {
        updateSearchList(state,data){
            state.searchList = data;
        },
        updatelistOffset(state, data) {
            state.listOffset = data;
        }
    },
    actions: {
        // 搜索列表的数据
        updateSearchListAsync(store, param) {
            var that = this._vm;
            var qs = require('qs');
            var data = qs.stringify({...param.params});
            var loader = that.$loading({fullscreen: true});
            let url = `${that.$urlConfig.config.searchListUrl}/v1/search?${data}`;
            that.$http({
                methods: 'get',
                url: url
            }).then((res) => {
                loader.close();
                var resCode = parseInt(res.data.rescode);
                if(resCode <= 10000) {
                    store.commit("updateSearchList",res.data.data);
                    param.success(res);
                    loader.close();
                } else {
                    param.fail(res);
                    loader.close();
                }
            }).catch((error) => {
                param.error(error);
                loader.close();
            });
        },
    }
};

export default searchList;


