<template>
  <div>
        <div id="001"></div>
        <div>
            <el-tabs v-model="activeName" @tab-click="handleClick" v-for="(item, inx) in seriesInfoData" :key="inx">
                <el-tab-pane class="tabPane" label="商品详情" name="first" v-html="item.description"></el-tab-pane>
                <el-tab-pane class="tabPane" label="型号列表" name="second" >
                    <SeriesInfoTable :activeName="activeName"/>
                </el-tab-pane>
                <el-tab-pane class="tabPane noneTip" label="视频详解" name="third">更多精彩,在路上...</el-tab-pane>
                <el-tab-pane class="tabPane noneTip" label="学堂" name="fourth">更多精彩,在路上...</el-tab-pane>
                <el-tab-pane class="tabPane noneTip" label="常见问题解答" name="fifth">更多精彩,在路上...</el-tab-pane>
            <el-tab-pane class="tabPane cadDownload" label="CAD 图" name="sixth">
                <div class="tipInfo" v-if="tipMessage !==''">
                    {{tipMessage}} 
                </div>
                <div v-show="!pdfLoading" class="tipInfo">
                    <i class="el-icon-loading"></i>
                </div>
                <div v-show="tipMessage == '' && pdfLoading">
                    <div class="pageButton">
                        <!-- <a :href="item.cad_url" v-if="tipMessage == ''" :download="item.cad_url" target="_blank"><span class="iconfont icon-xiazai"></span>下载</a>  -->
                        <span class="iconfont icon-iconfontzuo" @click="onPrevPage"></span>
                        <span class="iconfont icon-xiayiye" @click="onNextPage"></span>
                    </div>
                    <canvas id="the-canvas"></canvas> 
                    <div><span class="page"><span id="page_num">{{pageNum}}</span> / <span id="page_count">{{totalPages}}</span></span></div>
                </div> 
            </el-tab-pane>
            <el-tab-pane class="tabPane noneTip" label="3D 预览" name="seventh">更多精彩,在路上...</el-tab-pane>            
        </el-tabs>
        </div>
        
  </div>
</template>
<script>
import Title from "../../title/title";
import CountChange from "../../countChange/main/CountChange";
import SeriesInfoTable from "./SeriesInfoTable";
import { mapState, mapActions } from "vuex";
import PDFJS from "pdfjs-dist";
export default {
  props: {
    showActiveName: {
      type: String,
      required: true
    }
  },
  components: {
    CountChange,
    Title,
    SeriesInfoTable
  },
  data() {
    return {
      activeName: "first",
      tipMessage: "",
      pdfDoc: null,
      pageNum: 1, //pdf页数
      pageRendering: false,
      pageNumPending: null,
      scale: 1,
      totalPages: "", // pdf总页数
      pdfLoading: false,
    };
  },
  mounted() {
      
  },
  methods: {
    ...mapActions({
      updateSeriesModalList: "seriesInfo/updateSeriesModalList",
    }),
    handleClick(tab, e) {
      this.$emit("changeShowActiveName", this.activeName);
      if (
        e.currentTarget.id === "tab-sixth" &&
        this.seriesInfoData[0].cad_urls.length > 0
      ) {
        this.pdfLoading = false;
        var that = this;
        PDFJS.getDocument(
          this.seriesInfoData[0].cad_urls[0]
        ).then(function(pdfDoc_) {
          that.pdfDoc = pdfDoc_;
          that.totalPages = that.pdfDoc.numPages;
          that.renderPage(that.pageNum);
          that.pdfLoading = true;
        });
        this.tipMessage = "";
      } else {
        this.tipMessage = "更多精彩,在路上...";
        this.pdfLoading = true;
      }
    },
    renderPage(num) {
      var canvas = document.getElementById("the-canvas"),
        ctx = canvas.getContext("2d");
      this.pageRendering = true;
      var that = this;
      this.pdfDoc.getPage(num).then(function(page) {
        var viewport = page.getViewport(that.scale);
        canvas.height = viewport.height;
        canvas.width = viewport.width;

        var renderContext = {
          canvasContext: ctx,
          viewport: viewport
        };
        var renderTask = page.render(renderContext);

        renderTask.promise.then(function() {
          that.pageRendering = false;
          if (that.pageNumPending !== null) {
            // New page rendering is pending
            that.renderPage(pageNumPending);
            that.pageNumPending = null;
          }
        });
      });
    },
    queueRenderPage(num) {
      if (this.pageRendering) {
        this.pageNumPending = num;
      } else {
        this.renderPage(num);
      }
    },
    onPrevPage() {
      if (this.pageNum <= 1) {
        return;
      }
      this.pageNum--;
      this.queueRenderPage(this.pageNum);
    },
    onNextPage() {
      if (this.pageNum >= this.pdfDoc.numPages) {
        return;
      }
      this.pageNum++;
      this.queueRenderPage(this.pageNum);
    },
  },
  computed: {
    ...mapState({
      seriesInfoData: state => state.seriesInfo.seriesInfoData,
      seriesModalList: state => state.seriesInfo.seriesModalList,
    })
  },
  watch: {
    showActiveName(newvalue, oldvalue) {
      this.activeName = newvalue;
    },
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.tabPane {
  min-height: 300px;
  margin: 30px 0;
  box-shadow: none;
  &:focus,
  &:hover,
  &:active {
    box-shadow: none;
  }
}
.cadDownload {
  text-align: center;
  font-size: 18px;
  .tipInfo {
    line-height: 300px;
  }
}
.noneTip {
  height: 300px;
  line-height: 300px;
  text-align: center;
  font-size: 18px;
}
.pageButton {
  line-height: 100px;
  height: 100px;
  text-align: right;
  margin-top: -40px;
  a {
    float: left;
    color: #fff;
    background-color: #0275d8;
    width: 100px;
    text-align: center;
    height: 40px;
    line-height: 40px;
    font-size: 14px;
    margin-top: 30px;
    span {
      color: #fff;
      margin-right: 5px;
      margin-left: 0;
      font-size: 16px;
    }
  }
  span {
    font-size: 25px;
    color: #333;
    margin-left: 5px;
    cursor: pointer;
  }
}
.page {
  line-height: 0;
}
.footer {
  padding-top: 0;
}


</style>



