<template>
    <!-- 点击购买弹出此模态框 -->
    <el-dialog
        :visible.sync="centerDialogVisible"
        width="60%"
        custom-class="seriesInfoModal"
        >
            <div v-for="(item, index) in productInfo" :key="index">
            <Title title="商品详情" englishTitle="" iconClass="icon-icon-test1"/>
            <div class="seriesProductInfo">
                <ul>
                    <li><span>商 品 ID</span>: {{item.gno}}</li>
                    <li class="price">
                         <span>价 格: <strong>&yen; {{singlePrice[0]}}</strong></span>
                    </li>
                    <li class="diffNumPrice">
                        <ul class="differentNum">
                            <li v-for="(priceItem, ids) in item.prices" :key="ids"> 
                                <span class="num">{{priceItem.min_quantity}}-{{priceItem.max_quantity}}</span>
                                <span class="numPrice">{{priceItem.price}}</span>
                                <span class="rect"></span>
                            </li>
                        </ul>      
                    </li>
                    <li><span>发货日 :</span> {{leadTime}}天</li>
                    <li><span>起订量 :</span> {{item.min_buy + item.sales_unit}}</li>
                    <li><span>库存 :</span> {{item.stock + item.sales_unit}}</li>
                    <li><span>数量折扣 :</span> {{item.prices.length > 1?'有':'无'}}</li>
                    <li class="numChange">
                                <CountChange 
                                :min="item.min_buy" :max="item.stock" 
                                :quantity="num[0]" :cartId="1" 
                                :prices="item.prices" :index="index" 
                                @changeCount="changeCount"
                                />
                    </li>
                </ul>
                <div slot="footer" class="dialog-footer seriesButton">
                    <p>总 金 额 : <strong>{{totalMoney[0]}}</strong></p>
                    <el-button @click="save" :data-gno="rowData.gno">收藏</el-button>
                    <el-button type="primary" @click="addShopCar" :data-gno="rowData.gno">加入购物车</el-button>
                 </div>
            </div>
            </div>
             <div>
            <Title title="商品参数" englishTitle="" iconClass="icon-canshupeizhi"/>
            <ul class="productParam clearfix">
                <li>型号: {{rowData.partnumber}}</li>
                <li v-for="(specItem, specIndex) in rowData.specs" :key="specIndex" v-if="specItem.spec_value !== ''">
                    <div>
                        {{specItem.spec_name}} : {{specItem.spec_value}}
                    </div>
                </li>
            </ul>
            </div>
        </el-dialog>
</template>

<script>
import { mapState, mapActions } from "vuex";
import CountChange from "../../countChange/main/CountChange";
import swal from "sweetalert"; //弹出消息提示框
import "../../../assets/sweetAlert/sweetAlert.css";
export default {
  components: {
    CountChange
  },
  props: {
    rowData: {
      type: Object,
      required: true
    },
    dialogShow: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      centerDialogVisible: false,
      totalMoney: [],
      singlePrice: [],
      num: [],
      prices: [],
      leadTime: 3
    };
  },
  methods: {
    ...mapActions({
      updateProductInfoAsync: "seriesInfo/updateProductInfoAsync",
      addToShopcar: "shopcar/addToShopcar",
      saveGoods: "shopcar/saveGoods"
    }),
    changeCount(params) {
      this.$set(this.singlePrice, params.index, params.price);
      this.$set(this.totalMoney, params.index, params.singleAllPrices);
      this.$set(this.num, params.index, params.shopcarQuantity);
      this.leadTime = this.changeLeadTime(params.shopcarQuantity, this.prices);
    },
    changeLeadTime(value, prices) {
      var leadTime;
      prices.forEach((item, index) => {
        if (value >= item.min_quantity && value <= item.max_quantity) {
          leadTime = item.lead_time;
        } else if (value < prices[0].min_quantity) {
          leadTime = prices[0].lead_time;
        } else if (value > prices[prices.length - 1].max_quantity) {
          leadTime = prices[prices.length - 1].lead_time;
        }
      });
      return leadTime;
    },
    save(e) {
      var gno = e.currentTarget.dataset.gno;
      this.saveGoods({
        params: gno,
        success: res => {
          this.centerDialogVisible = false;
          var msg = res.data.msg;
          var tipInfo;
          if (msg.indexOf(":") != -1) {
            tipInfo = msg.split(":")[1];
          } else {
            tipInfo = "加入收藏成功";
          }
          swal({
            title: "",
            text: tipInfo,
            icon: "info",
            button: "关闭"
          });
        },
        error: error => {
        //   console.log(error);
        }
      });
    },
    addShopCar(e) {
      var gno = e.currentTarget.dataset.gno;
      var quantity = this.num[0];
      this.addToShopcar({
        params: {
          gno: gno,
          quantity: quantity
        },
        success: function(res) {
          let resCode = parseInt(res.data.rescode);
          if (resCode <= 10000) {
            this.centerDialogVisible = false;
            swal({
              title: "加入购物车成功",
              icon: "success",
              buttons: ["继续购物", "去购物车结算"]
              // dangerMode: true,
            }).then(willDelete => {
              if (willDelete) {
                this.$router.push("/shopcar");
              } else {
              }
            });
          } else {
            var msg = res.data.msg;
            var tipInfo;
            if (msg.indexOf(":") != -1) {
              tipInfo = msg.split(":")[1];
            } else {
              tipInfo = "加入购物车失败,请重试";
            }
            swal({
              title: "",
              text: tipInfo,
              icon: "info",
              button: "关闭"
            });
          }
        }.bind(this),
        error: function(res) {
          //    swal({title: "",
          //         text: "请求失败",
          //         icon: "info",
          //         button: "关闭",});
        }
      });
    }
  },
  watch: {
    rowData(newvalue, oldvalue) {
      if (newvalue.gno) {
        var loader = this.$loading({ fullScreen: true });
        var gno = newvalue.gno;
        this.updateProductInfoAsync({
          param: gno,
          success: res => {
            this.singlePrice = [];
            this.totalMoney = [];
            this.prices = [];
            this.num = [];
            this.num.push(res.data.data.min_buy);
            this.singlePrice.push(res.data.data.first_price);
            this.totalMoney.push(
              (
                res.data.data.min_buy * parseInt(res.data.data.first_price)
              ).toFixed(2)
            );
            this.prices = res.data.data.prices;
            this.leadTime = this.changeLeadTime(
              res.data.data.min_buy,
              this.prices
            );
            loader.close();
            this.centerDialogVisible = true;
          },
          fail: res => {
            var msg = res.data.msg;
            var tipInfo;
            if (msg.indexOf(":") != -1) {
              tipInfo = msg.split(":")[1];
            } else {
              tipInfo = "获取信息失败,请重试";
            }
            swal({
              title: "",
              text: tipInfo,
              icon: "info",
              button: "关闭"
            });
            loader.close();
          },
          error: error => {
            //   console.log(error);
            loader.close();
          }
        });
      } else {
        this.centerDialogVisible = false;
      }
    },
    centerDialogVisible(newvalue, oldvalue) {
      if (!newvalue) {
        this.$emit("chageDialogShow", newvalue);
      }
    }
  },
  computed: {
    ...mapState({
      productInfo: state => state.seriesInfo.productInfo
    })
  }
};
</script>

<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.seriesProductInfo {
  font-size: 14 * @fontSize;
  color: #333;
  > ul {
    padding: 20 * @fontSize 0;
    border-bottom: 1px solid #ddd;
    > li {
      height: 40px;
      line-height: 40px;
      &.price {
        background: url("../../../assets/images/bgPic.png");
        padding-left: 10 * @fontSize;
        margin: 15px 0 25px 0;
        strong {
          color: @baseColor;
          margin-left: 10 * @fontSize;
        }
      }
      &.diffNumPrice {
        position: relative;
        .differentNum {
          li {
            position: relative;
            height: 26px;
            line-height: 26px;
            display: inline-block;
            position: relative;
            margin-right: 20 * @fontSize;
            font-size: 14 * @fontSize;
            &:last-child {
              margin-right: 0;
            }
            .rect {
              position: absolute;
              left: 50%;
              top: 0;
              border-width: 27px 25px 0px 0px;
              border-style: solid;
              border-color: #0275d8 #fff #0275d8 #0275d8;
            }
            .num {
              background-color: #0275d8;
              color: #fff;
              float: left;
              width: 128px;
              height: 28px;
              line-height: 28px;
              text-align: center;
              overflow: hidden;
            }
            .numPrice {
              color: #0275d8;
              width: 100px;
              float: left;
              text-align: center;
              border: 1px solid #0275d8;
              border-left: none;
              overflow: hidden;
            }
          }
        }
      }
      &.numChange {
        margin-top: 30 * @fontSize;
        width: 200 * @fontSize;
      }
    }
  }
  .seriesButton {
    text-align: right;
    margin: 20px 0 80px 0;
    height: 40px;
    line-height: 40px;
    > p {
      float: left;
      font-size: 18 * @fontSize;
      margin: 0;
      padding: 0;
      strong {
        color: @baseColor;
      }
    }
    button {
      overflow: hidden;
    }
  }
}
.productParam {
  padding: 30px 0;
  li {
    height: 40px;
    line-height: 40px;
    width: 25%;
    float: left;
  }
}
</style>



