
const solutionList = {
    namespaced: true,
    state:{
        filterList:[],
        filterResult:{}
    },
    getters:{

    },
    mutations:{
        updateFilterList(state,data) {
            state.filterList = data;
        },
        updateFilterResult(state,data) { // 获取筛选条件
            state.filterResult = data;
        }
    },
    actions:{
        updateFilterListAsync(store, param) {
            var that = this._vm;
            let qs = require('qs');
            let data = qs.stringify(param.params);
            let url = that.$urlConfig.config.stepListUrl+"/solution/v1/solution?" + data;
            that.$http({
                method:"get",
                url:url
            }).then((res) => {
                if(res.data.success) {
                    store.commit("updateFilterList",res.data.data);
                    param.success(res)
                } else {
                    store.commit("updateFilterList",[]);
                    param.fail(res);
                }
            }).catch((error) => {
                param.error(error)
            })
        },
        updateFilterResultAsync(store, param) {
            var that = this._vm;
            let qs = require('qs');
            let data = qs.stringify(param.params);
            let url = that.$urlConfig.config.stepListUrl+"/solution/v1/filter?" + data;
            that.$http({
                method:"get",
                url:url
            }).then((res) => {
                if(res.data.success) {
                    store.commit("updateFilterResult",res.data.data);
                    param.success(res)
                } else {
                    store.commit("updateFilterResult",{});
                    param.fail(res)
                }
            }).catch((error) => {
                param.error(error)
            })
        },
    }
}

export default  solutionList;



