<template>
    <div class="solutionAddInfo">
        <h4>用户补充信息</h4>
       <div class="solutionAddForm">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="120px" class="solitionAddInfoForm">
                <el-form-item label="方案预算" prop="budget">
                  <el-input v-model="ruleForm.budget" placeholder="请输入预算金额"></el-input>
                  <span class="solutionBudgetUnit">元</span>
                </el-form-item>
                <el-form-item label="意向付款比例" required class="payment">
                    <el-row class="initialPayment">
                        <el-col>
                            <el-form-item prop="initialPayment">
                                <el-select v-model="ruleForm.initialPayment" @change="changeInitialPayment">
                                    <el-option label="30%" value="30"></el-option>
                                    <el-option label="35%" value="35"></el-option>
                                    <el-option label="40%" value="40"></el-option>
                                    <el-option label="45%" value="45"></el-option>
                                    <el-option label="50%" value="50"></el-option>
                                </el-select>
                                <span class="paymentInfo">首付</span>
                            </el-form-item>
                        </el-col>
                    </el-row>
                  <el-row>
                        <el-col>
                            <el-form-item prop="finalPayment">
                                <el-select v-model="ruleForm.finalPayment" @change="changeFinalPayment">
                                    <el-option label="70%" value="70"></el-option>
                                    <el-option label="65%" value="65"></el-option>
                                    <el-option label="60%" value="60"></el-option>
                                    <el-option label="55%" value="55"></el-option>
                                    <el-option label="50%" value="50"></el-option>
                                </el-select>
                                <span class="paymentInfo">尾款</span>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form-item>
                <el-form-item label="焊接名称" prop="weldingName">
                  <el-input v-model="ruleForm.weldingName" placeholder="请输入焊接名称"></el-input>
                </el-form-item>
                <el-form-item label="焊接图片" prop="weldingImages">
                    <el-upload
                    ref="weldingImages"
                    :limit = 5
                    :data="uptoken"
                        action="//upload.qiniup.com"
                        list-type="picture-card"
                        :before-upload="weldingImageBeforeUpload"
                        :on-success = "weldingImagesSuccess"
                        :on-preview="weldingImagePrevies"
                        :on-remove="weldingImageRemove">
                         <i class="el-icon-upload"></i>
                         <div slot="tip" class="el-upload__tip">焊接图片,可支持上传五张</div>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                      <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </el-form-item>
                <el-form-item label="焊件CAD">
                  <el-upload
                  ref="weldingCAD"
                      class="CADupload"
                      :data="uptoken"
                      :before-upload="weldingCADBeforeUpload"
                      :on-success = "weldingCADSuccess"
                      action="//upload.qiniup.com"
                      :on-remove="CADRemove"
                    >
                      <el-button  type="primary" plain size="small" v-if="isShowCadButton"><i class="el-icon-upload2"></i> 上传</el-button>
                  </el-upload>
                </el-form-item>
                <el-form-item label="附件">
                  <el-upload
                  ref="weldingAttachment"
                      class="attachmentUpload"
                      :data="uptoken"
                      :before-upload="weldingAttachmentBeforeUpload"
                      :on-success = "weldingAttachmentSuccess"
                      action="//upload.qiniup.com"
                      :on-remove="AttachmentRemove"
                    >
                      <el-button  type="primary" plain size="small" v-if="isShowAttachmentButton"><i class="el-icon-upload2"></i> 上传</el-button>
                  </el-upload>
                </el-form-item>
                <el-form-item label="备注" prop="remark">
                    <el-input
                        type="textarea"
                        :rows="4"
                        placeholder="请输入内容"
                        v-model="ruleForm.remark">
                    </el-input>
                </el-form-item>
                 <el-form-item>
                   <el-button type="primary" @click="submitForm('ruleForm')">保存</el-button>
                   <el-button @click="resetForm('ruleForm')">发布</el-button>
                 </el-form-item>
            </el-form>
       </div>
    </div>
</template>

<script>
import UUID from "uuid";
const CAD_TYPES = ["doc", "docx", "pdf", "dwt", "dxf", "dxb","dwg"]; // 支持的CAD文件格式
const FILE_TYPES = ["jpg", "png", "gif", "jpeg"]; // 支持上传的图片文件类型
export default {
  created() {
    let url = this.$urlConfig.config.commonUrl + "/qiniu/upload_token";
    this.$http.get(url).then(res => {
      this.uptoken = {
        token: res.data
      };
    });
  },
  mounted() {},
  data() {
    var checkBudget = (rule, value, callback) => {
        var parseIntValue = parseInt(value);
      if (!value.trim()) {
        return callback(new Error("请输入预算金额"));
      } else if(!parseIntValue){
        return callback(new Error("请输入数字"));
      } else {
          return callback();
      }
    };
    var checkweldingName = (rule, value, callback) => {
      if (!value.trim()) {
        return callback(new Error("请输入焊接名称"));
      } else {
        return callback();
      }
    };
    return {
      dialogImageUrl: "",
      dialogVisible: false,
      weldingCAD: [],
      weldingAttachment: [],
      isShowCadButton: true,
      isShowAttachmentButton: true,
      remark: "",
      uptoken: {},
      ruleForm: {
        budget: "",
        initialPayment: "",
        finalPayment: "",
        weldingName: "",
        weldingImages: []
      },
      rules: {
        budget: [{ required: true, validator: checkBudget, trigger: "blur" }],
        initialPayment: [
          { required: true, message: "请选择首付款", trigger: "change" }
        ],
        finalPayment: [
          { required: true, message: "请选择尾款", trigger: "change" }
        ],
        weldingName: [
          {
            required: true,
            validator: checkweldingName,
            message: "请输入焊接名称",
            trigger: "blur"
          }
        ],
        weldingImages: [
          {
            required: true,
            type: "array",
            message: "请上传焊接图片",
            trigger: "blur"
          }
        ],
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          alert("submit!");
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    changeInitialPayment(val) {
      this.ruleForm.finalPayment = 100 - val + "%";
    },
    changeFinalPayment(val) {
      this.ruleForm.initialPayment = 100 - val + "%";
    },
    weldingImagePrevies(file, fileList) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    weldingImageBeforeUpload(file) {
      var fileSuffix = file.name.split(".")[1].toLowerCase(); // 获取文件后缀名
      const isJPG =
        fileSuffix === "png" ||
        fileSuffix === "jpg" ||
        fileSuffix === "jpeg" ||
        fileSuffix === "gif";
      if (!isJPG) {
        const h = this.$createElement;
        this.$message({
          message: h("p", null, [
            h("span", null, "请上传图片"),
            h("i", { style: "color: teal" })
          ])
        });
        return false;
      }
      var imgId = UUID().replace(/-/g, "");
      this.uptoken["key"] =
        "/solution/FA0000001/c/img/" + imgId + "." + fileSuffix;
    },
    weldingImagesSuccess(res, file, fileList) {
      if (fileList.length >= 5) {
        this.$refs.weldingImages.$children[1].$el.style.display = "none"; //当长度等于5时，上传框消失
      }
      this.ruleForm.weldingImages = fileList;
    },
    weldingImageRemove(file, fileList) {
      this.$refs.weldingImages.$children[1].$el.style.display = "inline-block";
      this.ruleForm.weldingImages = fileList;
    },
    weldingCADBeforeUpload(file) {
      var fileSuffix = file.name.split(".")[1].toLowerCase();
      const isCAD =
        fileSuffix === "doc" ||
        fileSuffix === "docx" ||
        fileSuffix === "pdf" ||
        fileSuffix === "dwt" ||
        fileSuffix === "dxf" ||
        fileSuffix === "dxb";
      if (!isCAD) {
        const h = this.$createElement;
        this.$message({
          message: h("p", null, [
            h("span", null, "请上传CAD文件"),
            h("i", { style: "color: teal" })
          ])
        });
        return false;
      }
      var cadId = UUID().replace(/-/g, "");
      this.uptoken["key"] =
        "/solution/FA0000001/c/cad/" + cadId + "." + fileSuffix;
    },
    weldingCADSuccess(res, file, fileList) {
      this.$refs.weldingCAD.$children[0].$el.style.display = "none";
      this.weldingCAD = fileList;
      this.isShowCadButton = false;
    },
    CADRemove() {
      this.$refs.weldingCAD.$children[0].$el.style.display = "inline-block";
      this.weldingCAD = [];
      this.isShowCadButton = true;
    },
    weldingAttachmentBeforeUpload(file) {
      var fileSuffix = file.name.split(".")[1].toLowerCase();
      const isAttachment =
        fileSuffix === "doc" ||
        fileSuffix === "docx" ||
        fileSuffix === "pdf" ;
      if (!isAttachment) {
        const h = this.$createElement;
        this.$message({
          message: h("p", null, [
            h("span", null, "请上传文件"),
            h("i", { style: "color: teal" })
          ])
        });
        return false;
      }
      var attachmentId = UUID().replace(/-/g, "");
      this.uptoken["key"] =
        "/solution/FA0000001/c/attachment/" + attachmentId + "." + fileSuffix;
    },
    weldingAttachmentSuccess(res, file, fileList) {
      this.$refs.weldingAttachment.$children[0].$el.style.display = "none";

      this.weldingAttachment = fileList;
      this.isShowAttachmentButton = false;
    },
    AttachmentRemove() {
      this.$refs.weldingAttachment.$children[0].$el.style.display =
        "inline-block";
      this.weldingAttachment = [];
      this.isShowAttachmentButton = true;
    },
  },
  watch: {}
};
</script>
<style lang="less" scoped>
@baseColor: #0275d8;
.solutionAddInfo {
  h4 {
    margin: 30px 0;
    padding-bottom: 15px;
    font-size: 16px;
    position: relative;
    &::after {
      content: "";
      width: 100%;
      height: 2px;
      background-color: @baseColor;
      position: absolute;
      left: 0;
      bottom: 0;
      z-index: 1;
    }
  }
  .solutionAddForm {
    width: 50%;
    margin: 0 auto;
    .solutionBudgetUnit {
      position: absolute;
      right: 8px;
      bottom: 0;
      color: #cccccc;
    }
    .payment {
      .initialPayment {
        margin-bottom: 30px;
      }
      .paymentInfo {
        position: absolute;
        right: 28px;
        bottom: 0;
        color: #cccccc;
      }
      .el-form-item {
        .el-select {
          width: 100%;
        }
      }
    }
  }
}
</style>



