<template>
    <div>
        <el-dialog title="请选择发票类型" :visible.sync="dialogTableVisible" width="40%">
            <el-tabs type="border-card">
                <el-tab-pane>
                    <span slot="label">增值税发票</span>
                    <el-select v-model="selectedValue" placeholder="请选择已有发票" class="selectInvoice" 
                        :disabled="invoiceList.length == 0"  @change="changeSelect">
                        <el-option
                            v-for="(item,index) in invoiceList"
                            :key="index"
                            :label="item.partyname+' ( 税号 : '+item.taxno+')'"
                            :value="index">
                        </el-option>
                    </el-select>
                    <el-button type="primary" @click="addInvoiceList" class="addInvoice"><span class="iconfont icon-tianjia"></span>新增开票信息</el-button>
                    <el-form :model="ruleForm"  :rules="rules" ref="ruleForm" label-width="120px" class="ruleForm" v-show="isSelected">
                        <el-form-item label="单位名称" prop="companyName">
                            <el-input  v-model="ruleForm.companyName" auto-complete="off" placeholder="请输入单位名称" :disabled="selectInvoice"></el-input>
                        </el-form-item>
                        <el-form-item label="纳税人识别码" prop="taxPayerCode">
                            <el-input  v-model="ruleForm.taxPayerCode" auto-complete="off" placeholder="请输入纳税人识别码" :disabled="selectInvoice"></el-input>
                        </el-form-item>
                        <el-form-item label="注册地址" prop="registerAddress">
                            <el-input  v-model="ruleForm.registerAddress" auto-complete="off" placeholder="请输入注册地址" :disabled="selectInvoice"></el-input>
                        </el-form-item>
                        <el-form-item label="注册电话" prop="registerMobileNumber">
                            <el-input  v-model="ruleForm.registerMobileNumber" auto-complete="off" placeholder="请输入注册电话" :disabled="selectInvoice"></el-input>
                        </el-form-item>
                        <el-form-item label="开户银行" prop="depositBank">
                            <el-input  v-model="ruleForm.depositBank" auto-complete="off" placeholder="请输入开户银行" :disabled="selectInvoice"></el-input>
                        </el-form-item>
                        <el-form-item label="银行账号" prop="bankAccount">
                            <el-input  v-model="ruleForm.bankAccount" auto-complete="off" placeholder="请输入银行账户" :disabled="selectInvoice"></el-input>
                        </el-form-item>
                        <el-form-item prop="checked">
                            <el-checkbox v-model="ruleForm.checked" v-if="!selectInvoice">设置为默认发票</el-checkbox>
                        </el-form-item>
                        <el-form-item class="sureInvoice">
                            <el-button @click="resetForm('ruleForm')">取消</el-button>
                            <!-- 新增发票用按钮 -->
                            <el-button type="primary" @click="submitForm('ruleForm')" v-if="!selectInvoice">确认</el-button>
                            <!-- 选择已有发票用按钮 -->
                            <el-button type="primary" @click="sureInvoice" v-if="selectInvoice">确认</el-button>
                         </el-form-item>
                    </el-form>
                </el-tab-pane>
                <el-tab-pane>
                    <span slot="label">不开发票</span>
                     <el-form>
                         <el-form-item class="noInvoiceTip">
                             您选择不开发票
                         </el-form-item>
                        <el-form-item class="noInvoiceButton">
                            <el-button @click="dialogTableVisible=false">取消</el-button>
                            <el-button type="primary" @click="noInvoice">确认</el-button>
                        </el-form-item>
                     </el-form>
                </el-tab-pane> 
            </el-tabs>
        </el-dialog>
    </div>
</template>
<script>
import {mapState, mapActions} from 'vuex';
export default {
    props:{
        invoiceAlert:{
            type:Boolean,
            required:true
        },
    },
    data(){
        var checkCompanyName = (rule, value, callback) => {
            if(!value.trim()) {
                return callback(new Error('单位名称不能为空'))
            } else {
                return callback()
            }
        };
        var checkTaxPayerCode = (rule, value, callback) => {
            if(!value.trim()) {
                return callback(new Error('纳税人识别码不能为空'))
            }else {
                return callback()
            }
        };
        var checkRegisterAddress = (rule, value, callback) => {
            return callback()
        };
        var checkRegisterMobileNumber = (rule, value, callback) => {
            var testReg = /^[0-9-]*$/;
            if(!testReg.test(value.trim())){
                return callback(new Error('请输入正确的注册电话'))
            }else {
                return callback()
            }
        };
        var checkDepositBank = (rule, value, callback) => {
            if(!value.trim()) {
                return callback(new Error('开户银行不能为空'))
            }else {
                return callback()
            }
        };
        var checkBankAccount = (rule, value, callback) => {
            var testAccount = /^[0-9]*$/;
            if(!value.trim()) {
                return callback(new Error('银行账户不能为空'))
            } else if(!testAccount.test(value.trim())) {
                return callback(new Error('请输入数字'))
            } else {
                return callback()
            }
        };
        var checkSelected = (rule, value, callback) => {
            return callback();
        };
        return {
            selectedValue: '',
            invoiceTitle:'',
            dialogTableVisible:false,
            isSelected:false,//是否显示form框
            selectInvoice:false,
            ruleForm: {
                companyName:"",
                taxPayerCode:"",
                registerAddress:"",
                registerMobileNumber:"",
                depositBank:"",
                bankAccount:"",
                checked:false,
            },
            rules:  {
                companyName:[
                    {required: true,validator: checkCompanyName, trigger: 'blur'}
                ],
                taxPayerCode:[
                    {required: true,validator: checkTaxPayerCode, trigger: 'blur'}
                ],
                registerAddress:[
                    {required: false,validator: checkRegisterAddress, trigger: 'blur'}
                ],
                registerMobileNumber:[
                    {required: false,validator: checkRegisterMobileNumber, trigger: 'blur'}
                ],
                depositBank:[
                    {required: true,validator: checkDepositBank, trigger: 'blur'}
                ],
                bankAccount:[
                    {required: true,validator: checkBankAccount, trigger: 'blur'},
                ],
                checked:[
                    {required: false,validator: checkSelected, trigger: 'blur'}
                ]
            }
        }
    },
    methods:{
        ...mapActions({
            updateInvoiceListAsync:"order/updateInvoiceListAsync",
            addInvoice:"order/addInvoice"
        }),
        submitForm(formName) {//增加新发票的确认按钮
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    var invoiceParams = {
                        partyname:this.ruleForm.companyName,
                        taxno:this.ruleForm.taxPayerCode,
                        address:this.ruleForm.registerAddress,
                        phone:this.ruleForm.registerMobileNumber,
                        bank:this.ruleForm.depositBank,
                        bank_account:this.ruleForm.bankAccount,
                        is_default:this.ruleForm.checked?1:0,
                        invoice_type:1,
                    }
                    this.addInvoice({
                        params:invoiceParams,
                        success:function(res){
                            var rescode = parseInt(res.data.rescode);
                              if(rescode <= 10000) {
                                  this.dialogTableVisible = false;
                                  this.invoiceTitle = this.ruleForm.companyName+" ( "+this.ruleForm.taxPayerCode+" ) ";
                                  this.updateInvoiceListAsync();
                              } else {
                                  const h = this.$createElement;
                                  this.$message({
                                      message: h('p', null, [
                                          h('span', null, '新增发票失败，请重试'),
                                          h('i', { style: 'color: teal' },)
                                      ]),
                                      center: true
                                  })
                              }
                            
                        }.bind(this),
                        error:function (error){
                            const h = this.$createElement;
                              this.$message({
                                  message: h('p', null, [
                                      h('span', null, '请求服务失败，请重试'),
                                      h('i', { style: 'color: teal' },)
                                  ]),
                                  center: true
                              })
                        }.bind(this)
                    })
                } else {
                    return false;
                }
            });
      },
      addInvoiceList(){
          this.selectedValue = "";
          this.isSelected = true;//显示form框
          this.$refs['ruleForm'].resetFields();
          this.selectInvoice = false;//每个输入框都可编辑
      },
      sureInvoice(){//确认选择已有发票执行
        this.dialogTableVisible = false;
        this.invoiceTitle = this.selectedValue;
        this.$emit('changeInvoiceTitle',this.invoiceTitle);
      },
      noInvoice(){//不开发票确认按钮
            this.invoiceTitle = "您选择不开发票";
            this.dialogTableVisible = false;
      },
      resetForm(formName) {
          this.dialogTableVisible = false;
      },
      changeSelect(value){
            this.$refs['ruleForm'].resetFields();
            this.isSelected = true;//显示form框
            var params = this.invoiceList[value];
            this.ruleForm.companyName = params.partyname;
            this.ruleForm.taxPayerCode = params.taxno;
            this.ruleForm.registerAddress = params.address;
            this.ruleForm.registerMobileNumber = params.phone;
            this.ruleForm.depositBank = params.bank;
            this.ruleForm.bankAccount = params.bank_account;
            if(!params.is_default) {
                this.ruleForm.checked = false;
            } else {
                this.ruleForm.checked = true;
            }
            this.selectInvoice = true;//输入框不可编辑
        }
    },
    computed:{
        ...mapState({
            invoiceList:state=>state.order.invoiceList
        })
    },
    watch:{
        invoiceAlert:function (newvalue,oldvalue) {
            this.dialogTableVisible = newvalue;
        },
        dialogTableVisible:function(newvalue,oldvalue) {
            if(!newvalue) {
                this.isSelected = false;
                this.$refs['ruleForm'].resetFields();
                this.$emit('changeInvoiceTitle',this.invoiceTitle);
            }
            if(newvalue) {
                this.selectedValue = '';
            }
        }
    }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.selectInvoice {
    width: 50%;
    margin: 10%;
}
.addInvoice {
    width: 28%;
}
.sureInvoice {
    text-align: right;
}
.noInvoiceTip {
    text-align: center;
    padding: 50px 0;
    font-size: 18px;
}
.noInvoiceButton {
    text-align: right;
}
.icon-tianjia {
    font-size: 1/16rem;
    margin-right: 3*@fontSize;
}
</style>



