<template>
  <div>
        <div class="wizard-navigation">
            <div class="nav-content" v-for='(item,idx) in arr'  :key='idx'>{{item}}</div>
            <div class="move-nav" :style='{left:left+"px"}' ></div>
        </div>
        <div class="wizard-content">
            <div v-if='isActive===1' class="first-step">
                <div class="left">
                  <h4 class="title">
                    行业
                  </h4>
                  <div v-for='(item,idx) in industry' :key='idx' 
                       :class="['choose',chooseIdx === idx ? 'active' : '']" 
                       @click='changeChoose(idx)'>
                       <div class="icon">
                            <i :class="['iconfont', item.icon]"></i>                         
                      </div>
                      <h5>{{item.text}}</h5>
                  </div>
                </div>
                <div class="right">
                  <h4 class="title">
                    场景
                  </h4>
                  <transition
                    appear
                   :duration="{ enter: 1000}"
                    name="custom-classes-transition"
                    enter-active-class="animated bounceInUp"
                    appear-active-class="animated bounceInUp"
                  > 
                    <div class="choose-content "  v-for='(item,idx) in industry' :key='idx' v-if="chooseIdx===idx">
                        <div v-for='(child,index) in item.childChoose' :key=index 
                             :class="[selectedItem===index?'active':'']"
                             @click="itemChange(index)">
                            <p>{{child}}
                              <i class="icon-gou iconfont" v-if='selectedItem===index'></i>
                            </p>
                        </div>
                        <div v-if='item.childChoose.length==0' class="chooseNothing"> 敬请期待</div>
                    </div>
                  </transition>
                </div>
            </div>
            <div v-if='isActive===2' class="second-step" id='second-step'> 
                <swiper :options="swiperOption" ref="mySwiper">
                  <!-- slides -->
                    <swiper-slide v-for="(item, index) in metalData" :key="index">
                        <a href="javascript:;" @click="checkedImg(index)">
                            <!-- <img :src="item.src" alt=""> -->
                            <img src="../../../assets/images/slideshow1.png" alt="">
                            <p>{{item}}</p>   
                            <p v-if='index===checkedImgData' class="pChecked">已选择：{{item}}</p>
                        </a>
                    </swiper-slide>
                  <!-- Optional controls -->  
                    <div class="swiper-button-prev prev" slot="button-prev"></div>
                    <div class="swiper-button-next next" slot="button-next"></div>
                </swiper>
            </div>
            <div v-if='isActive===3' class="third-step">
                  <div class="title">
                      <b>焊接方法选择</b>
                      <div @click='changeModel'>
                         <span>专家</span> 
                         <span>向导</span> 
                         <p
                            :style='{left:modelTypeLeft+"px"}'>
                           {{modelType[modelTypeIndex]}}
                         </p>
                      </div>
                  </div>
                  <div class="content">
                    <div v-show='modelTypeIndex===0' class='experts'>
                      <div class="left  wrapper" v-cloak ref="wrapper1">
                        <div class="wrapper-box">
                          <div class="bscroll-containe">
                           <label v-for='(expertWeldingItem,idx) in weldingData' :key='idx'
                              @click='chooseWelding(idx,expertWeldingItem)'>
                              <input type="radio" name='welding' 
                                      :checked="idx===weldingIdx?true:false">&nbsp;&nbsp;&nbsp;{{expertWeldingItem}}
                              <span v-if='idx===weldingIdx'></span>
                            </label>
                        </div>  
                        </div>                                   
                      </div>
                      <p class="welding-title">焊接方法</p>
                      <div class="right setTextAlign">
                          <div class="technology">
                            <div class="title">工&nbsp;艺</div>
                            <p><span>飞溅<i></i></span>:<span>{{WeldingTechnologyData.splash}}</span></p>
                            <p><span>成型<i></i></span>:<span>{{WeldingTechnologyData.model}}</span></p>
                            <p><span>生产效率<i></i></span>:<span>{{WeldingTechnologyData.speed}}</span></p>   
                          </div> 
                          <div class="gas">
                            <div class="title">气&nbsp;体</div>
                            <p><span>成分<i></i></span>:<span>{{WeldingTechnologyData.gas}}</span></p>
                            <p><span>成本<i></i></span>:<span>{{WeldingTechnologyData.cost}}</span></p>
                          </div>
                      </div>
                    </div>
                    <div v-if='modelTypeIndex===1' class="guide">
                        <div class="left">
                          <div class="technology setTextAlign">
                            <div class="title">工&nbsp;艺</div>
                            <p><span>飞溅<i></i></span>:</p>
                            <p><span>成型<i></i></span>:</p>
                            <p><span>生产效率<i></i></span>:</p>   
                          </div> 
                          <div class="effect">
                            <div class="title">效&nbsp;果</div>
                            <p v-for='(item,idx) in newGuideData' :key='idx'>
                                <label v-for='(choose,index) in item.data' :key='index'> 
                                  <input type="radio" :name='item.text'  :checked="index===item.checked?true:false"  @click.stop='chooseEffect(idx,index)'>
                                  {{choose}}
                                  <span v-if='index===item.checked'  @click.stop='chooseEffect(idx,index)'></span>
                                </label>
                            </p>
                          </div>
                        </div>
                        <div class="right">
                          <swiper :options="swiperGuideOption" ref="mySwiperGuide" v-if='!selectedGuideDataIsEmpty'>
                            <!-- slides -->
                              <swiper-slide v-for="(item, index) in selectedGuideMethods" :key="index">
                                  <a href="javascript:;" >
                                      <div  @click='checkedGuideMethods(item)'
                                          :class="['swiper-guide',checkedGuideMethodsData===item?'active':'']">
                                        <div class="name">
                                          <div class="text">
                                            {{item}}
                                          </div>
                                          <div class="ico">
                                              <img src="../../../assets/images/CO2.png" alt="">
                                          </div>
                                        </div>
                                        <p>气体成本：{{selectedGuideData[item].cost}}</p>
                                        <p>保护气体:{{selectedGuideData[item].compose}}</p>
                                        <p><span>robo2025</span></p>
                                      </div>
                                  </a>
                                  <i class="icon-gou iconfont" v-if='checkedGuideMethodsData===item'></i>
                              </swiper-slide>
                            <!-- Optional controls -->  
                              <div class="swiper-button-prev prev" slot="button-prev"></div>
                              <div class="swiper-button-next next" slot="button-next"></div>
                          </swiper>
                          <div class='enterTip' v-if='enterTip'>
                            请选择工艺效果，确定焊接方法
                          </div>
                           <div class='enterTip' v-if='selectedGuideDataIsEmpty&&!enterTip'>
                            暂无匹配
                          </div>
                        </div>
                    </div>
                  </div>
            </div>
            <div v-if='isActive===4' class="frouth-step">
                <div class="left">
                    <div>
                        <p>板材厚度</p>
                        <p><input type="number" v-model='plankVal'>mm</p>
                        <p>参考范围之0.8-110mm</p>
                    </div>
                    <div>
                        <p>作业半径</p>
                        <p><input type="number" v-model='radiuVal'>mm</p>
                        <p>最大作业范围1925mm</p>
                    </div>
                </div>
                <div class="right">
                    <img src="../../../assets/images/plank.png" alt="">
                </div>
            </div>
            <div v-if='isActive===5' class="fifth-step">
              <div class="tab-pane" id="fifthStep">
									<div class="row">
										<div class="col-sm-4 col-sm-offset-1">
											<h4 class="title">
												<i class="icon-liebiao2 iconfont"></i>行业&场景</h4>
											<div class="form-p">
												<p>行业：</p>
												<span>{{industry[chooseIdx].text}} </span>
											</div>
											<div class="form-p">
												<p>场景：</p>
												<span>{{industry[chooseIdx].childChoose[selectedItem]}}</span>
											</div>
										</div>
										<div class="col-sm-4 col-sm-offset-1">
											<h4 class="title">
												<i class="icon-changjingdakai iconfont"></i>焊接方法</h4>
											<div class="form-p">
												<p>母材成分：</p>
												<span>{{metalData[checkedImgData]}}</span>
											</div>
											<div class="form-p">
												<p>焊接方法：</p>
												<span>{{WeldingTechnologyData.method}}</span>
											</div>
											<div class="form-p">
												<p>焊接气体成本：</p>
												<span>{{WeldingTechnologyData.cost}}</span>
											</div>
											<div class="form-p">
												<p>飞溅：</p>
												<span>{{WeldingTechnologyData.splash}}</span>
											</div>
											<div class="form-p">
												<p>成型：</p>
												<span>{{WeldingTechnologyData.model}}</span>
											</div>
											<div class="form-p">
												<p>生产效率：</p>
												<span>{{WeldingTechnologyData.speed}}</span>
											</div>
										</div>
										<div class="col-sm-4 col-sm-offset-1">
											<h4 class="title">
												<i class="icon-jinggaowarning21 iconfont"></i>最大板厚值</h4>
											<div class="form-p">
												<p>最大厚板值：</p>
												<span>{{plankVal}}mm</span>
											</div>
											<div class="form-p">
												<p>最大作业半径：</p>
												<span>{{radiuVal}}mm</span>
											</div>
										</div>
									</div>
								</div>
            </div>
        </div>
        <div class="wizard-footer">
            <div class='prev' v-if="isActive!=1" @click="tabChange(-1)">
                上一步
            </div>
            <div :class="['next',disabled?'disabled':'']" @click="!disabled && tabChange(1)">
                {{msg}}
            </div>
        </div>
  </div>
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
import BScroll from "better-scroll";
export default {
  name: "stepTable",
  data() {
    return {
      isActive: 1,
      isShow: false,
      left: 70,
      arr: [
        "用户场景",
        "选择母材",
        "选择焊接方法",
        "输入板厚及作业范围",
        "需求确认"
      ],
      msg: "下一步",
      // 用户场景的数据
      industry: [
        { text: "家具", icon: "icon-jiaju", childChoose: ["焊接箱体", "其他"] },
        { text: "五金", icon: "icon-wujingongju-", childChoose: [] },
        { text: "汽车", icon: "icon-qiche", childChoose: [] },
        {
          text: "数码3C",
          icon: "icon-shouji",
          childChoose: ["焊接打印机机壳", "其他"]
        },
        {
          text: "建筑施工",
          icon: "icon-shigongzhong",
          childChoose: ["焊接跳板", "焊接门架", "其他"]
        },
        { text: "其他", icon: "icon-yejinkuangchan", childChoose: [] }
      ],
      chooseIdx: 0, //左侧选了第几个
      selectedItem: 0, //右侧选了第几个
      //   母材轮播图
      swiperOption: {
        navigation: {
          nextEl: ".next",
          prevEl: ".prev"
        },
        speed: 500,
        slidesPerView: 3,
        spaceBetween: 0,
        autoplay: false,
        observer: true,
        observeParents: true
      },
      checkedImgData: "", //必须有值
      // 选择焊接方法
      modelType: ["专家", "向导"],
      modelTypeIndex: 0,
      modelTypeLeft: -1,
      // 专家数据
      weldingIdx: 0,
      weldingOptions: {
        scrollY: true,
        mouseWheel: {
          speed: 20,
          invert: false,
          easeTime: 300
        },
        scrollbar: {
          fade: false,
          interactive: false
        }
      },
      // 导购数据
      guideCheckedDate: [
        { text: "splash", checked: 100 },
        { text: "model", checked: 100 },
        { text: "speed", checked: 100 }
      ],
      newGuideData: [],
      enterTip: true,
      checkedGuideMethodsData: "", //最后选择的方法
      // 导购轮播图
      swiperGuideOption: {
        navigation: {
          nextEl: ".next",
          prevEl: ".prev"
        },
        speed: 500,
        slidesPerView: 2,
        spaceBetween: 0,
        autoplay: false,
        observer: true,
        observeParents: true
      },
      // 板材厚度
      plankVal: "",
      // 作业半径
      radiuVal: "",
      disabled: false
    };
  },
  created() {},
  watch: {
    isActive: function() {
      if (this.isActive > 4) {
        this.msg = "提交";
      } else {
        this.msg = "下一步";
      }
      if (this.isActive === 2) {
        if (this.checkedImgData === "") {
          this.disabled = true;
        }
      }
      if (this.isActive === 4) {
        if (this.plankVal === "" || this.radiuVal === "") {
          this.disabled = true;
        } else {
          this.disabled = false;
        }
      }
    },
    checkedImgData: function() {
      if (this.checkedImgData !== "") {
        this.disabled = false;
      }
    },
    modelTypeIndex: function() {
      if (this.modelTypeIndex === 0) {
        this.disabled = false;
        this.updateWeldingTechnology(this.weldingData[0]);
        this.weldingIdx = 0;
      } else {
        this.disabled = true;
        const that = this;
        this.updateWelding({
          metal: this.metalData[this.checkedImgData],
          success: weldingMethod => {
            that.newGuideData = that.guideCheckedDate.map(val => {
              if (that.guideData[val.text]) {
                return {
                  ...val,
                  data: that.guideData[val.text]
                };
              }
              return val;
            });
          }
        });
        this.checkedGuideMethodsData = "";
      }
    },
    checkedGuideMethodsData: function() {
      if (this.checkedGuideMethodsData === "") {
        this.disabled = true;
      } else {
        this.disabled = false;
      }
    },
    weldingData: function() {
      if (this.weldingData.length > 6) {
        this.$nextTick(() => {
          this.myScrollBar = new BScroll(
            this.$refs.wrapper1,
            this.weldingOptions
          );
        });
      }
    },
    newGuideData: {
      handler: function(val) {
        var bool = val.some(mapval => {
          return mapval.checked !== 100;
        });
        this.enterTip = !bool;
      },
      deep: true
    },
    plankVal: function(val) {
      if (this.radiuVal !== "" && this.plankVal !== "") {
        this.disabled = false;
      }
    },
    radiuVal: function(val) {
      if (this.radiuVal !== "" && this.plankVal !== "") {
        this.disabled = false;
      }
    }
  },
  computed: {
    ...mapState({
      metalData: state => state.stepList.metalData,
      weldingData: state => state.stepList.weldingData,
      WeldingTechnologyData: state => state.stepList.WeldingTechnologyData,
      guideData: state => state.stepList.guideData,
      selectedGuideData: state => state.stepList.selectedGuideData,
      selectedGuideDataIsEmpty: state =>
        state.stepList.selectedGuideDataIsEmpty,
      selectedGuideMethods: state => state.stepList.selectedGuideMethods
    })
  },
  methods: {
    ...mapActions({
      updateMetal: "stepList/updateMetal", //请求母材数据
      updateWelding: "stepList/updateWelding", //请求专家焊接方法
      updateWeldingTechnology: "stepList/updateWeldingTechnology", //根据焊接方法请求气体成分
      updateGuide: "stepList/updateGuide", //根据效果请求焊接方法
      updateFilterListAsync: "solutionList/updateFilterListAsync" //请求方案列表
    }),
    // 步骤改变
    tabChange(step) {
      // 获得母材数据
      if (this.isActive === 1 && step > 0) {
        this.updateMetal();
        this.isActive += step;
        this.left += 239.5 * step;
        // 获得焊接方法数据
      } else if (this.isActive === 2 && step > 0) {
        // 判断是否选择母材
        if (this.checkedImgData !== "") {
          const that = this;
          this.updateWelding({
            metal: this.metalData[this.checkedImgData],
            success: weldingMethod => {
              that.updateWeldingTechnology(weldingMethod);
              that.newGuideData = that.guideCheckedDate.map(val => {
                if (that.guideData[val.text]) {
                  return {
                    ...val,
                    data: that.guideData[val.text]
                  };
                }
                return val;
              });
            }
          });
          this.isActive += step;
          this.left += 239.5 * step;
          this.modelTypeIndex = 0;
          this.modelTypeLeft = -1;
          this.weldingIdx = 0;
        } else {
        }
      } else if (this.isActive === 3 && step > 0) {
        if (this.modelTypeIndex === 1 && this.checkedGuideMethodsData === "") {
        } else {
          this.isActive += step;
          this.left += 239.5 * step;
        }
      } else if (this.isActive === 4 && step > 0) {
        if (this.radiuVal === "" || this.plankVal === "") {
        } else {
          this.isActive += step;
          this.left += 239.5 * step;
        }
      }
      if (step < 0) {
        this.isActive += step;
        this.left += 239.5 * step;
        this.disabled = false;
      }
      // 提交，请求方案列表数据，判断是否有符合条件的方案列表
      if (this.msg == "提交" && step > 0) {
        var loader = this.$loading();
        let params = {
          metal: this.metalData[this.checkedImgData],
          welding: this.WeldingTechnologyData.method,
          height: this.plankVal,
          radius: this.radiuVal
        };
        this.updateFilterListAsync({
          params: params,
          success: res => {
            // 有符合条件是数据，跳转到方案列表页
            var stepTableParam = {
              industry: this.industry[this.chooseIdx].text,
              scene: this.industry[this.chooseIdx].childChoose[
                this.selectedItem
              ],
              metalData: this.metalData[this.checkedImgData],
              weldingTechnologyData: this.WeldingTechnologyData,
              plankVal: this.plankVal,
              radiuVal: this.radiuVal
            };
            sessionStorage.setItem(
              "stepTableParam",
              JSON.stringify(stepTableParam)
            );
            this.$router.push("/solutionList");
            loader.close();
          },
          fail: res => {
            loader.close();
            this.$msgbox({
              message: `<div class='warn-box' >
                          <div class='warn-img-box'></div>
                          <h1>暂无符合条件方案</h1>
                          <p>是否发布新的需求？</p>
                        </div>`,
              showCancelButton: true,
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              center: false,
              dangerouslyUseHTMLString: true
            })
              .then(() => {
                //点击确认跳转到方案详情页面
              })
              .catch(() => {
                // 点击取消不执行任何操作
              });
          },
          error: error => {
            // console.log(error);
          }
        });
      }
    },
    // 行业选项改变
    changeChoose(choose) {
      this.chooseIdx = choose;
      this.selectedItem = 0; //初始化右边选项
    },
    // 行业场景内容变化
    itemChange(idx) {
      this.selectedItem = idx;
    },
    // 选择母材
    checkedImg(idx) {
      this.checkedImgData = idx;
    },
    // 选择焊接方法
    // 选择模式
    changeModel() {
      this.modelTypeIndex = this.modelTypeIndex == 0 ? 1 : 0;
      this.modelTypeLeft = this.modelTypeLeft == -1 ? 40 : -1;
    },
    // 选择专家方法
    chooseWelding(idx, weldingMethod) {
      this.weldingIdx = idx;
      this.updateWeldingTechnology(weldingMethod);
    },
    // 选择效果
    chooseEffect(idx, index) {
      this.enterTip = false;
      if (this.newGuideData[idx].checked === index) {
        this.newGuideData[idx].checked = 100;
      } else {
        this.newGuideData[idx].checked = index;
      }
      var data = {};
      var params = this.newGuideData.map(val => {
        if (val.checked !== 100) {
          data[val.text] = val.data[val.checked];
          return { [val.text]: val.data[val.checked] };
        }
      });
      this.updateGuide({ metal: this.metalData[this.checkedImgData], ...data });
    },
    // 选择导向方法
    checkedGuideMethods(method) {
      if (this.checkedGuideMethodsData === method) {
        this.checkedGuideMethodsData = "";
      } else {
        this.checkedGuideMethodsData = method;
      }
      this.updateWeldingTechnology(method);
    }
  }
};
</script>


<style lang="less" scoped>
@import "../../../assets/stepList/css/material-bootstrap-wizard.css";
@import "../../../assets/stepList/css/bootstrap.css";
@import "../../../assets/stepList/fonts/fonts.css";
@import "../../../assets/stepList/css/index.css";
@import url("https://cdn.bootcss.com/animate.css/3.5.1/animate.min.css");

@baseColor: #0275d8;
@fontSize: 1/16rem;
* {
  padding: 0;
  margin: 0;
}
div,
p {
  box-sizing: border-box;
}
.wizard-navigation {
  width: 100%;
  height: 40px;
  display: flex;
  background-color: @baseColor;
  color: #fff;
  line-height: 40px;
  position: relative;
  font-size: 14px;
  .nav-content {
    flex: 1;
    text-align: center;
  }
  .move-nav {
    position: absolute;
    width: 100px;
    top: 35px;
    border-bottom: 1px solid #fff;
    transition: all 0.5s cubic-bezier(0.29, 1.42, 0.79, 1);
  }
}
.wizard-content {
  width: 100%;
  height: 415px;
  padding: 10px 20px;
  box-sizing: border-box;
  select.form-control:focus {
    box-shadow: 0 0 0 0 rgba(0, 123, 255, 0.25);
  }
  select:focus + .line {
    width: 400px;
  }
  input.form-control:focus {
    box-shadow: 0 0 0 0 rgba(0, 123, 255, 0.25);
  }
  .line {
    width: 0;
    height: 2px;
    background-color: @baseColor;
    margin: 0 auto;
    position: absolute;
    top: 98px;
    left: 0px;
    right: 0px;
    transition: width 0.2s linear;
  }
  li {
    float: left;
    padding: 0 15px 0;
    height: 90px;
    width: 50%;
    position: relative;
  }
  .first-step {
    width: 100%;
    height: 100%;
    .title {
      padding: 0 15px 0px;
      height: 65px;
      line-height: 65px;
      font-weight: 700;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        left: 0;
        bottom: -15px;
        width: 100%;
        height: 2px;
        background-color: #0275d8;
        z-index: 1;
      }
    }
    .left {
      width: 40%;
      height: 100%;
      float: left;
      .choose {
        width: 50%;
        height: 90px;
        float: left;
        padding: 0 15px;
        box-sizing: border-box;
        margin: 27px 0 0 0;
        cursor: pointer;
        .icon {
          width: 68px;
          height: 68px;
          border: 4px solid #ccc;
          border-radius: 50%;
          float: left;
          text-align: center;
          vertical-align: middle;
          color: #999999;
          margin: 0 auto 20px;
          transition: all 0.2s;
          -webkit-transition: all 0.2s;
          i {
            font-size: 38px;
            line-height: 61px;
          }
        }
        h5 {
          float: left;
          height: 64px;
          line-height: 71px;
          padding-left: 20px;
          font-size: 1.05rem;
        }
      }
      .choose.active {
        .icon {
          border-color: @baseColor;
          color: #fff;
          background: @baseColor;
        }
        h5 {
          color: @baseColor;
        }
      }
      .choose:hover {
        .icon {
          border-color: @baseColor;
          color: #fff;
          background: @baseColor;
        }
        h5 {
          color: @baseColor;
        }
      }
    }
    .right {
      width: 40%;
      height: 100%;
      float: right;
      .choose-content {
        width: 100%;
        div {
          width: 42%;
          float: left;
          padding: 20px 0px 0;
          cursor: pointer;
          p {
            border: 2px solid #ccc;
            border-radius: 5px;
            color: #999;
            height: 50px;
            text-align: center;
            line-height: 45px;
            font-size: 0.9rem;
            transition: all 0.2s;
            -webkit-transition: all 0.2s;
            position: relative;
            i {
              position: absolute;
              right: -1px;
              bottom: 9px;
              width: 20px;
              height: 20px;
            }
          }
        }
        div:nth-child(even) {
          float: right;
        }
        div:hover p {
          border-color: @baseColor;
          color: @baseColor;
        }
        div.active p {
          border-color: @baseColor;
          color: @baseColor;
        }
        div.chooseNothing {
          width: 100%;
          height: 100%;
          text-align: center;
          line-height: 200px;
          font-size: 20px;
        }
      }
    }
  }
  .second-step {
    width: 100%;
    height: 100%;
    padding: 0 15px;
    position: relative;
    > div {
      width: 100%;
      padding: 0 10px;
      height: 100%;
    }
    // 分割线
    div.separated {
      height: 90%;
      width: 0;
      border-right: 1px solid #cec8c8;
      position: absolute;
      left: 319px;
      top: 5%;
      z-index: 9;
    }
    div.separated.right {
      left: 639px;
    }
    .swiper-slide-next:before {
      position: absolute;
      content: "";
      left: 0;
      top: 15%;
      width: 1px;
      height: 46%;
      background: #cec8c8;
    }
    .swiper-slide-next:after {
      position: absolute;
      content: "";
      right: 0;
      top: 15%;
      width: 1px;
      height: 46%;
      background: #cec8c8;
    }

    .swiper-wrapper {
      padding: 0 70px !important;
    }
    .swiper-container.swiper-container-horizontal {
      overflow: hidden !important;
    }
    .swiper-slide {
      text-align: center;
      background: #fff;
      /* Center slide text vertically */
      display: -webkit-box;
      display: -ms-flexbox;
      display: -webkit-flex;
      display: flex;
      -webkit-box-pack: center;
      -ms-flex-pack: center;
      -webkit-justify-content: center;
      justify-content: center;
      -webkit-box-align: center;
      -ms-flex-align: center;
      -webkit-align-items: center;
      align-items: center;
      a {
        display: block;
        width: 215 * @fontSize;
        height: 200 * @fontSize;
        margin-right: 10 * @fontSize;
        margin-left: 10 * @fontSize;
        img {
          width: 100%;
          height: 100%;
          transition: all 0.25s;
          padding: 5%;
          margin-top: -40px;
          &:hover {
            transform: translateY(-3px);
          }
        }
        p {
          color: #333;
          margin-top: 60px;
        }
        p.pChecked {
          border: 1px solid @baseColor;
          padding: 8px 15px;
          border: 2px solid #0275d8;
          border-radius: 5px;
          position: relative;
          top: -88px;
          background: #fff;
          width: fit-content;
          left: 50%;
          transform: translateX(-50%);
          color: @baseColor;
          transition: all 0.5s;
        }
        &:hover {
          text-decoration: none;
        }
      }
    }
    .swiper-button-prev,
    .swiper-container-rtl .swiper-button-next {
      left: 0px !important;
      top: 125px !important;
    }
    .swiper-button-next,
    .swiper-container-rtl .swiper-button-prev {
      right: 0px !important;
      top: 125px !important;
    }
    .swiper-button-prev,
    .swiper-button-next {
      width: 35px;
      height: 35px;
      background-size: 39%;
      border: 1px solid @baseColor;
      border-radius: 50%;
    }
    .waring {
      position: absolute;
      z-index: 99;
      bottom: 12px;
      left: 50%;
      width: 200px;
      height: 80px;
      border: 1px solid @baseColor;
      background: #fff;
      border-radius: 5px;
      text-align: center;
      line-height: 80px;
      font-size: 20px;
      transform: translateX(-50%);
    }
  }
  .third-step {
    width: 100%;
    height: 100%;
    padding: 10px;
    .title {
      height: 70px;
      line-height: 70px;
      border-bottom: 3px solid @baseColor;
      font-size: 20px;
      b {
        float: left;
      }
      div {
        float: left;
        width: 90px;
        height: 20px;
        border: 1px solid #333;
        border-radius: 25px;
        line-height: 20px;
        font-weight: normal;
        position: relative;
        font-size: 12px;
        text-align: center;
        top: 50%;
        left: 20px;
        transform: translateY(-50%);
        cursor: pointer;
        span {
          width: 50%;
          float: left;
        }
        p {
          position: absolute;
          width: 50px;
          height: 20px;
          left: -1px;
          top: -1px;
          border-radius: 20px;
          background: @baseColor;
          color: #fff;
          cursor: pointer;
          transition: all 0.5s;
        }
      }
    }
    .content {
      width: 100%;
      padding: 55px 0 0 0;
      .experts {
        position: relative;
        p.welding-title {
          position: absolute;
          left: 35px;
          top: -13px;
          background: #fff;
          z-index: 9;
          padding: 0 3px;
          font-weight: 700;
        }
        .left {
          width: 400px;
          height: 210px;
          border: 1px solid #333;
          padding: 20px 25px;
          float: left;
          position: relative;
          overflow: hidden;
          label {
            width: 50%;
            float: left;
            padding: 15px 0;
            color: #000;
            position: relative;
            input {
              padding-right: 5px;
              margin-right: 5px;
            }
            span {
              position: absolute;
              left: 3px;
              top: 20px;
              width: 6px;
              height: 6px;
              border-radius: 50%;
              background: @baseColor;
            }
          }
          .bscroll-containe {
            padding: 0 0 10px 0;
          }
          .bscroll-containe:after {
            content: "";
            display: block;
            clear: both;
          }
        }
        .right {
          width: 680px;
          float: left;
          height: 210px;
          padding: 0 0 0 110px;
          div {
            width: 50%;
            height: 100%;
            float: left;
            .title {
              font-size: 16px;
              font-weight: 700;
              border: 0;
              height: 20px;
              line-height: 1px;
              width: 100%;
              padding: 0;
              &:after {
                clear: both;
                content: "";
                display: block;
              }
            }
            p {
              width: 100%;
              height: 40px;
              line-height: 40px;
              margin: 25px 0 0 0;
              span {
                
                &:last-of-type {
                  display: inline-block;
                  min-width: 60px;
                  height: 40px;
                  border-bottom: 1px solid #333;
                  text-align: center;
                  line-height: 40px;
                  margin-left: 30px;
                }
              }
            }
          }
        }
      }
      .guide {
        .left {
          width: 476px;
          height: 210px;
          padding: 0 25px;
          float: left;
          .technology {
            width: 90px;
            height: 210px;
            float: left;
            .title {
              font-size: 16px;
              font-weight: 700;
              border: 0;
              height: 50px;
              line-height: 50px;
              width: 100%;
              padding: 0;
            }
            p {
              height: 40px;
              line-height: 40px;
              margin: 0;
            //   span:first-of-type {
            //     text-align: justify;
            //     text-align-last: justify;
            //     -webkit-text-align-last: justify;
            //     vertical-align: top;
            //     display: inline-block;
            //     width: 80%;
            //     margin-right: 5%;
            //   }
            }
          }
          .effect {
            float: left;
            // width: 299px;
            .title {
              font-size: 16px;
              font-weight: 700;
              border: 0;
              height: 50px;
              line-height: 50px;
              width: 100%;
              padding: 0;
              text-align: center;
            }
            p {
              width: 100%;
              height: 40px;
              border-bottom: 1px solid #000;
              color: #000;
              margin: 0;
              margin-bottom: 20px;
              label {
                margin: 0;
                line-height: 40px;
                color: #000;
                font-size: 15px;
                position: relative;
                width: 67px;
                height: 40px;
                input {
                    margin-right: 5px;
                }
                span {
                  position: absolute;
                  left: 3px;
                  top: 16px;
                  width: 6px;
                  height: 6px;
                  border-radius: 50%;
                  background: @baseColor;
                }
                // &:nth-child(1) > span {
                //   left: 15px;
                // }
              }
            }
          }
        }
        .right {
          width: 660px;
          height: 210px;
          padding: 0 20px;
          float: right;
          position: relative;
          .swiper-container,
          .swiper-container-horizontal {
            height: 100%;
            padding: 0 18px;
            .swiper-slide {
              width: 50% !important;
              height: 103%;
              padding: 5% 4% 4%;
              position: relative;
              i {
                position: absolute;
                right: 23px;
                bottom: 24px;
                width: 20px;
                height: 16px;
                border-bottom-right-radius: 6px;
                overflow: hidden;
                color: @baseColor;
              }
              a {
                color: #000;
                &:hover {
                  text-decoration: none;
                }
              }
              .active {
                border: 1px solid @baseColor !important;
                overflow: hidden;
              }
              .swiper-guide {
                width: 100%;
                height: 100%;
                border: 1px solid #333;
                border-radius: 5px;
                padding: 10px;
                .name {
                  width: 100%;
                  height: 40px;
                  margin-bottom: 10px;
                  .text {
                    width: 50%;
                    height: 100%;
                    line-height: 40px;
                    float: left;
                    border-bottom: 1px solid #333;
                    color: #000;
                    font-weight: 700;
                  }
                  .ico {
                    width: 50%;
                    height: 100%;
                    float: left;
                    img {
                      margin: 0 auto;
                    }
                  }
                }
                p {
                  height: 30px;
                  line-height: 30px;
                  font-size: 14px;
                  font-weight: 700;
                  margin: 0 !important;
                }
                p:last-child {
                  width: 80%;
                  margin: 0 auto !important;
                  border-bottom: 1px solid #c5bfbf;
                  position: relative;
                  height: 23px;
                  span {
                    position: absolute;
                    left: 50%;
                    -webkit-transform: translateX(-50%);
                    transform: translateX(-50%);
                    top: 7px;
                    background: #fff;
                    display: block;
                    padding: 0 7px;
                    color: #c5bfbf;
                    font-weight: normal;
                  }
                }
              }
            }
            .swiper-button-prev,
            .swiper-container-rtl .swiper-button-next {
              left: -10px !important;
              top: 99px !important;
            }
            .swiper-button-next,
            .swiper-container-rtl .swiper-button-prev {
              right: -10px !important;
              top: 99px !important;
            }
            .swiper-button-prev,
            .swiper-button-next {
              width: 30px;
              height: 30px;
              background-size: 39%;
            }
          }
          .enterTip {
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            background: #fff;
            text-align: center;
            line-height: 210px;
            font-size: 22px;
            font-weight: 700;
            z-index: 99;
          }
        }
      }
    }
    .line {
      top: 72px;
    }
    select:focus + .line {
      width: 396px;
    }
  }
  .frouth-step {
    width: 100%;
    height: 100%;
    padding: 5% 10%;
    .left,
    .right {
      height: 100%;
      float: left;
    }
    .left {
      width: 40%;
      div {
        width: 100%;
        height: 50%;
        margin-bottom: 20px;
        p {
          width: 100%;
          height: 40px;
          line-height: 40px;
          margin: 0;
          font-size: 18px;
          font-weight: 600;
          input {
            height: 30px;
            width: 250px;
            margin-right: 10px;
            border: 2px solid #000;
            margin-top: 10px;
            padding: 0 5px;
            font-size: 14px;
          }
          &:last-child {
            font-size: 12px;
            height: 30px;
            line-height: 30px;
            font-weight: normal;
            color: #666;
          }
        }
      }
    }
    .right {
      width: 60%;
      img {
        display: block;
        margin: 0 auto;
        position: relative;
        right: -44px;
      }
    }
  }
  .fifth-step {
    width: 100%;
    height: 100%;
    padding: 0 67px;
    .col-sm-4 {
      padding: 0;
      text-align: center;
      .title {
        border-bottom: 3px solid @baseColor;
        padding-bottom: 15px;
        height: 75px;
        line-height: 80px;
        font-size: 20px;
        margin-bottom: 10px;
        i {
          position: relative;
          top: 4px;
          padding-right: 6px;
          font-size: 38px;
          font-weight: 400;
        }
      }
      .form-p {
        width: 100%;
        height: 35px;
        line-height: 35px;
        p {
          float: left;
          text-align: right;
          margin: 0;
          font-weight: 700;
        }
        span {
          float: left;
          text-align: left;
        }
      }
      &:first-child {
        text-align: left;
        .title i {
          font-size: 20px;
          top: 0px;
        }
      }
      &:nth-child(2) {
        p {
          width: 58%;
          text-align: right;
        }
      }
      &:last-child {
        text-align: right;
        p {
          width: 80%;
          text-align: right;
        }
      }
    }
  }
}
.wizard-footer {
  width: 100%;
  height: 60px;
  padding: 0 20px;
  box-sizing: border-box;
  div {
    width: 140px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    background: @baseColor;
    border-radius: 3px;
    cursor: pointer;
  }
  div.prev {
    float: left;
  }
  div.next {
    float: right;
  }
  div.disabled {
    background: #666;
    cursor: not-allowed;
  }
}
input[type="number"] {
  -moz-appearance: textfield;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.setTextAlign {
    p {
        margin-bottom: 20px!important;
        span:first-of-type{
            height:40px;
            line-height:40px;
            width:65px;
            text-align:justify;
            display:inline-block;
            overflow:hidden;
            vertical-align: -14px;;
            margin-right: 5px;
            i {
                display:inline-block;
                width:100%;
                height:0;
            }
        }
    }
}
</style>






