
const loginRegister = {
    namespaced: true,
    state:{
        userInfo:{}, //存储用户信息
        userInfoShow:false,//用来判断是否显示用户信息
    },
    getters: {

    },
    mutations:{
        updateUserInfo(state, data) {
            state.userInfo = {};
            state.userInfo = data;
            state.userInfoShow = true;
        }
    },
    actions:{
        login(state,data){//登录
            var that = this._vm;
            var loader = that.$loading({ fullscreen: true });
            var nextPath = encodeURIComponent(`${location.origin}/#/verify`);//转码后可以成功跳转
            let url = that.$urlConfig.config.loginUrl +"/server/authorize"+ "?" + "next="+ nextPath + "&disable_redirect=" + (data||0);
            localStorage.setItem("originPath",location.href);
            location.href = url;
            loader.close();
        },
        register(){//注册
            var that = this._vm;
            var loader = that.$loading({ fullscreen: true });
            var nextPath = encodeURIComponent(`${location.origin}/#/verify`);//转码后可以成功跳转
            let url = that.$urlConfig.config.loginUrl + "/register" + "?" + "next="+ nextPath;;
            localStorage.setItem("originPath",location.href);
            location.href = url;
            loader.close();
        },
        loginOut(store){//退出登录
            var that = this._vm;
            let url = that.$urlConfig.config.loginUrl + "/logout";
            var token =that.$Cookies.get('accessData');
            that.$Cookies.remove("userInfo");
            var callbackUrl = location.href;
            location.href = url + "?" + "access_token=" + token + "&next=" + callbackUrl;
        },
        // 获取用户名
        getUserInfo(store){
            var that = this._vm;
            let url = that.$urlConfig.config.loginUrl + "/server/verify";
            that.$http({
                methods:'get',
                url:url
            }).then((res)=>{
                store.commit('updateUserInfo', res.data.data)
            }).catch((error)=>{
                console.log(error.response)
            })
        },
    }
}

export default loginRegister;


