<template>
    <div class="product-select container">
        <Title
            title='智能匹配'
            iconClass='icon-wangyuanjing'
            class="title"
        />
        <el-row class="filter">
            <el-col class="brand-filter" :span='9'>
                <BrandFilter
                    title='选择伺服电机品牌'
                />
            </el-col>
            <el-col class="img" :span='6'>
                <img src="../assets/images/arrow.png" alt="">
            </el-col>
            <el-col class="type-filter" :span='9'>
                <TypeFilter
                 title='选择伺服电机型号'/>
            </el-col>
        </el-row>
        <ListMethod/>
    </div>
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
import Title from "../components/title/title.vue";
import BrandFilter from "../components/FilterItem/main/BrandFilter.vue";
import TypeFilter from '../components/FilterItem/main/TypeFilter.vue';
import ListMethod from'../components/productListDetail/main/ListMethod.vue';
export default {
  components: {
    Title,
    ListMethod,
    BrandFilter,
    TypeFilter
  },
  created() {
    this.productParam = { offset: 0, limit: this.limit };
  },
  mounted() {
    this.updateProductListAsync({
      params: { offset: 0, limit: this.limit },
      mutionType: "updateproductList",
      dataType: "productList"
    }); // 初始化页面数据
  },
  data() {
    return {
      limit: 10,
      productParam: {}
    };
  },
  methods: {
    // ...mapMutations(["updateCategoryDisplay"]),
    ...mapActions(["updateProductListAsync"]),
    updateProductList: function(param) {
      this.productParam = param;
      this.updateProductListAsync({
        params: param,
        mutionType: "updateproductList",
        dataType: "productList"
      });
    }
  },
  computed: {
    ...mapState(["productList", "totalCount"]),
    ...mapGetters(["listCurrpage"])
  }
};
</script>
<style lang="less" scoped>
@baseColor:#0275d8;
.container {
  .title {
    margin: 20px 0;
  }
  .filter {
    height: 300px;
    div {
      height: 300px;
    }
    .img {
      display: flex;
      align-items: center;
      img {
        width: 70px;
      }
    }
  }
}
</style>




