const order = {
    namespaced: true,
    state:{
        addressList:[],
        invoiceList:[],
        productList:[],
        orderSuccessCode:{},
        orderInfo:[]
    },
    getters:{

    },
    mutations:{
        updateAddressList(state, data) {
            state.addressList = data;
        },
        updateInvoiceList(state, data) {
            state.invoiceList = data;
        },
        updateProductList(state, data) {
            state.productList = data;
        },
        updateorderSuccessCode(state, data) {
            state.orderSuccessCode = data;
        },
        updateorderInfo(state,data){
            state.orderInfo = data;
        }

    },
    actions:{
        updateAddressListAsync(store){
            var that = this._vm;
            var loader = that.$loading({ fullscreen: true });
            let url = that.$urlConfig.config.userManagerUrl + '/customer/addresses';
            that.$http({
                method: 'get',
                url: url
            }).then((res) => {
                loader.close();
                store.commit('updateAddressList', res.data.data);
            }).catch((error) => {
            })
        },
        addAddress(store,param) {
            var that = this._vm;
            let url = that.$urlConfig.config.userManagerUrl + '/customer/addresses';
            that.$http({
                method: 'post',
                url: url,
                data:param.params
            }).then((res) => {
                param.success(res);
            }).catch((error) => {
                param.error(error);
            })
        },
        editAddress(store,param) {
            var that = this._vm;
            let url = that.$urlConfig.config.userManagerUrl + '/customer/addresses/'+param.addressId;
            that.$http({
                method: 'put',
                url: url,
                data:param.params
            }).then((res) => {
                param.success(res);
            }).catch((error) => {
                param.error(error);
            })
        },
        deleteAddress(store,param) {
            var that = this._vm;
            let url = that.$urlConfig.config.userManagerUrl + '/customer/addresses/'+param.addressId;
            that.$http({
                method: 'delete',
                url: url,
            }).then((res) => {
                param.success(res);
            }).catch((error) => {
                param.error(error);
            })
        },
        updateInvoiceListAsync(store) {
            var that = this._vm;
            var loader = that.$loading({ fullscreen: true });
            let url = that.$urlConfig.config.userManagerUrl + '/customer/invoices';
            that.$http({
                method: 'get',
                url: url
            }).then((res) => {
                loader.close();
                store.commit('updateInvoiceList', res.data.data);
            }).catch((error) => {
                loader.close();
            })
        },
        addInvoice(store,param){
            var that = this._vm;
            let url = that.$urlConfig.config.userManagerUrl + '/customer/invoices';
            that.$http({
                method: 'post',
                url: url,
                data:param.params
            }).then((res) => {
                // console.log(res)
                param.success(res);
            }).catch((error) => {
                // console.log(error)
                param.error(error);
            })
        },
        updateProductListAsync(store,param){
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + '/carts?'+param.params;
            var loader = that.$loading({ fullscreen: true });
            that.$http({
                method: 'get',
                url: url,
            }).then((res) => {
                var rescode = parseInt(res.data.rescode);
                if(rescode <= 10000) {
                    store.commit('updateProductList',res.data.data);
                } else {
                    param.success(res)
                }
                loader.close();
            }).catch((error) => {
                param.error(error);
                loader.close();
            })
        },
        createOrder(store,param){
            var that = this._vm;
            let url = that.$urlConfig.config.submitOrderUrl + '/v1/user/order';
            that.$http({
                method:"post",
                url:url,
                data:param.params
            }).then((res)=>{
                var rescode = parseInt(res.data.rescode);
                if(rescode <= 10000) {
                    store.commit('updateorderSuccessCode',res.data.data);
                    param.success(res)
                } else {
                    param.fail(res)
                }
            }).catch((error) => {
                param.error(error)
            })
        },
        updateorderInfoAsync(store,param){
            var that = this._vm;
            let url = that.$urlConfig.config.submitOrderUrl + '/v1/user/order/'+param.orderId + "?order_type=2";
            that.$http({
                method: 'get',
                url: url,
            }).then((res) => {
                var rescode = parseInt(res.data.rescode);
                if(rescode <= 10000) {
                    store.commit('updateorderInfo',res.data.data);
                    param.success(res)
                } else {
                    param.fail(res)
                }
            }).catch((error) => {
                param.error(error);
            })
        }

    }

}

export default order;


