<template>
  <div class="productBigImg">
      <el-row class="noTipInfo" v-if="searchList.list_data&&searchList.list_data.length <= 0">
            <el-col>
                暂无相关商品<router-link to="/"><el-button type="primary">去逛逛</el-button></router-link>
            </el-col>
        </el-row>
      <ul class="clearfix">
          <li v-for="(item, index) in searchList.list_data" :key="index" class="productDescription">
               <router-link :to="'/seriesInfo/'+item.pno">
                    <img :src="item.img_url" alt="">
                </router-link>
                <router-link :to="'/seriesInfo/'+item.pno">
                    <p class="desc">{{item.product_name}}</p>
                </router-link>
                <p>参&nbsp;&nbsp;考&nbsp;&nbsp;价 : <span class="price"> &yen; {{item.price}}</span></p>
                <p>发&nbsp;&nbsp;货&nbsp;&nbsp;日 : <span> {{item.lead_time}}</span></p> 
                <p>数量折扣 : <span>{{item.price_count >= 0?'有': '无'}}</span></p>
          </li>
      </ul>
    </div>
</template>
<script>
import { mapState, mapActions } from "vuex";
export default {
  data() {
    return {};
  },
  mounted() {},
  computed: {
    ...mapState({
      searchList: state => state.searchList.searchList
    })
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.noTipInfo {
    text-align: center;
  height: 300px;
  line-height: 300px;
  font-size: 16px;
  a {
    margin-left: 20px;
    font-size: 16px;
  }
}
.productDescription {
  width: 30%;
  margin-top: 40px;
  padding: 15px;
  box-sizing: border-box;
  box-shadow: 0px 0px 1px 2px rgba(0, 0, 0, 0.02);
  transition: all 0.5s;
  font-size: 14 * @fontSize;
  margin-right: 5%;
  float: left;
  &:nth-child(3n) {
    margin-right: 0;
  }
  &:hover {
    box-shadow: 0px 0px 1px 2px rgba(0, 0, 0, 0.1);
  }
  img {
    width: 180 * @fontSize;
    height: 180 * @fontSize;
    display: block;
    margin: 0 auto;
    cursor: pointer;
  }
  .price {
    color: #0275d8;
    font-weight: 700;
    display: inline-block;
  }
  .desc {
    height: 50px;
    text-align: left;
    color: #333;
    font-size: 14 * @fontSize;
    cursor: pointer;
    font-weight: 600;
    margin: 0;
    margin-top: 30px;
  }
}
</style>



