const shopcar = {
    namespaced: true,
    state:{
        shopcarList:[]
    },
    getters:{

    },
    mutations:{
        updateshopcarList(state,data){//初始化购物车列表数据
            state.shopcarList = [];
            state.shopcarList = data;
        },
    },
    actions:{
        updateshopcarListAsync(store,param) {
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + '/carts';
            that.$http.get(url,{
            
            }).then((res) => {
                console.log(res)
                var rescode = parseInt(res.data.rescode);
                if(rescode <= 10000){
                    store.commit('updateshopcarList',res.data.data);
                    param.success(res)
                } else {
                    param.fail(res);
                }
                
            }).catch((error) => {
                // param.error(error);
            })
        },
        addToShopcar(state,param) {
            var that = this._vm;
            var loader = that.$loading({ fullscreen: true });
            let url = that.$urlConfig.config.seriesUrl + '/carts';
            that.$http({
                method:'post',
                url:url,
                data:param.params
            }).then((res) => {
                param.success(res);
                loader.close();
            }).catch((error)=>{
                param.error(error);
                loader.close();
            })
        },
        saveGoods(state,param){
            var that = this._vm;
            var loader = that.$loading({ fullscreen: true });
            let url = that.$urlConfig.config.userManagerUrl + '/customer/favorites';
            that.$http({
                method:'post',
                url:url,
                data:{gno:param.params}
            }).then((res) => {
                param.success(res);
                loader.close();
            }).catch((error)=>{
                param.error(error);
                loader.close();
            })
        }
    }

}

export default shopcar;


