<template>
    <div class="container">
        <div class="paySuccess">
            <div>
                <div class="swal-icon swal-icon--success">
                    <span class="swal-icon--success__line swal-icon--success__line--long"></span>
                    <span class="swal-icon--success__line swal-icon--success__line--tip"></span>
                    <div class="swal-icon--success__ring"></div>
                    <div class="swal-icon--success__hide-corners"></div>
                </div>
                <span class="tipInfo">支付成功！</span>
            </div>
            <div class="paySuccessOrder">
            	<div><span>订&nbsp;&nbsp;单&nbsp;&nbsp;号：</span><strong>{{payInfo.order_no}}</strong></div>
            	<div class="payMoney"><span>支付金额：</span><strong>&yen; {{payInfo.amount}}</strong></div>
            </div>
            <div class="paySuccessDetail">
                <router-link to="/">
                    <el-button type="primary" class="goIndex">继续购物</el-button>
                </router-link>
            </div>
        </div>
    </div>
</template>
<script>
import {mapState,mapActions} from 'vuex';
export default {
  data(){
      return {

      }
  },
  mounted(){
      this.getPayInfo({
          params:location.href.split("?")[1].split("=")[1],
          success:(res)=>{
              console.log(res)
          },
          fail:(res)=>{
              console.log(res)
          },
          error:(error)=>{
              console.log(error)
          }
      })
  },
  methods:{
      ...mapActions({
          getPayInfo:"payment/getPayInfo"
      })
  },
  computed:{
      ...mapState({
          payInfo:state=>state.payment.payInfo
      })
  }
}
</script>
<style lang="less" scoped>
@import "../assets/sweetAlert/sweetAlert.css";
@baseColor: #0275d8;
@fontSize: 1/16rem;
.paySuccess {
    box-shadow: 0px 1px 4px 1px rgba(0, 0, 0, 0.08);
    margin: 80*@fontSize 0;
    padding: 80*@fontSize 0;
    text-align: center;
    font-size: 20*@fontSize;
    .tipInfo {
        font-size: 35px;
        font-weight: 600;
    }
    .paySuccessOrder {
        display: inline-block;
        text-align: left;
        margin-top: 50*@fontSize;
        font-size: 16*@fontSize;
        >div {
            height: 50*@fontSize;
            line-height: 50*@fontSize;
            strong {
                color: @baseColor;
                margin-left: 8*@fontSize;
            }
        }

    }
    .goIndex {
        background-color: @baseColor;
        border-color: @baseColor;
        margin-top: 50*@fontSize;
        font-size: 18*@fontSize;
        letter-spacing: 2px;
        margin-bottom: 30*@fontSize;
    }
}
.swal-icon {
	display: inline-block;
    margin: 0;
    vertical-align: -30px;
    margin-right: 20px;
}
.swal-icon--success__line {
	background-color: rgba(2, 117, 216, 0.9);
}
.swal-icon--success__ring {
	border: 4px solid rgba(2, 117, 216, 0.3);
}
.swal-icon--success {
	border-color: rgba(2, 117, 216, 0.9);
}
</style>



