<template>
<div>
    <el-row class="filter" v-for="(item, index) in seriesInfoData" :key="index">
        <el-col :span="5" class="filterItem" v-if="item.specs.length > 0">
             <div v-for="filterItem in item.specs" :key="filterItem.id">
                <p>{{filterItem.spec_unit === '' ? filterItem.spec_name:filterItem.spec_name+'('+filterItem.spec_unit+')'}}</p>
                <div class="wrapper" ref="wrapper">
                     <div class="wrapper-box">
                         <ul class="bscroll-container">
                            <li @click="changeSelected" key="-1" :data-name="filterItem.spec_name" data-value="-1"> 
                                <label>
                                    <input type="radio" 
                                        :name="filterItem.spec_name"
                                        checked
                                    > 
                                    <span>全部</span>
                                </label>
                            </li>
                            <li 
                                v-for='(choose,idx) in filterItem.spec_values'
                                @click="changeSelected"
                                :key="idx"
                                :data-value="choose"
                                :data-name="filterItem.spec_name"
                            > 
                                <label>
                                    <input type="radio" 
                                        :name="filterItem.spec_name" 
                                    > 
                                    <span>{{choose}}</span>
                                </label>
                            </li>
                         </ul>
                     </div>
                 </div>
            </div> 
        </el-col>
        <el-col :span="item.specs.length > 0?18 : 24" :offset="item.specs.length > 0?1 : 0" class="seriesTable">
            <el-row class="noTipInfo" v-if="tableData.length <= 0">
                <el-col>
                    暂无相关商品<router-link to="/"><el-button type="primary">去逛逛</el-button></router-link>
                </el-col>
            </el-row>
            <div v-else>
                <el-table
                :data="tableData"
                border
                style="width: 100%">
                <el-table-column
                    :resizable = false
                    align="center"
                    fixed
                    prop="partnumber"
                    label="型号"
                    width="180">
                </el-table-column>
                <el-table-column
                :resizable = 'false'
                align="center"
                fixed
                  prop="price"
                  label="价格(元)"
                  width="120">
                </el-table-column>
                <el-table-column
                :resizable = 'false'
                v-for="(itemSpec, ids) in tableData[0].specs" :key="ids"
                align="center"
                  :label="itemSpec.spec_unit === '' ? itemSpec.spec_name:itemSpec.spec_name+'('+itemSpec.spec_unit+')'"
                  default="无"
                  min-width="116">
                  <template slot-scope="scope">
                        <span v-for="(specValue,valueIndex) in specParams[scope.$index]" :key="valueIndex">
                            <span v-if="specValue.spec_name === itemSpec.spec_name">{{specValue.spec_value || "无"}}</span>
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                :resizable = 'false'
                align="center"
                  label="编辑"
                  fixed="right"
                  width="120">
                   <template slot-scope="scope">
                        <el-button @click="tableClick(scope.row)" type="primary">购买</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination">
                <el-pagination
                v-if="totalTablePage > 0"
                layout="prev, pager, next"
                @current-change = "changePagination"
                :total="totalTablePage">
                </el-pagination>
            </div>
            </div>
        </el-col>
    </el-row>
    <SeriesInfoModal :dialogShow="dialogShow" :rowData="rowData" @chageDialogShow="chageDialogShow"/>
</div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import SeriesInfoModal from "./ProductInfoModal";
import BScroll from "better-scroll";
export default {
  components: {
    SeriesInfoModal
  },
  props: {
    activeName: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      limit: 10,
      specParams: [], // 表格中不固定参数
      tableData: [],
      totalTablePage: 0,
      filterParams: {},
      dialogShow: false,
      rowData: {},
      checked: true
    };
  },
  mounted() {
    //   console.log(this.$route.query);
    //   var keyArr = Object.keys(this.specValue);
    //   var valueArr = [];
    //   keyArr.forEach(item => {
    //     valueArr.push(this.specValue[item]);
    //   });
    let qs = require("qs");
    let paramString = qs.stringify({ limit: this.limit, offset: 0 });
    this.specParams = [];
    this.tableData = [];
    this.totalTablePage = -1;
    this.updateSeriesModalList({
      param1: this.$route.params.id,
      param2: paramString,
      success: res => {
        this.totalTablePage = parseInt(res.headers["x-content-total"]);
        this.tableData = res.data.data;
        res.data.data.forEach((item, index) => {
          this.specParams.push(item.specs);
        });
      },
      fail: res => {
        // console.log(res);
      },
      error: error => {
        // console.log(error);
      }
    });
  },
  methods: {
    ...mapActions({
      updateSeriesModalList: "seriesInfo/updateSeriesModalList"
    }),
    changeSelected(e) {
      //改变选择状态，获取选中的值，值为-1时，默认选择全部
      var loader = this.$loading({ fullScreen: true });
      this.totalTablePage = -1; // 当改变筛选条件时，页面从第一页开始
      let value = e.currentTarget.dataset.value;
      let name = e.currentTarget.dataset.name;
      if (value === "-1") {
        delete this.filterParams[name];
      } else {
        this.filterParams[name] = value;
      }
      var keyArr = Object.keys(this.filterParams);
      var valueArr = [];
      keyArr.forEach(item => {
        valueArr.push(this.filterParams[item]);
      });
      let qs = require("qs");
      let paramString = qs.stringify(
        {
          spec_names: keyArr,
          spec_values: valueArr,
          limit: this.limit,
          offset: 0
        },
        { indices: false }
      );
      this.updateSeriesModalList({
        param1: this.$route.params.id,
        param2: paramString,
        success: res => {
          this.totalTablePage = parseInt(res.headers["x-content-total"]);
          this.specParams = [];
          this.tableData = res.data.data;
          res.data.data.forEach((item, index) => {
            this.specParams.push(item.specs); // 每一列对应不同参数的值
          });
          loader.close();
        },
        fail: res => {
          //   console.log(res);
          loader.close();
        },
        error: error => {
          //   console.log(error);
          loader.close();
        }
      });
    },
    tableClick(row) {
      this.dialogShow = true;
      this.rowData = row;
    },
    changePagination(page) {
      var loader = this.$loading({ fullScreen: true });
      var offset = (page - 1) * this.limit;
      var keyArr = Object.keys(this.filterParams);
      var valueArr = [];
      keyArr.forEach(item => {
        valueArr.push(this.filterParams[item]);
      });
      let qs = require("qs");
      let paramString = qs.stringify(
        {
          spec_names: keyArr,
          spec_values: valueArr,
          limit: this.limit,
          offset: offset
        },
        { indices: false }
      );
      this.updateSeriesModalList({
        param1: this.$route.params.id,
        param2: paramString,
        success: res => {
          this.totalTablePage = parseInt(res.headers["x-content-total"]);
          this.specParams = [];
          this.tableData = res.data.data;
          res.data.data.forEach((item, index) => {
            this.specParams.push(item.specs); // 每一列对应不同参数的值
          });
          loader.close();
        },
        fail: res => {
          //   console.log(res);
          loader.close();
        },
        error: error => {
          //   console.log(error);
          loader.close();
        }
      });
    },
    chageDialogShow() {
      this.dialogShow = false;
      this.rowData = {};
    }
  },
  computed: {
    ...mapState({
      seriesInfoData: state => state.seriesInfo.seriesInfoData
    })
  },
  watch: {
    activeName(newvalue, oldvalue) {
      if (newvalue === "second") {
        const options = {
          scrollY: true,
          mouseWheel: {
            speed: 20,
            invert: false,
            easeTime: 300
          },
          scrollbar: {
            fade: false,
            interactive: true
          }
        };
        this.seriesInfoData[0].specs.forEach((item, index) => {
          if (item.spec_values.length > 2) {
            new BScroll(this.$refs.wrapper[index], options);
          }
        });
      }
    },
    seriesInfoData(newvalue, oldvalue) {// 搜索时不执行mounted
      let qs = require("qs");
      let paramString = qs.stringify({ limit: this.limit, offset: 0 });
      this.specParams = [];
      this.tableData = [];
      this.totalTablePage = -1;
      this.updateSeriesModalList({
        param1: this.$route.params.id,
        param2: paramString,
        success: res => {
          this.totalTablePage = parseInt(res.headers["x-content-total"]);
          this.tableData = res.data.data;
          res.data.data.forEach((item, index) => {
            this.specParams.push(item.specs);
          });
        },
        fail: res => {
          // console.log(res);
        },
        error: error => {
          // console.log(error);
        }
      });
    }
  }
};
</script>

<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.filter {
  .filterItem {
    .wrapper {
      position: relative;
      overflow: hidden;
      max-height: 120px;
      border: 1px solid #ddd;
    }
    > div {
      margin-bottom: 30 * @fontSize;
    }
    p {
      background-color: rgb(41, 147, 240);
      margin: 0;
      height: 47px;
      line-height: 47px;
      color: #fff;
      font-weight: 600;
      padding-left: 10px;
      font-size: 14px;
    }
    ul {
      background-color: #fff;
      border-top: none;
      li {
        padding-left: 3px;
        height: 40px;
        line-height: 40px;
        cursor: pointer;
        label {
          width: 100%;
          display: block;
          height: 100%;
          span {
            font-size: 12px;
          }
        }
      }
      li:hover {
        background-color: rgba(2, 117, 216, 0.03);
      }
    }
  }
  .noTipInfo {
    height: 300px;
    line-height: 300px;
    font-size: 16px;
    text-align: center;
    a {
      margin-left: 20px;
      font-size: 16px;
    }
  }
  .seriesTable {
    font-size: 12px;
    .pagination {
      text-align: right;
      margin-top: 20 * @fontSize;
      margin-right: -15px;
    }
  }
}
</style>



