<template>
  <div class="container" id="shopcar">
      <div class="warmTip">
            <div class="warmTipLeft">温馨提示：</div>
            <div class="warmTipRight">
            	<p>1.建议您先登录，再把产品加入购物车，以便工业魔方为您提供更精准的产品范围及价格</p>
            	<p>2.购物车内产品显示"失效"的原因可能为：① 商品已经下架; ② 价格异常;</p>
            </div>
        </div>
        <div>
            <el-row class="shopcartitle">
                <el-col :span="2" class="shopcarInput">
                    <input type="checkbox" id="shopcarLabel1" :checked="checked"  @click="checkedAll"> <label for="shopcarLabel1">全选</label>
                </el-col>
                <el-col :span="7">
                    商品信息
                </el-col>
                <el-col :span="4">
                    单价(元)
                </el-col>
                <el-col :span="3">
                    数量
                </el-col>
                <el-col :span="4">
                    小计(元)
                </el-col>
                 <el-col :span="4">
                   操作
                </el-col>
            </el-row>
            <div>
                <el-row v-if="initShopcarData.length <= 0">
                    <el-col class="noGoodsTip">
                        暂无相关商品,<router-link to="/"><el-button type="primary">去逛逛</el-button></router-link>
                    </el-col>
                </el-row>
               <el-row class="shopcarList" v-for="(item, index) in initShopcarData" :key="index">
                    <el-col :span="1" class="shopcarInput">
                          <input type="checkbox" v-model="singleChecked[index]" :data-index="index">
                      </el-col>
                      <el-col :span="8">
                          <div class="productDetailLeft">
                              <router-link :to="'/modalNumberInfo/'+item.goods.partnumber">
                                  <img :src="item.goods.product.cover_url" alt="">
                              </router-link>
                          </div>
                          <div class="productDetailRight">
                              <router-link :to="'/modalNumberInfo/'+item.goods.partnumber">
                                  <h5 :title="item.goods.product.product_name">{{item.goods.product.product_name}}</h5> 
                              </router-link>
                              <div><span>订货号：</span><span>{{item.gno}}</span></div> 
                              <div>
                                  <span>起订量：</span><span>{{item.goods.min_buy}}{{item.goods.sales_unit}}</span>
                              </div>
                              <div><span>库存：</span><span>{{item.goods.stock}}</span></div></div>
                      </el-col>
                      <el-col :span="4" class="line-height_180">
                               &yen; {{singlePrices[index]}}
                      </el-col>
                      <el-col :span="3">
                            <CountChange class="countChange"
                            :min="item.goods.min_buy" :max="item.goods.stock" :quantity="item.quantity" 
                            :cartId="item.id" :prices="item.goods.prices" @changeCount="changePrice" :index="index"/>  
                      </el-col>
                      <el-col :span="4" class="line-height_180">
                              <strong>&yen; {{singleAllPrices[index]}}</strong>
                      </el-col>
                      <el-col :span="4" class="line-height_180">
                          <div>
                              <el-button type="primary" plain :id="item.gno" @click="save">收藏</el-button>
                              <el-button plain :id="item.id" @click="deleteGoods" :data-index="index">删除</el-button>
                          </div>
                      </el-col>
                </el-row>
            </div>
              <el-row class="shopcartitle">
                <el-col :span="2" class="shopcarInput">
                    <input type="checkbox" id="shopcarLabel2" :checked="checked" @click="checkedAll"> <label for="shopcarLabel2">全选</label>
                </el-col>
                <el-col :span="3" :offset="11" class="totalCount">
                    已选 <span class="el-icon-loading" v-show="!hasLoading"></span><strong v-show="hasLoading">{{totalCount}}</strong>件商品
                </el-col>
                <el-col :span="4">
                    应付总价: <span class="el-icon-loading" v-show="!hasLoading"></span><strong v-show="hasLoading">&yen; {{totalPrices}}</strong>元
                </el-col>
                 <el-col :span="3" :offset="1">
                    <el-button type="primary" class="gotoDeal" @click="gotoDeal">去结算</el-button>
                </el-col>
            </el-row>
        </div>
        <div class="salesBest">
            <Title title="热销商品" englishTitle="" iconClass="icon--redu"/>
            <ul class="clearfix">
                <li v-for="(item, index) in indexProduct" :key="index" class="productDescription">
               <router-link :to="'/seriesInfo/'+item.pno">
                    <img :src="item.cover_url" alt="">
                </router-link>
                <router-link :to="'/seriesInfo/'+item.pno">
                    <p class="desc">{{item.product_name}}</p>
                </router-link>
                <p>参&nbsp;&nbsp;考&nbsp;&nbsp;价 : <span class="price"> &yen; {{item.price}}</span></p>
                <p>发&nbsp;&nbsp;货&nbsp;&nbsp;日 : <span> {{item.lead_time}}</span></p> 
                <p>数量折扣 : <span> {{item.has_quantity_discount === 1?'有': '无'}}</span></p> 
          </li>
            </ul>
        </div>
  </div>
</template>
<script>
import CountChange from "../components/countChange/main/CountChange";
import { mapState, mapActions } from "vuex";
import Vue from "vue";
import store from "../store/store";
import Title from "../components/title/title.vue";
import ProductDescription from "../components/productDescription/main/ProductDescription.vue";
import swal from "sweetalert"; //弹出消息提示框
import "../assets/sweetAlert/sweetAlert.css";
var singleArr = [];
export default {
  components: {
    CountChange,
    Title,
    ProductDescription
  },
  created() {},
  mounted() {
    this.updateshopcarListAsync({
      success: res => {
        // 初始化成功执行watch中的函数
        // console.log("res",res);
        // this.singleCount = [];
        // this.singlePrices = [];
        // this.singleAllPrices = [];
        // res.data.data.forEach((item,index)=>{
        //     this.singleCount.push(item.quantity);
        //     this.singlePrices.push(this.initPrices(item.quantity,item.prices));
        //     var singlePrice = parseInt(this.singlePrices[index]);
        //     var singleAllPrice = this.singleCount[index]*singlePrice;
        //     this.singleAllPrices.push(singleAllPrice.toFixed(2));
        // })
      },
      fail: res => {},
      error: error => {
        // console.log(error)
      }
    });
    var loader = this.$loading({ fullscreen: true });
    this.updateIndexProductAsync({
      success: res => {
        // console.log(res)
        loader.close();
      },
      fail: res => {
        // console.log(res)
        loader.close();
      },
      error: error => {
        // console.log(error)
        loader.close();
      }
    });
  },
  data() {
    return {
      checked: false,
      singleChecked: [],
      singlePrices: [],
      singleAllPrices: [],
      singleCount: [],
      totalCount: 0,
      totalPrices: 0.0,
      productDetailShow: true,
      hasLoading: true //为true显示总数量和总钱数
    };
  },
  methods: {
    ...mapActions({
      updateIndexProductAsync: "indexPage/updateIndexProductAsync",
      updateshopcarListAsync: "shopcar/updateshopcarListAsync",
      saveGoods: "shopcar/saveGoods"
    }),
    checkedAll() {
      //全选
      this.checked = !this.checked;
      this.singleChecked = [];
      this.initShopcarData.forEach(() => {
        this.singleChecked.push(this.checked);
      });
    },
    initPrices(value, prices) {
      var firstPrice = 0;
      for (var i = 0; i < prices.length; i++) {
        if (
          value >= prices[i].min_quantity &&
          value <= prices[i].max_quantity
        ) {
          firstPrice = prices[i].price;
        } else if (value < prices[0].min_quantity) {
          firstPrice = prices[0].price;
        } else if (value > prices[prices.length - 1].max_quantity) {
          firstPrice = prices[prices.length - 1].price;
        }
      }
      return firstPrice;
    },
    changePrice(param) {
      Vue.set(this.singleCount, param.index, param.shopcarQuantity); //修改数量
      Vue.set(this.singlePrices, param.index, param.price); //修改单价
      Vue.set(this.singleAllPrices, param.index, param.singleAllPrices); //修改单个商品总价
      let url = this.$urlConfig.config.seriesUrl + "/carts/" + param.cartId;
      var loader = this.$loading({ fullscreen: true });
      this.$http({
        //加入购物车数量修改
        url: url,
        method: "PUT",
        data: {
          quantity: param.shopcarQuantity
        }
      }).then(res => {
        loader.close();
      });
    },
    deleteGoods: function(e) {
      //删除商品
      var loader = this.$loading({ fullscreen: true });
      var id = e.currentTarget.id;
      var index = e.currentTarget.dataset.index;
      var arrId = [];
      arrId.push(id);
      this.hasLoading = false;
      let url = this.$urlConfig.config.seriesUrl + "/carts";
      swal({
        title: "确认删除该商品？",
        icon: "info",
        buttons: ["取消", "确认"]
      }).then(willDelete => {
        if (willDelete) {
          this.$http({
            url: url,
            method: "DELETE",
            data: {
              ids: arrId
            }
          }).then(res => {
            this.updateshopcarListAsync({
              success: res => {
                // console.log("updateshopcar", res);
                this.singleChecked.splice(index, 1);
                singleArr = this.singleChecked; //数据删除成功后记录选中状态，数据更新后使用
                this.hasLoading = true;
                // console.log(singleArr, "singleArr");
              },
              fail: res => {},
              error: error => {}
            });
            loader.close();
          });
        } else {
          this.hasLoading = true;
          loader.close();
        }
      });
    },
    save: function(e) {
      //收藏商品
      var goodsGno = e.currentTarget.id;
      this.saveGoods({
        params: goodsGno,
        success: res => {
          var msg = res.data.msg;
          var tipInfo;
          if (msg.indexOf(":") != -1) {
            tipInfo = msg.split(":")[1];
          } else {
            tipInfo = "加入收藏成功";
          }
          swal({
            title: "",
            text: tipInfo,
            icon: "info",
            button: "关闭"
          });
        },
        error: error => {
          // console.log(error);
        }
      });
    },
    gotoDeal: function(e) {
      var count = 0;
      var arr = [];
      this.singleChecked.forEach((item, index) => {
        if (item) {
          count++;
          arr.push(this.initShopcarData[index].id);
        }
      });
      if (count <= 0) {
        const h = this.$createElement;
        this.$message({
          message: h("p", null, [
            h("span", null, "请选择相关商品"),
            h("i", { style: "color: teal" })
          ]),
          center: true
        });
      } else {
        this.$router.push({ path: "/order", query: { ids: arr } });
      }
    }
  },
  computed: {
    ...mapState({
      indexProduct: state => state.indexPage.indexProduct,
      initShopcarData: state => state.shopcar.shopcarList
    })
  },
  watch: {
    initShopcarData: function(newvalue, oldvalue) {
      console.log("---", newvalue, "---");
      this.singleCount = []; //数据每次有变化时更新数量，单价和总价
      this.singlePrices = [];
      this.singleAllPrices = [];
      newvalue.forEach((item, index) => {
        this.singleCount.push(item.quantity);
        this.singlePrices.push(
          this.initPrices(item.quantity, item.goods.prices)
        );
        var singlePrice = parseInt(this.singlePrices[index]);
        var singleAllPrice = this.singleCount[index] * singlePrice;
        this.singleAllPrices.push(singleAllPrice.toFixed(2));
      });
      if (newvalue.length < oldvalue.length && singleArr.length > 0) {
        //执行删除操作
        this.singleChecked = singleArr;
      } else {
        singleArr = [];
        this.singleChecked = [];
        newvalue.forEach(() => {
          //初始化页面执行
          this.singleChecked.push(false);
        });
      }
    },
    singleChecked: function(newvalue, oldvalue) {
      this.totalCount = 0;
      this.totalPrices = 0;
      var count = 0;
      for (var i = 0; i < newvalue.length; i++) {
        if (newvalue[i]) {
          count++;
          this.totalCount += this.singleCount[i];
          this.totalPrices += parseFloat(this.singleAllPrices[i]);
        }
      }
      this.totalPrices = this.totalPrices.toFixed(2);
      if (newvalue.length === count && newvalue.length > 0) {
        this.checked = true;
      } else {
        this.checked = false;
      }
    },
    singleCount: function(newvalue, oldvalue) {
      this.totalCount = 0;
      this.totalPrices = 0;
      for (var i = 0; i < newvalue.length; i++) {
        if (this.singleChecked[i]) {
          this.totalCount += newvalue[i];
          this.totalPrices += parseFloat(this.singleAllPrices[i]);
        }
      }
      this.totalPrices = this.totalPrices.toFixed(2);
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      singleArr = [];
    });
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.warmTip {
  color: #666;
  font-size: 12 * @fontSize;
  border: 1px solid #ccc;
  border-bottom: 0;
  padding: 10 * @fontSize;
  margin: 30 * @fontSize 0;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #ccc;
    z-index: 1;
  }
  .warmTipLeft {
    float: left;
    margin-top: 12 * @fontSize;
  }
  .warmTipRight {
    overflow: hidden;
    p:first-child {
      margin-bottom: 10px;
    }
    p:nth-child(2) {
      margin-bottom: 0;
    }
  }
}
.noGoodsTip {
  min-height: 100 * @fontSize;
  height: 100 * @fontSize;
  line-height: 100 * @fontSize;
  text-align: center;
  font-size: 18 * @fontSize;
  a {
    button {
      border-color: @baseColor;
      background-color: @baseColor;
      margin-left: 10 * @fontSize;
      font-size: 16 * @fontSize;
      letter-spacing: 2px;
    }
  }
}
.shopcarList {
  border-bottom: 1px solid #ccc;
  transition: all 0.3s;
  height: 180px;
  text-align: center;
  &:hover {
    background-color: rgba(2, 117, 216, 0.05);
  }
  > div {
    position: relative;
    height: 100%;
    font-size: 14 * @fontSize;
    .countChange {
      position: absolute;
      top: 50%;
    }
  }
  .shopcarInput {
    height: 180px;
    line-height: 180px;
    text-align: left;
  }
  .productDetailLeft {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    img {
      width: 100%;
      height: 100%;
    }
  }
  .productDetailRight {
    text-align: left;
    position: absolute;
    top: 50%;
    right: 0;
    width: 65%;
    transform: translateY(-50%);
    font-size: 14 * @fontSize;
    h5 {
      font-weight: 600;
      padding-bottom: 0;
      cursor: pointer;
      margin-bottom: 15px;
      margin-top: 0;
      font-size: 14 * @fontSize;
      color: #333;
    }
  }
  .line-height_180 {
    height: 180 * @fontSize;
    line-height: 180 * @fontSize;
    strong {
      font-weight: 700;
      color: @baseColor;
    }
  }
}
.shopcartitle {
  text-align: center;
  background-color: #f1f1f1;
  height: 40px;
  line-height: 40px;
  margin-top: 30px;
  font-size: 14 * @fontSize;
  .shopcarInput {
    text-align: left;
    input[type="checkbox"] {
      vertical-align: -1px;
    }
  }
  strong {
    color: @baseColor;
    font-weight: 600;
    margin: 0 8 * @fontSize;
  }
  .totalCount {
    text-align: right;
  }
  .gotoDeal {
    width: 100%;
    background-color: @baseColor;
    border-color: @baseColor;
    border-radius: 0;
  }
}
.salesBest {
  margin-top: 30 * @fontSize;
}
.productDescription {
  width: 30%;
  margin-top: 40px;
  padding: 15px;
  box-sizing: border-box;
  box-shadow: 0px 0px 1px 2px rgba(0, 0, 0, 0.02);
  transition: all 0.5s;
  font-size: 14 * @fontSize;
  margin-right: 5%;
  float: left;
  &:nth-child(3n) {
    margin-right: 0;
  }
  &:hover {
    box-shadow: 0px 0px 1px 2px rgba(0, 0, 0, 0.1);
  }
  img {
    width: 180 * @fontSize;
    height: 180 * @fontSize;
    display: block;
    margin: 0 auto;
    cursor: pointer;
  }
  .price {
    color: #0275d8;
    font-weight: 700;
    display: inline-block;
  }
  .desc {
    height: 50px;
    text-align: left;
    color: #333;
    font-size: 14 * @fontSize;
    cursor: pointer;
    font-weight: 600;
    margin: 0;
    margin-top: 30px;
  }
}
</style>



