// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import Vuex from 'vuex';
import ElementUI from 'element-ui';
import "babel-polyfill";
import 'element-ui/lib/theme-chalk/index.css';
import router from './router';
import store from "./store/store";
import config from "./util/urlConfig";
import axios from 'axios';
import Cookies from "js-cookie";
import Services from "./util/service";
import VueAwesomeSwiper from 'vue-awesome-swiper';
import swal from 'sweetalert';//弹出消息提示框
import "./assets/sweetAlert/sweetAlert.css"

Vue.use(Vuex);
Vue.use(ElementUI);
Vue.use(VueAwesomeSwiper);
Vue.prototype.$http = axios; //把axios挂载到原型上，不必每个页面引入
Vue.prototype.$urlConfig = config; //把所有域名挂载Vue原型上
Vue.prototype.$Cookies = Cookies; //把Cookies挂载到原型上

import "./assets/style.less";
import 'swiper/dist/css/swiper.css'


Vue.config.productionTip = false;
// 每次切换组件页面回到顶部
router.afterEach(function (to, from, next) {
  window.scrollTo(0, 0);
})
// 接入客服，需每次执行函数
const serviceUserInfo = JSON.parse(Cookies.get("userInfo")||'{}');
Services.service.initService({
    username:serviceUserInfo.username,
    mobile:serviceUserInfo.mobile,
})
//页面每次接受请求时检测是否已登录
const AUTH_TOKEN = Cookies.get("accessData");
axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.interceptors.response.use(function (response) {//根据请求返回信息判断是否登陆
    return response;
  }, function (error) {
    if(!error.response) {
        swal({title: "",
            text: "请求超时，请重试",
            icon: "info",
            button: "关闭",});
    } else {
        let url = config.config.loginUrl + "/server/verify";
        var errorCode = parseInt(error.response.data.rescode);
        var errorUrl;
        if(error.config.url.indexOf("?") > -1) {//当有参数时，获取去掉time参数的url
            errorUrl = error.config.url.split("?")[0];
        }
        if(errorUrl === url) {
            Vue.$Cookies.remove("userInfo");
            return;
        } else if (errorCode >= 20000 && errorCode <= 30000) {
            store.dispatch("loginRegister/login");
        } else if (errorCode > 30000) {
            alert(error.response.data.msg);
            store.dispatch("loginRegister/login",1);
        }
    }
  });
  axios.interceptors.request.use(function (config) {//请求前如果是get请求，则加时间戳
    if( config.method === "get" ) {
        if(config.url.indexOf("?") !== -1) {//有参数
            config.url = config.url + "&time="+new Date().getTime();
        } else { // 无参数
            config.url = config.url + "?"+"time="+new Date().getTime();
        }
    }
    const timeout = 10000;
    return {...config,timeout:timeout};
  }, function (error) {
    return Promise.reject(error);
  });
/* eslint-disable no-new */
const app = new Vue({
  el: '#app',
  router,
  store,
})



