const indexPage = {
    namespaced: true,
    state:{
        indexProduct: [],//首页数据
    },
    getters:{

    },
    mutations:{
        updateIndexProduct(state,data){//更新首页数据
            state.indexProduct = data;
        },
    },
    actions:{
        updateIndexProductAsync(store,param) {
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + '/main/products';
            that.$http({
                method:'get',
                url:url
            }).then((res)=>{
                var rescode = parseInt(res.data.rescode);
                if(rescode <= 10000) {
                    store.commit('updateIndexProduct',res.data.data);
                    param.success(res)
                } else {
                    param.fail(res)
                }
            }).catch((error) => {
                param.error(error);
            }) 
        },
    }
}
export default indexPage;


