import Vue from 'vue';
import Vuex from 'vuex';

import loginRegister from './module/loginRegister';
import order from './module/order';
import shopcar from './module/shopcar';
import searchList from './module/searchList.js';
import payment from './module/payment';
import indexPage from "./module/indexPage";
import productInfo from "./module/productInfo";
import productList from "./module/productList";
import stepList from './module/stepList.js';
import seriesInfo from "./module/seriesInfo";
import seriesList from "./module/seriesList";
import solutionList from "./module/solutionList";
import modalNumberInfo from "./module/modalNumberInfo";
import solutionInfo from "./module/solutionInfo";
Vue.use(Vuex);

export default new Vuex.Store({
    strict: true, // 如果在其他地方修改state， 则会报错
    state: {
        categoryDisplay: true,
        cateListData:[]
    },
    mutations: {
        updateCategoryDisplay(state, bool) {//头部商品分类数据显示位置，为true显示在头部
            state.categoryDisplay = bool;
        },
        updateCateListData(state,data) {
            state.cateListData = data;
        },
        updatePrice(state, data) {//初始化的时候改变first_price的值
            if (data.length > 0) {
                for (var i = 0; i < data.length; i++) {
                    var prices = data[i].prices;
                    for (var j = 0; j < prices.length; j++) {
                        if (data[i].min_buy >= prices[j].min_quantity && data[i].min_buy <= prices[j].max_quantity) {
                            data[i].first_price = prices[j].price;
                        } else if (data[i].min_buy < prices[0].min_quantity) {
                            data[i].first_price = prices[0].price;
                        } else if (data[i].min_buy > prices[prices.length - 1].max_quantity) {
                            data[i].first_price = prices[prices.length - 1].price;
                        }
                    }
                }
            }
        },
    },
    getters: {

    },
    actions: {
        updateCateListDataAsync(store,param) {
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl+'/categories/all';
            that.$http.get(url).then((res) => {
                var resCode = parseInt(res.data.rescode);
                if(resCode <= 10000) {
                    store.commit('updateCateListData',res.data.data);
                    param.success(res);
                } else {
                    param.fail(res);
                }
            }).catch((error) => {
                param.error(error);
            })
        }
    },
    modules: {
        loginRegister,
        order,
        shopcar,
        searchList,
        payment,
        indexPage,
        productInfo,
        productList,
        seriesInfo,
        seriesList,
        stepList,
        seriesInfo,
        solutionList,
        modalNumberInfo,
        solutionInfo
    },
});



