<template>
    <div>
        <div class="torbar">
            <Torbar />
        </div>
        <div class="header">
            <div class="container">
                <el-row>
                    <el-col :span="5"><Logo /></el-col>
                    <el-col :span='2' :offset="15">
                        <button>下载PDF</button>   
                    </el-col>
                    <el-col :span="2">
                        <button class="">加入购物车</button>
                    </el-col>  
                </el-row>
            </div>          
        </div>
        <div class="card container">

        </div>
        <div class="footer">
            <Footer />
        </div>
    </div>  
</template>
<script>
import Torbar from "../components/topbar/main/Topbar.vue";
import Logo from "../components/header/main/Logo.vue";
import Footer from "../components/footer/main/FooterBottom.vue";
export default {
  components: {
    Torbar,
    Logo,
    Footer
  }
};
</script>
<style lang="less" scoped>
.torbar {
  border-bottom: 1px solid #ddd;
  height: 30px;
  line-height: 30px;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 99;
  background: #fff;
}
.header {
  height: 4.375rem;
  line-height: 4.375rem;
  background-color: #fff;
  -webkit-box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.02);
  box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.02);
  position: fixed;
  top: 31px;
  width: 100%;
  z-index: 99;
}
.card {
  margin-top: 160px;
  padding-top: 15px;
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14),
    0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
  margin-top: 160px;
  padding-top: 15px;
  margin-bottom: 100px;
  min-height: 410px;
}
.footer {
  background-color: #f5f5f5;
  margin-top: 1.875rem;
}
</style>

    


