const modalNumberInfo = {
    namespaced: true,
    state:{
        modalNumberInfo:[]
    },
    getters:{

    },
    mutations:{
        updateModalNumberInfo(state, data) {
            state.modalNumberInfo = data;
        }
    },
    actions:{
        updateModalNumberInfoAsync(store, param) {
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + "/models/" + param.params;
            that.$http({
                method:"get",
                url: url
            }).then((res) => {
                var resCode = parseInt(res.data.rescode);
                if(resCode <= 10000) {
                    store.commit("updateModalNumberInfo", res.data.data);
                    param.success(res);
                } else {
                    param.fail(res)
                }
            }).catch((error) => {
                param.error(error)
            })
        }
    }
}

export default modalNumberInfo;


