<template>
  <div class="modalNumInfo">
      <div class="container">
          <div class="breadCrumbTitle">
              <BreadCrumb :breadCrumbData = "seriesInfoData"/>
          </div>
          <div class="clearfix">
              <div class="zoom">
                  <Zoom :zoomData = "seriesInfoData"/>
              </div>
              <div class="modalNumInfoRight">
                  <div class="productDetail" v-if="seriesInfoData.length >0 && productInfo.length > 0">
                        <div>
                            <h4 :title="seriesInfoData[0].product_name">{{seriesInfoData[0].product_name}}</h4>
                            <div class="price">
                                <span>价格：<strong>&yen; {{prices[0]}}</strong></span>
                            </div>
                            <div class="diffNumPrice">
                                <ul class="differentNum">
                                    <li v-for="(itemprice, index) in productInfo[0].prices" :key="index" v-if="index < 3"> 
                                        <span class="num">{{itemprice.min_quantity}} {{productInfo[0].sales_unit}} - {{itemprice.max_quantity}} {{productInfo[0].sales_unit}}</span>
                                        <span class="numPrice">&yen; {{itemprice.price}}</span>
                                        <span class="rect"></span>
                                    </li>
                                </ul> 
                            </div>
                            <ul class="detailAndBuy">
                                <li>品&nbsp;&nbsp;&nbsp;&nbsp;牌 : <span>{{seriesInfoData[0].brand.brand_name}}</span></li>
                                <li>产&nbsp;&nbsp;&nbsp;&nbsp;地 : <span>{{seriesInfoData[0].brand.registration_place}}</span></li>
                                <li>起订量 : <span>{{productInfo[0].min_buy + productInfo[0].sales_unit}}</span></li>
                                <li>库&nbsp;&nbsp;&nbsp;&nbsp;存 : <span>{{productInfo[0].stock + productInfo[0].sales_unit}}</span></li>
                                <li>发货日 : <span>{{leadTime}}天</span></li>
                            </ul>
                        </div>
                        <div>
                            <div class="numChange">
                                <CountChange 
                                :min="productInfo[0].min_buy" :max="productInfo[0].stock" 
                                :quantity="productInfo[0].min_buy" :cartId = 12 
                                :prices="productInfo[0].prices" @changeCount="changePrice" 
                                :index = 0 />
                            </div>
                            <div class="addShopCar">
                                <button class="addToShoppingCar" @click="addShopcar" :data-gno="productInfo[0].gno">
                                    <span class="iconfont icon-caigou-xianxing"></span>加入购物车
                                </button>
                                <button class="saveGoods" @click="save" :id="productInfo[0].gno"><span class="iconfont icon-mn_shoucang"></span>收藏商品</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
          <div class="modalNumInfoSummary" v-if="seriesInfoData.length > 0">
              <el-tabs tab-position="right" style="min-height: 200px;" v-model="activeName">
                <el-tab-pane label="产品概述" name="first"><p>{{seriesInfoData[0].summary}}</p></el-tab-pane>
                <el-tab-pane label="型号参数" name="second">
                    <el-table
                        :data="tableData"
                        border
                        style="width: 100%">
                        <el-table-column
                        align = "center"
                        label="商品型号"
                          min-width="180">
                          <template slot-scope="scope">
                                <span>
                                    {{tableData[scope.$index].spec_unit?tableData[scope.$index].spec_name + " ( " + tableData[scope.$index].spec_unit + " ) ":tableData[scope.$index].spec_name}}
                                </span>
                         </template>
                        </el-table-column>
                        <el-table-column
                        align = "center"
                        label="型号参数"
                          min-width="180">
                          <template slot-scope="scope">
                                <span>
                                    {{tableData[scope.$index].spec_value?tableData[scope.$index].spec_value:'--'}}
                                </span>
                         </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
                </el-tabs>
          </div>
          <div class="goSeriesInfo">
                <router-link :to="'/seriesInfo/'+modalNumberInfo.pno">
                    <el-button type="primary"><span class="iconfont icon-jiantou1-copy-copy-copy"></span>返回产品详情</el-button>
                </router-link>
            </div>
      </div>
  </div>
</template>
<script>
import BreadCrumb from "../components/breadCrumb/main/BreadCrumb";
import Zoom from "../components/zoom/main/Zoom";
import CountChange from "../components/countChange/main/CountChange.vue";
import { mapState, mapActions } from "vuex";
import swal from "sweetalert"; //弹出消息提示框
import "../assets/sweetAlert/sweetAlert.css";
export default {
  components: {
    BreadCrumb,
    Zoom,
    CountChange
  },
  mounted() {
    var loader = this.$loading();
    this.updateModalNumberInfoAsync({
      params: this.$route.params.id,
      success: res => {
        this.updateSeriesInfoDataAsync({
          param: res.data.data.pno,
          success: res => {
            loader.close();
          },
          fail: res => {
            loader.close();
          },
          error: error => {
            loader.close();
          }
        });
        this.updateProductInfoAsync({
          param: res.data.data.gno,
          success: res => {},
          fail: res => {},
          error: error => {}
        });
      },
      fail: res => {},
      error: error => {}
    });
  },
  data() {
    return {
      prices: [],
      num: [],
      activeName: "first",
      tableData: [],
      priceRange: [],
      leadTime: 1
    };
  },
  methods: {
    ...mapActions({
      updateModalNumberInfoAsync: "modalNumberInfo/updateModalNumberInfoAsync",
      updateSeriesInfoDataAsync: "seriesInfo/updateSeriesInfoDataAsync",
      updateProductInfoAsync: "seriesInfo/updateProductInfoAsync",
      addToShopcar: "shopcar/addToShopcar",
      saveGoods: "shopcar/saveGoods"
    }),
    changePrice(param) {
      this.leadTime = this.changeLeadTime(param.shopcarQuantity);
      this.$set(this.prices, param.index, param.price);
      this.$set(this.num, param.index, param.shopcarQuantity);
    },
    changeLeadTime(val) {
      var leadTime;
      this.priceRange.forEach(element => {
        if (val >= element.min_quantity && val <= element.max_quantity) {
          leadTime = element.lead_time;
        } else if (val < this.priceRange[0].min_quantity) {
          leadTime = this.priceRange[0].lead_time;
        } else if (
          val > this.priceRange[this.priceRange.length - 1].max_quantity
        ) {
          leadTime = this.priceRange[this.priceRange.length - 1].lead_time;
        }
      });
      return leadTime;
    },
    addShopcar: function(e) {
      var gno = e.currentTarget.dataset.gno;
      var quantity = this.num[0];
      this.addToShopcar({
        params: {
          gno: gno,
          quantity: quantity
        },
        success: function(res) {
          let resCode = parseInt(res.data.rescode);
          if (resCode <= 10000) {
            swal({
              title: "加入购物车成功",
              icon: "success",
              buttons: ["继续购物", "去购物车结算"]
            }).then(willDelete => {
              if (willDelete) {
                this.$router.push("/shopcar");
              } else {
              }
            });
          } else {
            var msg = res.data.msg;
            var tipInfo;
            if (msg.indexOf(":") != -1) {
              tipInfo = msg.split(":")[1];
            } else {
              tipInfo = "加入购物车失败,请重试";
            }
            swal({
              title: "",
              text: tipInfo,
              icon: "info",
              button: "关闭"
            });
          }
        }.bind(this),
        error: function(res) {
          //    swal({title: "",
          //         text: "请求失败",
          //         icon: "info",
          //         button: "关闭",});
        }
      });
    },
    save: function(e) {
      //收藏商品
      var goodsGno = e.currentTarget.id;
      this.saveGoods({
        params: goodsGno,
        success: res => {
          var msg = res.data.msg;
          var tipInfo;
          if (msg.indexOf(":") != -1) {
            tipInfo = msg.split(":")[1];
          } else {
            tipInfo = "加入收藏成功";
          }
          swal({
            title: "",
            text: tipInfo,
            icon: "info",
            button: "关闭"
          });
        },
        error: error => {
        //   console.log(error);
        }
      });
    }
  },
  computed: {
    ...mapState({
      modalNumberInfo: state => state.modalNumberInfo.modalNumberInfo,
      seriesInfoData: state => state.seriesInfo.seriesInfoData,
      productInfo: state => state.seriesInfo.productInfo
    })
  },
  watch: {
    productInfo: function(newvalue, oldvalue) {
      this.prices.push(newvalue[0].first_price);
      this.num.push(newvalue[0].min_buy);
      this.priceRange = newvalue[0].prices;
      this.leadTime = this.changeLeadTime(newvalue[0].min_buy);
    },
    modalNumberInfo(newvalue, oldvalue) {
      this.tableData = [];
      if (newvalue.specs && newvalue.specs.length > 0) {
        this.tableData = newvalue.specs;
      }
    },
    $route(newvalue, oldvalue) {
      var loader = this.$loading();
      this.updateModalNumberInfoAsync({
        params: this.$route.params.id,
        success: res => {
          this.updateSeriesInfoDataAsync({
            param: res.data.data.pno,
            success: res => {
              loader.close();
            },
            fail: res => {
              loader.close();
            },
            error: error => {
              loader.close();
            }
          });
          this.updateProductInfoAsync({
            param: res.data.data.gno,
            success: res => {},
            fail: res => {},
            error: error => {}
          });
        },
        fail: res => {},
        error: error => {}
      });
    }
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.modalNumInfo {
  margin: 30 * @fontSize;
  .breadCrumbTitle {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background-color: #0275d8;
      z-index: 1;
    }
  }
  .zoom {
    float: left;
    width: 35%;
    margin-right: 5%;
  }
  .modalNumInfoRight {
    float: right;
    width: 60%;
    position: relative;
    padding-bottom: 10px;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: #fff;
      z-index: 1;
    }
  }
  .modalNumInfoSummary {
    margin: 30 * @fontSize 0;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
    padding: 30px;
    p {
      text-indent: 2em;
    }
  }
  .goSeriesInfo {
    margin-bottom: 30px;
    .iconfont {
      margin-right: 5px;
    }
  }
}
.productDetail {
  margin-top: 30 * @fontSize;
  h4 {
    color: #333;
    font-weight: 700;
    font-size: 16 * @fontSize;
  }
  h3 {
    margin: 0;
    font-size: 16px;
  }
  p {
    text-indent: 2em;
  }
  .price {
    height: 40 * @fontSize;
    line-height: 40 * @fontSize;
    padding-left: 10 * @fontSize;
    background: url("../assets/images/bgPic.png") no-repeat;
    background-size: cover;
    margin-bottom: 20 * @fontSize;
    color: #666;
    font-size: 14 * @fontSize;
    strong {
      color: #0275d8;
      font-weight: 700;
      font-family: Arial;
      font-size: 16 * @fontSize;
      margin-left: 15 * @fontSize;
    }
  }
  .diffNumPrice {
    position: relative;
    margin-bottom: 20px;
    .differentNum {
      li {
        position: relative;
        height: 26px;
        line-height: 26px;
        display: inline-block;
        position: relative;
        margin-right: 2 * @fontSize;
        font-size: 14 * @fontSize;
        &:last-child {
          margin-right: 0;
        }
        .rect {
          position: absolute;
          left: 50%;
          top: 0;
          border-width: 27px 25px 0px 0px;
          border-style: solid;
          border-color: #0275d8 #fff #0275d8 #0275d8;
        }
        .num {
          background-color: #0275d8;
          color: #fff;
          float: left;
          width: 128px;
          height: 28px;
          line-height: 28px;
          text-align: center;
          overflow: hidden;
        }
        .numPrice {
          color: #0275d8;
          width: 100px;
          float: left;
          text-align: center;
          border: 1px solid #0275d8;
          border-left: none;
          overflow: hidden;
        }
      }
    }
  }
  .detailAndBuy {
    margin-bottom: 10 * @fontSize;
    padding-bottom: 20 * @fontSize;
    li {
      padding: 6px 0;
      font-size: 14 * @fontSize;
      span {
        margin-left: 5px;
      }
      a {
        margin-left: 10px;
        color: @baseColor;
      }
    }
  }
  .numChange {
    width: 150 * @fontSize;
    margin-top: 25px;
  }
}
.addShopCar button {
  margin-top: 6px;
  width: 110 * @fontSize;
  height: 40 * @fontSize;
  font-size: 14 * @fontSize;
  transition: all 0.3s;
  border: none;
  background-color: #0275d8;
  border: 1px solid #0275d8;
  color: #fff;
  margin-right: 30 * @fontSize;
  cursor: pointer;
  .iconfont {
    margin-right: 5px;
    font-size: 15 * @fontSize;
  }
}
.addShopCar .saveGoods {
  background-color: rgba(2, 117, 216, 0.03);
  border: 1px solid #0275d8;
  color: #0275d8;
}
.swal-button {
  background-color: @baseColor!important;
}
</style>



