<template>
    <div>
        <div class="listInfo">
            <el-row class="listInfoTitle">
                <el-col :span="7">商品信息</el-col>
                <el-col :span="2">品牌</el-col>
                <el-col :span="2">型号</el-col>
                <el-col :span="3">商品参数</el-col>
                <el-col :span="3">商品参数</el-col>
                <el-col :span="2">价格</el-col>
                <el-col :span="3">购买数量</el-col>
                <el-col :span="2">加入购物车</el-col>
            </el-row>
            <el-row class="listContent" v-for="(item, index) in productList" :key="index">
                <el-col :span="7" class="productContent">
                    <router-link :to="'/productInfo/'+item.gno">
                        <img :src="item.cover_url" alt="">
                        <h4>{{item.goods_name}}</h4>
                    </router-link>
                </el-col>
                <el-col :span="2">
                    <span class="positionAbsolute">{{item.brand_name}}</span>
                </el-col>
                <el-col :span="2">
                    <span class="positionAbsolute">{{item.partnumber}}</span>
                </el-col>
                <el-col :span="3" v-if="item.other_attrs.length > 0">
                    <span class="positionAbsolute">{{item.other_attrs[0].attr_name}}:{{item.other_attrs[0].attr_value}}</span>
                </el-col>
                <el-col :span="3" v-else-if="item.other_attrs.length <= 0">
                    <span class="positionAbsolute">无</span>
                </el-col>
                <el-col :span="3" v-if="item.other_attrs.length > 1">
                    <span class="positionAbsolute">{{item.other_attrs[1].attr_name}}:{{item.other_attrs[1].attr_value}}</span>
                </el-col>
                <el-col :span="3" v-else-if="item.other_attrs.length <= 1">
                    <span class="positionAbsolute">无</span>
                </el-col>
                <el-col :span="2">
                    <span class="positionAbsolute"> <strong>&yen; {{prices[index]}}</strong></span>
                </el-col>
                <el-col :span="3">
                    <CountChange class="countChange"
                        :min="item.min_buy" :max="item.stock" :quantity="count[index]" 
                        :cartId="item.id" :prices="item.prices" @changeCount="changePrice" :index="index"
                        />
                </el-col>
                <el-col :span="2">
                    <span class="positionAbsolute" @click="addShopcar" :data-gno="item.gno" :data-index = "index"><i class="iconfont icon-caigou-xianxing"></i></span>
                </el-col>
            </el-row>
        </div>  
    </div>
    
</template>
<script>
import CountChange from "../../countChange/main/CountChange";
import {mapState, mapActions} from 'vuex'
import Vue from 'vue';
var overAllCount = [];
var overAllPrice = [];
export default {
  components:{
      CountChange,
  },
  data(){
      return {
          param:"",
          prices:[],
          count:[],
      }
  },
  created(){
  },
  mounted(){
      if(this.listForProductList===true){
          this.isSmallCountChange=false;
      }else{
          this.isSmallCountChange=true;
      };
  },
  methods:{
    ...mapActions({
           addToShopcar:'shopcar/addToShopcar'
    }),
    changePrice:function (param) {
        this.param = param;
        Vue.set(this.prices,param.index,param.price);
        Vue.set(this.count,param.index,param.shopcarQuantity);
    },
    addShopcar:function(e){
           var gno = e.currentTarget.dataset.gno;
           var index = e.currentTarget.dataset.index;
           var quantity = this.count[index];
           this.addToShopcar({
               params:{
                   gno:gno,
                   quantity:quantity
               },success:function (res){
                   let resCode = parseInt(res.data.rescode);
                   if(resCode <= 10000) {
                       swal({
                            title: "加入购物车成功",
                            icon: "success",
                            buttons: ["继续购物", "去购物车结算"],
                            // dangerMode: true,
                        })
                        .then((willDelete) => {
                          if (willDelete) {
                            location.href =location.origin+"/#/shopcar";
                          } else {
                                
                          }
                        });
                   } else {
                        var msg = res.data.msg;
                        var tipInfo;
                        if(msg.indexOf(":")!=-1) {
                             tipInfo = msg.split(":")[1]
                        } else {
                           tipInfo = "加入购物车失败,请重试"
                        }
                       swal({title: "",
                            text: tipInfo,
                            icon: "info",
                            button: "关闭",});
                   }
               },error:function (res){
                   swal({title: "",
                        text: "请求失败",
                        icon: "info",
                        button: "关闭",});
                    }
           })
       }
    
  },
  computed:{
      ...mapState({
          productList:state=>state.productList.productList,
        //   listForProductList:state=>state.productList.listForProductList,
      })
  },
  watch:{
      productList:function (newvalue,oldvalue) {
            this.prices = [];
            this.count = [];
            for(var i = 0; i < newvalue.length; i++) {
                this.prices.push(newvalue[i].first_price);
                this.count.push(newvalue[i].min_buy);
            }
      }
  },
  beforeRouteEnter(to,from,next){
      if(from.path == "/productList/bigImgList" && to.path=="/productList") {
          next(vm=>{
              vm.prices = overAllPrice;
              vm.count = overAllCount;
          }); 
      }else {
          next();
      }
      
  },
  beforeRouteLeave(to,from,next){
      if(to.path == "/productList/bigImgList" && from.path=="/productList") {//条件判断是大图模式和列表模式切换，记录count值
        overAllPrice = this.prices;
          overAllCount = this.count;
      }
      next();
  }
}
</script>
<style lang="less" scoped>
    @fontSize: 1/16rem;
    @baseColor:#0275d8;
    .listInfo {
        text-align: center;
        margin-top: 15*@fontSize;
        font-size: 14*@fontSize;
        .listInfoTitle {
            background-color: rgba(2,117,216,0.03);
            height: 40*@fontSize;
            line-height: 40*@fontSize;
            color: #666;
            >div {
                border: 1px solid rgba(2,117,216,0.5);
                border-left: none;
                &:first-of-type {
                    border-left: 1px solid rgba(2,117,216,0.5);
                }
            }
        }
        .listContent {
            border-bottom: 1px solid #ccc;
            &:hover {
                background-color: rgba(2, 117, 216, 0.03);
            }
            >div {
                position: relative;
                height: 200*@fontSize;
                .countChange {
                    position: absolute;
                    top: 50%;
                }
                .positionAbsolute {
                    font-size: 12*@fontSize;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 100%;
                    word-wrap: break-word;
                    strong {
                        font-weight: normal;
                        color: @baseColor;
                    }
                    .icon-caigou-xianxing {
                        color: #fff;
                        width: 30px;
                        height: 30px;
                        border-radius: 50%;
                        background-color: #e5e5e5;
                        line-height: 30px;
                        text-align: center;
                        font-size: 18px;
                        transition: all 0.5s;
                        cursor: pointer;
                        display: inline-block;
                        &:hover {
                            background-color: @baseColor;
                        }
                    }
                }
            }
            .productContent {
                a {
                    display: block;
                    width: 100%;
                    height: 100%;
                    color: #333;
                }
                img {
                    width: 100*@fontSize;
                    height: 100*@fontSize;
                    position: absolute;
                    top: 50%;
                    left: 0;
                    transform: translateY(-50%);
                }
                h4 {
                    position: absolute;
                    top: 50%;
                    right: 10*@fontSize;
                    transform: translateY(-50%);
                    width: 65%;
                    margin: 0;
                    text-align: left;
                    font-weight: 700;
                    font-size: 14*@fontSize;
                }
            }
            
        }
    }
</style>



