<template>
    <div class="group-search">
        <div class="group-inner">
            <div class="mod-search clearfix">
                <div class="search-input">
                    <input type="text" class="main-search-keywords" maxlength="30" autocomplete="off" autofocus="true" placeholder="请输入产品名称及产品编号" spellcheck="false" :value='$route.query.keywords' @keyup.enter='searchProduct' @keyup='getValue'>
                    <button id="wagsearch-submit" @click='searchProduct'><i class="el-icon-search"></i></button>
                </div>
            </div> 
        </div>
    </div>
</template>
<script>
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  name: "SearchInput",
  data() {
    return {
      inputValue: "",
      limit: 5
    };
  },
  mounted() {},
  methods: {
    ...mapActions({
      updateSearchListAsync: "searchList/updateSearchListAsync"
    }),
    getValue(e) {
      this.inputValue = e.target.value;
    },
    searchProduct() {
      if (
        this.inputValue.trim() !== "" &&
        this.inputValue !== this.$route.params.keywords
      ) {
        this.updateSearchListAsync({
          params: {offset:0, limit: this.limt, keywords: this.inputValue},
          success: res => {
            if(res.data.data.tag === 1) {// 跳转到搜索列表页
                this.$router.replace({
                    path: "/searchList",
                    query: { page: 1, keywords: this.inputValue }
                });
            } else if(res.data.data.tag === 2 ) {// 跳转到产品详情页
                this.$router.replace({
                    path: "/seriesInfo/"+res.data.data.pno,
                    query:{keywords:res.data.data.pno}
                });
            } else if (res.data.data.tag === 3) { // 跳转到型号详情页
                this.$router.replace({
                    path: "/modalNumberInfo/"+res.data.data.partnumber,
                    query:{keywords:res.data.data.partnumber}
                });
            }
  
          },
          fail: res => {
        
          },
          error: error => {
            // console.log(error);
          
          }
        }); // 初始化页面数据
        
      }
    }
  }
};
</script>
<style lang="less" scoped>
@baseColor: #0275d8;
@fontSize: 1/16rem;
.group-search {
  height: 70 * @fontSize;
  line-height: 70 * @fontSize;
  overflow: hidden;
  position: relative;
  .group-inner {
    .mod-search {
      .search-input {
        width: 100%;
        padding: 0;
        margin: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        input {
          float: left;
          width: 80%;
          height: 40 * @fontSize;
          padding: 0;
          margin: 0;
          outline: none;
          border-width: 0;
          border: 1px solid #ccc;
          border-right: 1px solid @baseColor;
          box-sizing: border-box;
          padding-left: 10 * @fontSize;
          font-size: 14 * @fontSize;
        }
        #wagsearch-submit {
          width: 20%;
          float: right;
          font-size: 14 * @fontSize;
          border-radius: 0;
          outline: 0;
          cursor: pointer;
          color: #fff;
          background: #0275d8;
          height: 40 * @fontSize;
          line-height: 40 * @fontSize;
          border: none;
          outline: none;
          .el-icon-search {
            font-size: 20 * @fontSize;
            font-weight: 800;
          }
        }
      }
    }
  }
}
</style>



