<template>
    <div>
        <div class="listInfo">
            <el-row class="listInfoTitle">
                <el-col :span="8">商品信息</el-col>
                <el-col :span="3">品牌</el-col>
                <el-col :span="2">产地</el-col>
                <el-col :span="3">数量折扣</el-col>
                <el-col :span="3">参考价(元)</el-col>
                <el-col :span="2">发货期</el-col>
                <el-col :span="3">质保期</el-col>
            </el-row>
            <el-row class="noTipInfo" v-if="seriesList.length <= 0">
                <el-col>
                    暂无相关商品<router-link to="/"><el-button type="primary">去逛逛</el-button></router-link>
                </el-col>
            </el-row>
            <el-row class="listContent" v-for="(item, index) in seriesList" :key="index">
                <el-col :span="8" class="productContent">
                    <router-link :to="'/seriesInfo/'+item.pno">
                        <img :src="item.cover_url" alt="">
                        <h4>{{item.product_name}}</h4>
                    </router-link>
                </el-col>
                <el-col :span="3">
                    <span class="positionAbsolute">{{item.brand.brand_name}}</span>
                </el-col>
                <el-col :span="2">
                    <span class="positionAbsolute">{{item.brand.registration_place}}</span>
                </el-col>
                <el-col :span="3" >
                    <span class="positionAbsolute">{{item.has_quantity_discount === 1?'有': '无'}}</span>
                </el-col>
                <el-col :span="3" >
                    <span class="positionAbsolute">{{item.price}}</span>
                </el-col>
                <el-col :span="2">
                    <span class="positionAbsolute">{{item.lead_time}}</span>
                </el-col>
                <el-col :span="3">
                    <span class="positionAbsolute">{{item.shelf_life}}</span>
                </el-col>
            </el-row>
        </div>  
    </div>
    
</template>
<script>
import {mapState, mapActions} from 'vuex'
export default {
  components:{
      
  },
  data(){
      return {
          
      }
  },
  created(){
    //   console.log(this)
      
  },
  mounted(){
      
  },
  methods:{
  },
  computed:{
      ...mapState({
          seriesList: state => state.seriesList.seriesList,
      })
  },
  watch:{
      
  },
}
</script>
<style lang="less" scoped>
    @fontSize: 1/16rem;
    @baseColor:#0275d8;
    .listInfo {
        text-align: center;
        margin-top: 15*@fontSize;
        font-size: 14*@fontSize;
        .listInfoTitle {
            background-color: rgba(2,117,216,0.03);
            height: 40*@fontSize;
            line-height: 40*@fontSize;
            color: #666;
            >div {
                border: 1px solid rgba(2,117,216,0.5);
                border-left: none;
                &:first-of-type {
                    border-left: 1px solid rgba(2,117,216,0.5);
                }
            }
        }
        .noTipInfo {
            height: 300px;
            line-height: 300px;
            font-size: 16px;
            a {
                margin-left: 20px;
                font-size: 16px;
            }
        }
        .listContent {
            border-bottom: 1px solid #ccc;
            &:hover {
                background-color: rgba(2, 117, 216, 0.03);
            }
            >div {
                position: relative;
                height: 200*@fontSize;
                .countChange {
                    position: absolute;
                    top: 50%;
                }
                .positionAbsolute {
                    font-size: 12*@fontSize;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    width: 100%;
                    word-wrap: break-word;
                    strong {
                        font-weight: normal;
                        color: @baseColor;
                    }
                }
            }
            .productContent {
                a {
                    display: block;
                    width: 100%;
                    height: 100%;
                    color: #333;
                }
                img {
                    width: 100*@fontSize;
                    height: 100*@fontSize;
                    position: absolute;
                    top: 50%;
                    left: 0;
                    transform: translateY(-50%);
                }
                h4 {
                    position: absolute;
                    top: 50%;
                    right: 10*@fontSize;
                    transform: translateY(-50%);
                    width: 56%;
                    margin: 0;
                    text-align: left;
                    font-weight: 700;
                    font-size: 14*@fontSize;
                }
            }
            
        }
    }
</style>



