<template>
    <div>
        <!-- 新增或者编辑地址的模态框 -->
        <el-dialog title="收货地址" :visible.sync="dialogFormVisible" width="40%">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="收货人" prop="congister">
                    <el-input v-model="ruleForm.congister" placeholder="请输入收货人姓名"></el-input>
                </el-form-item>
                <el-form-item label="公司名称" prop="companyName">
                    <el-input v-model="ruleForm.companyName"  placeholder="请输入公司名称"></el-input>
                </el-form-item>
                <el-form-item label="所在地区" prop="selectedOptions">
                    <el-cascader
                        class="area"
                        :options="options"
                        v-model="ruleForm.selectedOptions"
                    ></el-cascader>
                </el-form-item>
                <el-form-item label="详细地址" prop="specificAddress">
                    <el-input v-model="ruleForm.specificAddress" placeholder="请输入详细地址"></el-input>
                </el-form-item>
                <el-form-item label="手机号码" prop="mobileNumber">
                    <el-input v-model="ruleForm.mobileNumber" placeholder="请输入手机号"></el-input>
                </el-form-item>
                <el-form-item label="固定电话">
                    <el-row>
                        <el-col :span="7">
                            <el-form-item prop="phoneNumber1">
                                <el-input v-model="ruleForm.phoneNumber1" placeholder="区号3-4位数字"></el-input>
                            </el-form-item>
                        </el-col>
                        <el-col :span="2" :offset="1">
                            <span class="line"></span>
                        </el-col>
                        <el-col :span="13" :offset="1">
                            <el-form-item prop="phoneNumber2">
                                <el-input v-model="ruleForm.phoneNumber2" placeholder="座机号7-8位数字"></el-input>
                            </el-form-item>
                        </el-col>
                    </el-row>
                </el-form-item>
                <el-form-item prop="checked">
                    <el-checkbox v-model="ruleForm.checked">设置为默认地址</el-checkbox>
                </el-form-item>
                <el-form-item class="sureAddress">
                    <el-button @click="resetForm('ruleForm')">取 消</el-button>
                    <el-button type="primary" @click="submitForm('ruleForm')" v-if="editIndex.length <= 0">确 定</el-button>
                    <el-button type="primary" @click="editForm('ruleForm')" v-if="editIndex.length > 0">确定</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>
    </div>
</template>
<script>
import options from "../../../util/cascader-address-options";
import { mapState,mapActions } from 'vuex';
export default {
    props:{
        add:{
            type:Boolean,
            required: true
        },
        editIndex:{
            type:String,
            required:false
        }
    },
    created(){
        
    },
    mounted(){
    },
  data(){
        var checkCongister = (rule, value, callback) => {
              if(!value.trim()) {
                  return callback(new Error('收货人姓名不能为空'))
              } else {
                  return callback();
              }
          };
        var checkCompanyName = (rule, value, callback) => {
            if(!value.trim()) {
                return callback(new Error('公司名称不能为空'))
            }else {
                return callback()
            }
        };
        var checkAreas = (rule, value, callback) => {
            if(value.length <= 0) {
                return callback(new Error('请选择地址'))
            }else {
                return callback()
            }
        };
        var checkspecificAddress = (rule, value, callback) => {
            if(!value.trim()) {
                return callback(new Error('详细地址不能为空'))
            } else {
                return callback()
            }
        };
        var checkMobileNumber = (rule, value, callback) => {
            var phoneReg=/^\d{11}$/;
            if(!value.trim()) {
                return callback(new Error('手机号码不能为空'));
            }else if(!phoneReg.test(value.trim())) {
                return callback(new Error('请输入11位数字'));
            } else {
                return callback()
            }
        };
        var checkPhoneNumber1 = (rule, value, callback) => {
            var checkPhoneNumber1=/^\d{3,4}$/;
            if(!value.trim()) {
                callback();
            }else if(!checkPhoneNumber1.test(value.trim())) {
                return callback(new Error('请输入3-4位数字'));
            }else {
                return callback()
            }
        };
        var checkPhoneNumber2 = (rule, value, callback) => {
            var checkPhoneNumber2=/^\d{7,8}$/;
            if(!value.trim()) {//如果为空，直接返回
                callback();
            }else if(!checkPhoneNumber2.test(value.trim())) {
                return callback(new Error('请输入7-8位数字'));
            } else {
                return callback()
            }
        };
        var checkSelected = (rule, value, callback) => {
            return callback();
        };
      return {
        dialogFormVisible: false,
        formLabelWidth: '120px',
        ruleForm: {
            congister: '',
            companyName: '',
            specificAddress: '',
            mobileNumber: '',
            phoneNumber1:'',
            phoneNumber2:'',
            selectedOptions: [],
            checked: false,
        },
        rules:{
            congister: [
                // { required: true, message: '请输入收货人姓名', trigger: 'blur' },
                {required: true,validator: checkCongister, trigger: 'blur'}
            ],
            companyName:[
                {required: true,validator: checkCompanyName, trigger: 'blur'}
            ],
            selectedOptions : [
                {required: true,validator:checkAreas, trigger: 'blur'}
            ],
            specificAddress : [
                {required: true,validator: checkspecificAddress, trigger: 'blur'}
            ],
            mobileNumber : [
                {required: true,validator: checkMobileNumber, trigger: 'blur'}
            ],
            phoneNumber1: [
                {required: false, validator: checkPhoneNumber1, trigger: 'blur'}
            ],
            phoneNumber2:[
                 {required: false,validator: checkPhoneNumber2, trigger: 'blur'}
            ],
            checked:[
                {required:false,validator: checkSelected}
            ]
        },
        options: options,
      }
  },
  methods:{
      ...mapActions({
          updateAddressListAsync:"order/updateAddressListAsync",
          addAddress:"order/addAddress",
          editAddress:"order/editAddress",
      }),
      resetFormData(){
          this.ruleForm.congister = "";
          this.ruleForm.companyName = "";
          this.ruleForm.specificAddress = "";
          this.ruleForm.mobileNumber = "";
          this.ruleForm.phoneNumber1 = "";
          this.ruleForm.phoneNumber2 = "";
          this.ruleForm.selectedOptions = [];
          this.ruleForm.checked = false;
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
                var params = this.reformParams(this.ruleForm);
                this.addAddress({//验证通过，执行添加地址的请求
                    params:params,
                    success:function (res) {
                        var rescode = parseInt(res.data.rescode);
                        if(rescode <= 10000) {
                            this.dialogFormVisible = false;
                            this.updateAddressListAsync();
                        } else {
                            const h = this.$createElement;
                            this.$message({
                                message: h('p', null, [
                                    h('span', null, '添加地址失败，请重试'),
                                    h('i', { style: 'color: teal' },)
                                ]),
                                center: true
                            })
                        }
                    }.bind(this),
                    error:function (res) {
                        const h = this.$createElement;
                        this.$message({
                            message: h('p', null, [
                                h('span', null, '请求服务失败，请重试'),
                                h('i', { style: 'color: teal' },)
                            ]),
                            center: true
                        })
                    }.bind(this)
                })
          } else {
            return false;
          }
        });
      },
      editForm(formName) {
          this.$refs[formName].validate((valid) => {
                if (valid) {
                    var params = this.reformParams(this.ruleForm);
                    this.editAddress({//验证通过，执行修改地址的请求
                        addressId:this.addressList[parseInt(this.editIndex)].id,
                        params:params,
                        success:function (res) {
                              var rescode = parseInt(res.data.rescode);
                              if(rescode <= 10000) {
                                  this.dialogFormVisible = false;
                                  this.updateAddressListAsync();
                              } else {
                                  const h = this.$createElement;
                                  this.$message({
                                      message: h('p', null, [
                                          h('span', null, '修改地址失败，请重试'),
                                          h('i', { style: 'color: teal' },)
                                      ]),
                                      center: true
                                  })
                              }
                          }.bind(this),
                          error:function (res) {
                              const h = this.$createElement;
                              this.$message({
                                  message: h('p', null, [
                                      h('span', null, '请求服务失败，请重试'),
                                      h('i', { style: 'color: teal' },)
                                  ]),
                                  center: true
                              })
                          }.bind(this)
                      })
                } else {
                  return false;
                }
            });
      },
      resetForm(formName) {
        this.dialogFormVisible = false;
      },
      reformParams(data){//在修改或新增地址时对得到的数据进行重组
            var params = {};
            params.consigner = data.congister ;  //收货人名称
            params.mobile = data.mobileNumber; //手机号码
            if((data.phoneNumber1.trim()).length<=0 || (data.phoneNumber2.trim()).length<=0) {
                params.phone = null;
            } else {
                params.phone = data.phoneNumber1 + '-' + data.phoneNumber2;  //电话  （可选）
            }
            params.company_name = data.companyName;  //公司名称
            params.province_id = data.selectedOptions[0]; //省
            params.city_id = data.selectedOptions[1];  //市
            params.district_id = data.selectedOptions[2];  //区  （可选）
            params.address = data.specificAddress;  //详细地址
            params.zipcode= null;  //邮政编码 （可选）
            if (data.checked) {
                params.is_default = 1;
            } else {
                params.is_default = 0;
            };
            return params;
      }
  },
  watch:{
      add:function (newvalue,oldvalue){
          this.dialogFormVisible = newvalue;
      },
      dialogFormVisible:function (newvalue,oldvalue) {
          if(newvalue) {
              this.resetFormData();
          }
          if(!newvalue) {
              this.$emit('changeAdd',newvalue);
              this.$refs["ruleForm"].clearValidate();
          }
      },
      editIndex:function (newvalue,oldvalue) {//根据editIndex判断点击的是添加地址还是编辑地址，如果为'',则是增加地址
            if(newvalue.length > 0) {//点击编辑时执行
                var index = parseInt(newvalue);
                this.ruleForm.congister = this.addressList[index].consigner;
                this.ruleForm.mobileNumber = this.addressList[index].mobile;
                this.ruleForm.companyName = this.addressList[index].company_name;
                this.ruleForm.specificAddress = this.addressList[index].address;
                if(this.addressList[index].phone == null) {
                    this.ruleForm.phoneNumber1 = '';
                    this.ruleForm.phoneNumber2 = '';
                } else {
                    this.ruleForm.phoneNumber1 = this.addressList[index].phone.split("-")[0];
                    this.ruleForm.phoneNumber2 = this.addressList[index].phone.split("-")[1];
                }
                this.ruleForm.selectedOptions = [String(this.addressList[index].province_id), String(this.addressList[index].city_id), String(this.addressList[index].district_id)];
                if(!this.addressList[index].is_default) {
                    this.ruleForm.checked = false;
                } else {
                    this.ruleForm.checked = true;
                }
            }
      }
  },
  computed:{
      ...mapState({
          addressList:state=>state.order.addressList
      })
  }
}
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.area {
    width: 100%;
}
.line {
    width: 100%;
    height: 1px;
    background-color: #dcdfe6;
    display: block;
    margin-top: 19*@fontSize;
}
.sureAddress {
    text-align: right;
}
</style>



