<template>
  <div class="productInfo">
      <div class="container">
          <div class="breadCrumbTitle">
              <BreadCrumb :breadCrumbData="seriesInfoData"/>
          </div>
          <div class="clearfix">
              <div class="zoom">
                  <Zoom :zoomData="seriesInfoData"/>
              </div>
              <div class="productInfoRight">
                  <div class="productDetail" v-for="(item, index) in seriesInfoData" :key="index">
                        <div>
                            <h4>{{item.product_name}}</h4>
                            <ul>
                                <li>品牌 : {{item.brand.brand_name}}</li>
                                <li>产地 : {{item.brand.registration_place}}</li>
                            </ul>
                            <h4>产品概述</h4>
                            <p>
                                {{item.summary}}
                            </p>
                            <div class="viewProductList">
                                <button @click="viewProductList" >
                                    <span class="iconfont icon-chakanxiangqing"></span><span>查看型号列表</span>
                                </button> 
                            </div>         
                        </div>
                    </div>
                </div>
            </div>
          <div class="SeriesInfoSummary">
              <SeriesInfoSummary :showActiveName="showActiveName" @changeShowActiveName="changeShowActiveName"/>
          </div>
      </div>
  </div>
</template>
<script>
import BreadCrumb from "../components/breadCrumb/main/BreadCrumb";
import Zoom from "../components/zoom/main/Zoom";
import SeriesInfoSummary from "../components/seriesInfo/main/SeriesInfoSummary";
import { mapState, mapActions } from "vuex";
import swal from "sweetalert"; //弹出消息提示框
import "../assets/sweetAlert/sweetAlert.css";
export default {
  components: {
    BreadCrumb,
    Zoom,
    SeriesInfoSummary
  },
  mounted() {
    var loader = this.$loading({ fullScreen: true });
    this.updateSeriesInfoDataAsync({
      param: this.$route.params.id,
      success: res => {
        // console.log(res);
        loader.close();
      },
      fail: res => {
        // console.log(res);
        loader.close();
      },
      error: error => {
        // console.log(error);
        loader.close();
      }
    });
  },
  data() {
    return {
      prices: [],
      num: [],
      showActiveName: ""
    };
  },
  methods: {
    ...mapActions({
      updateSeriesInfoDataAsync: "seriesInfo/updateSeriesInfoDataAsync"
    }),
    viewProductList() {
      if (this.showActiveName !== "second") {
        this.showActiveName = "second";
      }
      setTimeout(() => {
        document
          .getElementById("001")
          .scrollIntoView({
            behavior: "smooth",
            block: "center",
            inline: "nearest"
          });
      }, 100);
    },
    changeShowActiveName(val) {
      this.showActiveName = val;
    }
  },
  computed: {
    ...mapState({
      seriesInfoData: state => state.seriesInfo.seriesInfoData
    })
  },
  watch: {
    $route(newvalue, oldvalue) {
      var loader = this.$loading({ fullScreen: true });
      this.updateSeriesInfoDataAsync({
        param: this.$route.params.id,
        success: res => {
        //   console.log(res);
          loader.close();
        },
        fail: res => {
          // console.log(res);
          loader.close();
        },
        error: error => {
          // console.log(error);
          loader.close();
        }
      });
    }
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.productInfo {
  margin: 30 * @fontSize;
  .breadCrumbTitle {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 2px;
      background-color: #0275d8;
      z-index: 1;
    }
  }
  .zoom {
    float: left;
    width: 35%;
    margin-right: 5%;
  }
  .productInfoRight {
    float: right;
    width: 60%;
    position: relative;
    padding-bottom: 10px;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: #fff;
      z-index: 1;
    }
  }
  .productInfoSummary {
    margin: 30 * @fontSize 0;
  }
  .SeriesInfoSummary {
    margin-top: 30 * @fontSize;
  }
}
.productDetail {
  margin-top: 30 * @fontSize;
  h4 {
    color: #333;
    font-weight: 700;
    font-size: 16 * @fontSize;
  }
  ul {
    li {
      height: 40px;
      line-height: 40px;
      font-size: 14px;
    }
  }
  p {
    text-indent: 2em;
    line-height: 2;
    font-size: 14px;
  }
  .price {
    height: 40 * @fontSize;
    line-height: 40 * @fontSize;
    padding-left: 10 * @fontSize;
    background: url("../assets/images/bgPic.png") no-repeat;
    background-size: cover;
    margin-bottom: 20 * @fontSize;
    color: #666;
    font-size: 14 * @fontSize;
    strong {
      color: #0275d8;
      font-weight: 700;
      font-family: Arial;
      font-size: 16 * @fontSize;
      margin-left: 15 * @fontSize;
    }
  }
  .diffNumPrice {
    position: relative;
    margin-bottom: 20px;
    .differentNum {
      li {
        position: relative;
        height: 26px;
        line-height: 26px;
        display: inline-block;
        position: relative;
        margin-right: 2 * @fontSize;
        font-size: 14 * @fontSize;
        &:last-child {
          margin-right: 0;
        }
        .rect {
          position: absolute;
          left: 50%;
          top: 0;
          border-width: 27px 25px 0px 0px;
          border-style: solid;
          border-color: #0275d8 #fff #0275d8 #0275d8;
        }
        .num {
          background-color: #0275d8;
          color: #fff;
          float: left;
          width: 128px;
          height: 28px;
          line-height: 28px;
          text-align: center;
          overflow: hidden;
        }
        .numPrice {
          color: #0275d8;
          width: 100px;
          float: left;
          text-align: center;
          border: 1px solid #0275d8;
          border-left: none;
          overflow: hidden;
        }
      }
    }
  }
  .detailAndBuy {
    margin-bottom: 15 * @fontSize;
    padding-bottom: 20 * @fontSize;
    li {
      padding: 6px 0;
      font-size: 14 * @fontSize;
      span {
        margin-left: 5px;
      }
      a {
        margin-left: 10px;
        color: @baseColor;
      }
    }
  }
  .numChange {
    width: 150 * @fontSize;
  }
}
.viewProductList button {
  margin-top: 10px;
  width: 150 * @fontSize;
  height: 40 * @fontSize;
  line-height: 40 * @fontSize;
  font-size: 14 * @fontSize;
  border: none;
  background-color: #0275d8;
  border: 1px solid #0275d8;
  color: #fff;
  cursor: pointer;
  .iconfont {
    margin-right: 5px;
    font-size: 30 * @fontSize;
    vertical-align: middle;
  }
}
.swal-button {
  background-color: @baseColor!important;
}
</style>



