<template>
    <div class="rankTitle">
        <!-- 商品列表标题排序部分 -->
        <el-row v-show='isBigImgList'>
            <el-col :span="2">
                <span @click = "synSort" class="cursorPointer">综合排名</span>
            </el-col>
            <el-col :span="2" class="rankPrice"> 
                <span @click="priceSort" class="cursorPointer">
                    价格
                    <i  :class="[isUp===true?'color_ccc':'','el-icon-caret-top']"></i>
                    <i  :class="[isUp===false?'color_ccc':'','el-icon-caret-bottom']"></i>
                </span>
            </el-col>
            <el-col :span="2">
                <span><input type="checkbox" class="hasCount" id="hasCount" @click="countSort" :checked="hasCount"> <label for="hasCount" @click="countSort">库存</label></span>
            </el-col>
            <el-col :span="3" :offset="7">
                共<span class="totalAmount"> {{totalCount}} </span>件商品
            </el-col>
            <el-col :span="2">
                <router-link :to="{path:'/productList/bigImgList',query:this.$route.query}" :class="{selectedColor:isSelected}" @click.native="isSelected = true" >
                    <span class="iconfont icon-liebiao"></span>大图
                </router-link>
            </el-col>
            <el-col :span="2">
                <router-link :to="{path:'/productList',query:this.$route.query}" :class="{selectedColor:!isSelected}" @click.native="isSelected = false">
                    <span class="iconfont icon-liebiao1"></span> 列表
                </router-link>
            </el-col>
            <el-col :span="2">
                {{listCurrpage}} / {{totalPage}}
            </el-col>
            <el-col :span="2">
               <el-button type="text" class="arrow arrowPrev" @click="previousPage"><i class="el-icon-arrow-left"></i></el-button>
               <el-button type="text" class="arrow arrowNext" @click="nextPage"><i class="el-icon-arrow-right"></i></el-button>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import { mapState, mapMutations, mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  props: ["limit"],
  components: {},
  created() {
    if (this.$route.path == "/productList/bigImgList") {
      //初始化执行
      this.isSelected = true;
    } else {
      this.isSelected = false;
    }
    var param = this.$route.query;
    if (param.sort == "first_price" && param.order == "asc") {
      this.isUp = true;
    } else if (param.sort == "first_price" && param.order == "desc") {
      this.isUp = false;
    } else {
      this.isUp = 0;
    }
    if (param.has_stock == 1) {
      this.hasCount = true;
    } else if (param.has_stock == 0) {
      this.hasCount = false;
    } else {
      this.hasCount = 0;
    }
  },
  data() {
    return {
      isSelected: false,
      // currpage: 1,
      hasCount: 0, //库存排序，未点击时为0
      isUp: 0 //价格排序，未点击时为0
    };
  },
  methods: {
    ...mapMutations({
      updatelistOffset: "productList/updatelistOffset"
    }),
    synSort: function() {
      //点击综合排序
      this.isUp = 0;
      this.updatelistOffset(0);
      // this.currpage = 1;
      var param = this.getParam(
        this.listOffset * this.limit,
        this.limit,
        this.hasCount,
        this.isUp,
        this.$route.query.keywords
      );
      this.$router.push({ query: param });
      // this.$emit('updateProductList',param);
    },
    priceSort: function() {
      //点击价格排序
      this.isUp = !this.isUp;
      this.updatelistOffset(0);
      var param = this.getParam(
        this.listOffset * this.limit,
        this.limit,
        this.hasCount,
        this.isUp,
        this.$route.query.keywords
      );
      this.$router.push({ query: param });
      // this.$emit('updateProductList',param);
    },
    countSort: function() {
      //点击库存排序
      this.hasCount = !this.hasCount;
      this.updatelistOffset(0);
      var param = this.getParam(
        this.listOffset * this.limit,
        this.limit,
        this.hasCount,
        this.isUp,
        this.$route.query.keywords
      );
      this.$router.push({ query: param });
      // this.$emit('updateProductList',param);
    },
    previousPage: function() {
      //点击上一页
      if (this.listOffset <= 0) {
        this.updatelistOffset(0);
        return;
      }
      var count = this.listOffset - 1;
      this.updatelistOffset(count);
      var param = this.getParam(
        this.listOffset * this.limit,
        this.limit,
        this.hasCount,
        this.isUp,
        this.$route.query.keywords
      ); //获取参数，更新父组件数据
      this.$router.push({ query: param });
      // this.$emit('updateProductList',param);
    },
    nextPage: function() {
      //点击下一页
      var count = this.listOffset + 1;
      this.updatelistOffset(count);
      if (this.listOffset >= this.totalPage) {
        // this.currpage = this.totalPage;
        this.updatelistOffset(this.totalPage - 1);
        return;
      }
      var param = this.getParam(
        this.listOffset * this.limit,
        this.limit,
        this.hasCount,
        this.isUp,
        this.$route.query.keywords
      ); //获取参数，更新父组件数据
      this.$router.push({ query: param });
      this.$emit('updateProductList',param);
    },
    getParam: function(offset, pageSize, hasCount, isUp, keywords) {
      var param = { offset: offset, limit: this.limit, keywords: keywords };
      var orderObj = {};
      var haveObj = {};
      if (isUp === 0) {
        orderObj = {};
      } else if (isUp) {
        orderObj = { sort: "first_price", order: "asc" };
      } else {
        orderObj = { sort: "first_price", order: "desc" };
      }
      // 判断是否勾选库存
      if (hasCount === 0) {
        haveObj = {};
      } else if (hasCount) {
        haveObj = { has_stock: 1 };
      } else {
        haveObj = { has_stock: 0 };
      }
      var params = Object.assign(param, orderObj, haveObj);
      return params;
    }
  },
  computed: {
    ...mapState({
      productList: state => state.productList.productList,
      totalCount: state => state.productList.totalCount,
      listOffset: state => state.productList.listOffset,
      isBigImgList: state => state.productList.isBigImgList
    }),
    ...mapGetters({
      listCurrpage: "productList/listCurrpage"
    }),
    totalPage: function() {
      return Math.ceil(parseInt(this.totalCount) / this.limit);
    }
  },
  watch: {
    $route: function(newvalue, oldvalue) {
      var rankParams = this.$route.query;
      if (rankParams.sort == "first_price" && rankParams.order == "asc") {
        this.isUp = true;
      } else if (rankParams.sort == "first_price" &&rankParams.order == "desc") {
        this.isUp = false;
      } else {
        this.isUp = 0;
      }
      if (rankParams.has_stock == 1) {
        this.hasCount = true;
      } else if (rankParams.has_stock == 0) {
        this.hasCount = false;
      } else {
        this.hasCount = 0;
      }
      this.updatelistOffset(this.$route.query.offset / this.limit);
      this.$emit("updateProductList", rankParams);
      if (newvalue.path == "/productList/bigImgList") {
        this.isSelected = true;
      } else {
        this.isSelected = false;
      }
    }
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.rankTitle {
  height: 40*@fontSize;
  line-height: 40*@fontSize;
  background-color: #3284d8;
  position: relative;
  box-shadow: 0 16px 26px -27px rgba(2, 117, 216, 0.56),
    0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -8px rgba(2, 117, 216, 0.2);
  color: #fff;
  text-align: center;
  font-size: 14*@fontSize;
  overflow: hidden;
  &::after {
        content: "";
         position: absolute;
         left: 0;
         bottom: 0;
         width: 100%;
         height: 1px;
         background-color: #0275d8;
         z-index: 1;
    }
  .rankPrice {
    position: relative;
    i {
      position: absolute;
      right: 20*@fontSize;
      font-size: 12*@fontSize;
      transform: scale(0.8);
    }
    .el-icon-caret-top {
      top: 28%;
    }
    .el-icon-caret-bottom {
      bottom: 26%;
    }
  }
  .hasCount {
    vertical-align: -2px;
  }
  a {
    display: block;
    color: #fff;
    &.selectedColor {
      background-color: @baseColor;
    }
    .iconfont {
      font-size: 14*@fontSize;
      margin-right: 3*@fontSize;
    }
  }
  .cursorPointer {
    cursor: pointer;
    display: block;
    width: 100%;
    height: 100%;
  }
  .color_ccc {
    color: #ccc !important;
  }
  .arrow {
    color: #fff;
    width: 20px;
    font-size: 16*@fontSize;
  }
}
</style>



