import Vue from 'vue';
import Router from 'vue-router';
import IndexPage from '../pages/IndexPage.vue';
import ProductList from '../pages/ProductList.vue';
import productInfo from '../pages/ProductInfo.vue';
import ListMethod from '../components/productListDetail/main/ListMethod.vue';
import BigImgList from '../components/productListDetail/main/BigImgList.vue';
import Verify from '../pages/verify.vue';
import Main from '../pages/Main.vue';
import Shopcar from '../pages/Shopcar.vue';
import Order from '../pages/Order.vue';
import OrderInfo from '../pages/OrderInfo.vue';
import OrderDetail from '../pages/OrderDetail.vue';
import PaymentSuccess from '../pages/PaymentSuccess.vue';
import ProductSelect from '../pages/ProductSelect.vue';
import SeriesInfo from "../pages/SeriesInfo.vue";
import StepList from '../pages/StepList.vue';
import SolutionDetail from '../pages/SolutionDetail.vue';
import SolutionList from "../pages/SolutionList";
import SolutionInfo from "../pages/SolutionInfo";
import SeriesList from "../pages/SeriesList.vue";
import SeriesBigImg from "../components/seriesList/main/SeriesBigImg.vue";
import SeriesBigList from "../components/seriesList/main/SeriesBigList.vue";
import SearchList from '../pages/SearchList.vue';
import SearchBigImg from "../components/searchList/main/SearchBigImg.vue";
import SearchBigList from "../components/searchList/main/SearchBigList.vue";
import ModalNumberInfo from "../pages/ModalNumberInfo.vue"
Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            component: Main,
            children: [
                {
                    path: '/',
                    component: IndexPage,
                },
                {
                    path: '/productList',
                    component: ProductList,
                    children: [
                        {
                            path: 'bigImgList',
                            component: BigImgList,
                        }, {
                            path: '/',
                            component: ListMethod,
                        },
                    ],
                },
                {
                    path: '/seriesList',// 系列列表页
                    component: SeriesList,
                    children: [
                        {
                            path: 'seriesBigImg',
                            component: SeriesBigImg,
                        }, {
                            path: '/',
                            component: SeriesBigList,
                        },
                    ],
                },
                {
                    path: '/searchList',// 搜索列表页
                    component: SearchList,
                    children: [
                        {
                            path: 'searchBigImg',
                            component: SearchBigImg,
                        }, {
                            path: '/',
                            component: SearchBigList,
                        },
                    ],
                },
                {
                    path: '/productInfo/:id',
                    component: productInfo,
                    props: true,
                },
                {
                    path:"/modalNumberInfo/:id",
                    component: ModalNumberInfo
                },
                {
                    path: '/shopcar',
                    component: Shopcar,
                },
                {
                    path: '/order',//订单
                    component: Order,
                },
                {
                    path: '/orderInfo/:id',//订单详情
                    component: OrderInfo,
                    props: true,
                },
                {
                    path: '/orderDetail/:id',//订单及子订单详细信息
                    component: OrderDetail,
                    props: true,
                },
                {
                    path: '/paymentSuccess',//支付成功
                    component: PaymentSuccess,
                },
                {
                    path: '/seriesInfo/:id', //系列详情页
                    component: SeriesInfo,
                    props: true
                },
                {
                    path:'/solutionList',// 方案列表页
                    component:SolutionList,
                    name: 'solutionList',
                },
                {
                    path: '/stepList', // 方案选择页
                    component: StepList,
                },
                {
                    path:"/solutionInfo/:id",// 方案详情页
                    component: SolutionInfo
                },
                {
                    path:'/solutionDetail',
                    component:SolutionDetail,
                },
                {
                    path: '/ProductSelect',
                    component: ProductSelect,
                },

            ],
        },
        {
            path: '/verify',
            component: Verify,
        },
    ],
});



