<template>
  <div class="container">
      <div class="address">
          <div class="addressTitle">
              <Title title="收货地址" englishTitle="" iconClass="icon-diqiuyi"/>
                <span class="increaseAddress" @click="addAddress"><span class="iconfont icon-tianjia"></span>新增地址</span>
          </div>
           <div class="addressContent">
               <span class="increaseAddress" v-if="addressData.length <= 0" @click="addAddress"><span class="iconfont icon-tianjia"></span>新增地址</span>
               <ul class="specificAddress"> 
                    <li v-for="(item,index) in addressData" :key="index">
                        <input type="radio" name="chooseAddress" :checked="radioIndex == index" @click="changeAddress" :data-index="index">
                        <span class="name">{{item.consigner}}</span>
                        <span class="tel">{{item.mobile}}</span>
                        <span class="province">{{item.area[0]}}</span>
                        <span class="city">{{item.area[1]}}</span>
                        <span class="district">{{item.area[2]}}</span>
                        <span class="address">{{item.address}}</span>
                        <span class="defaultAddress" v-if="index === 0">默认</span>
                        <div class="edit">
                            <el-button size="small" type="primary" @click="editAddress" :data-index="index">编辑</el-button>
                            <el-button size="small" @click="deleteAddressList" :data-index="index">删除</el-button>
                        </div>
                    </li>
                </ul> 
               <Address :add="add" @changeAdd="changeAdd" :editIndex="editIndex"/>
           </div>
      </div>
      <div class="goodsInfo">
          <div class="goodsInfoTitle">
                <Title title="送货清单" englishTitle="" iconClass="icon-caidan"/>
                <router-link to="/shopcar" class="goShopcar"><span>返回购物车修改</span></router-link>
          </div>
          <div class="goodsInfoContent">
                <el-row class="infoTitle">
                    <el-col :span="9">商品信息</el-col>
                    <el-col :span="5">单价(元)</el-col>
                    <el-col :span="5">数量</el-col>
                    <el-col :span="5">小计(元)</el-col>
                </el-row>
                <OrderGoodsInfo @changeBillingInfo="changeBillingInfo"/>
          </div>
      </div>
      <div class="remark">
            <Title title="添加备注" englishTitle="" iconClass="icon-icon-test"/>
            <div class="remarkContent">
                <el-input
                type="textarea"
                :rows="4"
                maxlength="60"
                placeholder="不超过60字"
                v-model="textarea">
            </el-input>
            </div>
      </div>
      <div class="payMethod">
          <Title title="支付方式" englishTitle="" iconClass="icon--fufeiyuedu"/>
          <div>
              <img src="../assets/images/inlinePayment.jpg" alt="">
          </div>
      </div>
      <div class="invoice">
            <Title title="发票信息" englishTitle="" iconClass="icon-dingdan"/>
            <div class="invoiceInfo">
                <ul>
                    <li>
                        <span class="invoiceType">发票类型 :</span>
                        <span class="invoiceTypeInfo" @click="chooseInvoice">请选择 >></span>
                    </li>
                    <li>
                        <span class="invoiceTitle">发票抬头 :</span>
                        <span class="invoiceTitleInfo">{{invoiceTitle}}</span>
                    </li>
                </ul>   
            </div>
            <Invoice @changeInvoiceTitle="changeInvoiceTitle" :invoiceAlert="invoiceAlert"/>
      </div>
      <div class="billing">
          <Title title="结算信息" englishTitle="" iconClass="icon-money"/>
          <div class="billingInfo">
                <ul>
                    <li><span class="categoryNum">{{totalCategory}}</span>类 共<span class="commodityNum">{{totalCount}}</span>件 , 总计：
                    <strong><span class="commodityPrice">{{totalMoney}}</span></strong></li>
                    <li>运费：<strong>&yen;&nbsp;&nbsp;<span class="freightCharge">0.00</span></strong></li>
                    <li class="finalAddress">
                        {{finalAddress}}
                    </li>
                </ul>
            </div>
            <p>应付金额：<strong><span class="amountPayable">{{totalMoney}}</span></strong><el-button type="primary" class="submitOrder" @click="submitOrder">提交订单</el-button></p> 
      </div>
  </div>
</template>
<script>
import {mapState, mapActions} from 'vuex';
import Title from "../components/title/title";
import Address from "../components/Order/main/Address";
import OrderGoodsInfo from "../components/Order/main/OrderGoodsInfo.vue";
import Invoice from "../components/Order/main/Invoice.vue";
import options from "../util/cascader-address-options";
import {getAreaBycode} from "../util/cascader-address-options"
export default {
  components: {
    Title,
    Address,
    OrderGoodsInfo,
    Invoice
  },
  created(){
        this.invoiceAlert = false;
  },
  mounted(){
        this.updateAddressListAsync();
        this.updateInvoiceListAsync();
        let paramString = location.href.split("?")[1];
        let qs = require('qs');
        let paramArr = qs.parse(paramString);
        this.updateProductListAsync({
            params:paramString,
            success:function(res){
                
            },
            error:function(error){
                
            }
        })
  },
  data() {
    return {
        textarea:'',
        addressData:[],
        radioIndex:0,
        add:false,
        editIndex:'',//默认需要编辑的地址为空
        invoiceTitle:"",
        invoiceAlert:false,
        totalMoney:'',
        totalCount:'',
        totalCategory:"",
        finalAddress:''
    };
  },
  methods:{
      ...mapActions({
          updateAddressListAsync:"order/updateAddressListAsync",
          deleteAddress:"order/deleteAddress",
          updateInvoiceListAsync:"order/updateInvoiceListAsync",
          updateProductListAsync:"order/updateProductListAsync",
          createOrder:"order/createOrder",//点击生成订单
          updateshopcarListAsync:'shopcar/updateshopcarListAsync'//更新购物车
      }),
      addAddress:function () {
          this.add = true;
      },
      changeAdd:function (bool) {
          this.add = bool;
          this.editIndex = '';
      },
      editAddress(e){
          var index = e.currentTarget.dataset.index;
          this.add = true;
          this.editIndex = index;
      },
      deleteAddressList(e){
          var index = e.currentTarget.dataset.index;
          this.deleteAddress({
              addressId:this.addressList[parseInt(index)].id,
              success:function(res){
                    var rescode = parseInt(res.data.rescode);
                    if(rescode <= 10000) {
                        this.updateAddressListAsync();
                    } else {
                        const h = this.$createElement;
                        this.$message({
                            message: h('p', null, [
                                h('span', null, '删除地址失败，请重试'),
                                h('i', { style: 'color: teal' },)
                            ]),
                            center: true
                        })
                    }
              }.bind(this),
              error:function (res){
                    const h = this.$createElement;
                    this.$message({
                        message: h('p', null, [
                            h('span', null, '请求服务失败，请重试'),
                            h('i', { style: 'color: teal' },)
                        ]),
                        center: true
                    })
              }.bind(this)
          })
      },
      chooseInvoice(){//点击发票请选择，弹出选择发票框
            this.invoiceAlert = true;
      },
      changeInvoiceTitle(param){
          if(typeof(param)=='string' && param != '') {
              this.invoiceTitle = param;
          } else if(typeof(param)=='number') {
              this.invoiceTitle = this.invoiceList[param].partyname+" ( " + this.invoiceList[param].taxno + " ) ";
          } else {
              this.invoiceTitle = '';
          }
          this.invoiceAlert = false;
      },
      changeBillingInfo(param){//修改结算信息
        this.totalMoney = param.totalMoney.toFixed(2);
        this.totalCount = param.totalCount;
        this.totalCategory= param.totalCategory;
      },
      changeAddress(e){//改变收货地址
        var index = parseInt(e.currentTarget.dataset.index);
        this.finalAddress = this.addressList[index].consigner +" "+this.addressList[index].area[0]+this.addressList[index].area[1]+this.addressList[index].area[2] +" "+
                            this.addressList[index].address+" "+this.addressList[index].mobile+" ( "+this.addressList[index].company_name+" ) ";;
        this.radioIndex = index;
      },
      submitOrder(){//点击提交订单
        if(this.addressList.length <= 0) {
              const h = this.$createElement;
              this.$message({
                  message: h('p', null, [
                      h('span', null, '请添加收货地址'),
                      h('i', { style: 'color: teal' },)
                  ]),
                  center: true
              })
              return false;
        } else if(this.productList.length <= 0){
            const h = this.$createElement;
              this.$message({
                  message: h('p', null, [
                      h('span', null, '请添加商品'),
                      h('i', { style: 'color: teal' },)
                  ]),
                  center: true
              })
              return false;
        }else if (this.invoiceTitle == ""){
            const h = this.$createElement;
            this.$message({
                message: h('p', null, [
                    h('span', null, '请选择发票类型'),
                    h('i', { style: 'color: teal' },)
                ]),
                center: true
            })
            return false;
        } else {
            var loader = this.$loading({ fullscreen: true });
            var InvoiceParams = {}
            if(this.invoiceTitle=="您选择不开发票") {
                InvoiceParams = {
                    "title" : "你选择不开发票",
                    "account" : "",
                    "tax_number" : "",
                    "telephone" : "",//指发票栏的电话
                    "bank" : "",
                    "company_address" : "",//发票地址
                    "receipt_type":3
                }
            } else {
                var taxCode = this.invoiceTitle.split(" ( ")[1].split(" )")[0];
                var orderInvoiceArr = this.invoiceList.filter((item)=>{//订单发票
                    return item.taxno == taxCode
                })
                var orderInvoice = orderInvoiceArr[0];
                InvoiceParams = {
                    "title" : orderInvoice.partyname,
                    "account" : orderInvoice.bank_account,
                    "tax_number" : orderInvoice.taxno,
                    "telephone" : orderInvoice.phone,//指发票栏的电话
                    "bank" : orderInvoice.bank,
                    "company_address" : orderInvoice.address,//发票地址
                    "receipt_type":2
                };
            }
            var orderAddress = this.addressList[this.radioIndex];//订单地址
            var addressParams = {
                "guest_company_name":orderAddress.company_name,
                "receiver": orderAddress.consigner,
                "province": orderAddress.area[0],
                "city": orderAddress.area[1],
                "district": orderAddress.area[2],
                "mobile": orderAddress.mobile,
                "address": orderAddress.address,
                "remarks": this.textarea,
            }
            var productArr = [];
            this.productList.forEach((item)=>{
                productArr.push({
                    "goods_id": item.goods.gno,
                    "number": item.quantity,
                })
            });
            var productParams = {
                "goods_info":JSON.stringify(productArr)
            }
            var orderParams = Object.assign(InvoiceParams,addressParams,productParams);//提交订单的所有参数
            this.createOrder({
                params:orderParams,
                success:function(res){
                    // 提交订单成功后删除购物车商品
                    var ids = [];
                    this.productList.forEach((item,index) => {
                        ids.push(item.id);
                    })
                    var orderId = res.data.data.id;
                    let url = this.$urlConfig.config.seriesUrl + "/carts";
                    this.$http({
                        url:url,
                        method:'DELETE',
                        data:{
                            "ids":ids,
                        }
                    }).then((res)=>{

                        var rescode = parseInt(res.data.rescode);
                        if(rescode <= 10000) {
                            this.$router.push("/orderInfo/"+orderId)
                        } else {
                            
                        }
                    })
                    loader.close();
                }.bind(this),
                fail:function (res) {
                    const h = this.$createElement;
                    this.$message({
                        message: h('p', null, [
                            h('span', null, '提交订单失败,请重试'),
                            h('i', { style: 'color: teal' },)
                        ]),
                        center: true
                    })
                    loader.close();
                }.bind(this),
                error:function (res) {
                    const h = this.$createElement;
                    // this.$message({
                    //     message: h('p', null, [
                    //         h('span', null, '请求服务失败,请重试'),
                    //         h('i', { style: 'color: teal' },)
                    //     ]),
                    //     center: true
                    // })
                    loader.close();
                    
                }.bind(this)
            })
        }
      }
  },
  computed:{
      ...mapState({
          addressList:state=>state.order.addressList,
          invoiceList:state=>state.order.invoiceList,
          productList:state=>state.order.productList,
      })
  },
  watch:{
      addressList:function(newvalue,oldvalue) {
          this.radioIndex = 0;
            this.addressData = [];
            newvalue.forEach((item,index) => {
                item.area=getAreaBycode(String(item.district_id));
                this.addressData.push(item);
            });
         if(newvalue.length > 0) {
            this.finalAddress = newvalue[0].consigner +" "+newvalue[0].area[0]+newvalue[0].area[1]+newvalue[0].area[2] +" "+
                                newvalue[0].address+" "+newvalue[0].mobile+" ( "+newvalue[0].company_name+" ) ";
         } else {
             this.finalAddress = "";
         }
      }
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.address {
  margin-top: 30*@fontSize;
  .addressTitle {
    position: relative;
    .increaseAddress {
      cursor: pointer;
      position: absolute;
      bottom: 0;
      right: 0;
      font-size: 14*@fontSize;
      .iconfont {
        font-size: 14*@fontSize;
        margin-right: 3*@fontSize;
      }
    }
  }
  .addressContent {
    padding: 20*@fontSize 0;
    .increaseAddress {
      font-size: 14*@fontSize;
      cursor: pointer;
      .iconfont {
        font-size: 14*@fontSize;
        margin-right: 3*@fontSize;
      }
    }
  }
}
.specificAddress {
    font-size: 14*@fontSize;
    li {
        height: 40px;
        line-height: 40px;
        &:hover {
            background-color: rgba(2, 117, 216, 0.05);
            .edit {
                display: block;
            }
        }
        .defaultAddress {
            padding: 3px 8px;
            margin-left: 10*@fontSize;
            color: #0275f8;
            border-radius: 3px;
            border: 1px solid #0275d8;
            font-size: 12*@fontSize;
        }
        .edit {
            float: right;
            display: none;
            .editAddress {
                cursor: pointer;
            }
            .deleteAddress {
                cursor: pointer;
                margin-left: 10*@fontSize;
            }
            
        }
    }
}
.goodsInfo {
    .goodsInfoTitle {
        position: relative;
        .goShopcar {
            position: absolute;
            bottom: 0;
            right: 0;
            color: #333;
            font-size: 14*@fontSize;
        }
    }
    .infoTitle {
        text-align: center;
        background-color: #f1f1f1;
        height: 40px;
        line-height: 40px;
        margin-top: 30px;
        font-size: 14*@fontSize;
    }
}
.remark {
    margin: 40*@fontSize 0;
    .remarkContent {
        margin-top: 40*@fontSize;
    }
}
.payMethod {
    text-align: left;
    img {
        width: 200px;
        height: 102px;
        // float: left;
        margin: 25px 0;
    }
}
.invoice {
    .invoiceInfo {
        border: 1px solid #ccc;
        padding:10px;
        margin: 40px 0 30px 0;
        ul li {
            font-size:14px;
            height: 50px;
            line-height: 50px;
        }
        .invoiceTypeInfo {
            color: @baseColor;
            cursor: pointer;
            margin-left: 10*@fontSize;
        }
        .invoiceTitleInfo {
            margin-left: 10*@fontSize;
        }
    }

.selectInvoiceType {
    color:#0275d8;
    cursor:pointer;
    margin-left:10px;
}
}
.billing {  
    padding-left: 0;
    padding-right: 0;
    .billingInfo {
        border: 1px solid #ccc;
        padding:20px;
        margin-top: 40*@fontSize;
    }
    .billingInfo ul li {
        height: 40*@fontSize;
        line-height: 40*@fontSize;
        text-align: right;
        padding-right: 50*@fontSize;
        font-size: 14*@fontSize;
        &:last-child {
                padding-right: 20*@fontSize;
            }
        strong {
            color: @baseColor;
            font-weight: 600;
            margin-left: 30*@fontSize;
            display: inline-block;
            width: 50*@fontSize;
            text-align: left;
        }
        .categoryNum, .commodityNum {
            margin:0 8px;
        }
    }
    p {
        margin-top: 20px;
        text-align: right;
        .submitOrder {
            background-color: @baseColor;
            border-color: @baseColor;
            color:#fff;
            display: inline-block;
            text-align: center;
            margin-left:20px;
            cursor:pointer;
        }
        strong {
            color: @baseColor;
            font-weight: 600;
            margin: 0 20px 0 5px;
            font-size: 16*@fontSize;
        }
    }
}

 
</style>



