<template>
  <div>
      <ul class="title clearfix">
          <li class="active">全部</li>
          <li>机器人本体</li>
          <li>伺服驱动</li>
          <li>激光加工设备</li>
      </ul>
      <ul class="clearfix">
          <li v-for="(item, index) in indexProduct" :key="index" class="productDescription">
               <router-link :to="'/seriesInfo/'+item.pno">
                    <img :src="item.cover_url" alt="">
                </router-link>
                <router-link :to="'/seriesInfo/'+item.pno">
                    <p class="desc">{{item.product_name}}</p>
                </router-link>
                <p>参&nbsp;&nbsp;考&nbsp;&nbsp;价 : <span class="price"> &yen; {{item.price}}+</span></p>
                <p>发&nbsp;&nbsp;货&nbsp;&nbsp;日 : <span> {{item.lead_time}}天</span></p> 
                <p>数量折扣 : <span> {{item.has_quantity_discount === 1?'有': '无'}}</span></p> 
          </li>
          <li v-for="(item, index) in indexProduct" :key="index+10" class="productDescription">
               <router-link :to="'/seriesInfo/'+item.pno">
                    <img :src="item.cover_url" alt="">
                </router-link>
                <router-link :to="'/seriesInfo/'+item.pno">
                    <p class="desc">{{item.product_name}}</p>
                </router-link>
                <p>参&nbsp;&nbsp;考&nbsp;&nbsp;价 : <span class="price"> &yen; {{item.price}}+</span></p>
                <p>发&nbsp;&nbsp;货&nbsp;&nbsp;日 : <span> {{item.lead_time}}</span></p> 
                <p>数量折扣 : <span> {{item.has_quantity_discount === 1?'有': '无'}}</span></p> 
          </li>
      </ul>
      <div class='click-more'>
            <router-link :to="{path:'/seriesList',query:{page:1}}">
                <p>查看更多</p>
            </router-link>           
      </div>
  </div>
</template>
<script>
import ProductDescription from "../../productDescription/main/ProductDescription";
import { mapState, mapActions } from "vuex";
export default {
  components: {
    ProductDescription
  },
  props: {
    indexProduct: {
      type: Array,
      required: true
    }
  },
  mounted() {},
  data() {
    return {
      productDetailShow: true
    };
  },
  methods: {},
  computed: {}
};
</script>
<style lang="less" scoped>
@baseColor: #0275d8;
@fontSize: 1/16rem;
.title {
  width: 100%;
  border-bottom: 2px solid @baseColor;
  margin-top: 10px;
  li {
    float: left;
    width: 22%;
    height: 40px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
    transition: all 0.3s;
    margin-right: 4%;
    &:hover {
      background-color: @baseColor;
      color: #fff;
    }
    &.active {
      background-color: @baseColor;
      color: #fff;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}
.click-more {
  width: 100%;
  height: 40px;
  text-align: center;
  line-height: 40px;
  box-shadow: 0px 0px 1px 2px rgba(0, 0, 0, 0.02);
  margin: 20px 0;
  p {
    font-size: 14px;
    cursor: pointer;
    width: 140px;
    margin: 0 auto;
    color: #999;
    i {
      font-size: 20px;
      padding-left: 15px;
    }
  }
  p:hover {
    color: @baseColor;
  }
}
.productDescription {
  width: 30%;
  margin-top: 40px;
  padding: 15px;
  box-sizing: border-box;
  box-shadow: 0px 0px 1px 2px rgba(0, 0, 0, 0.02);
  transition: all 0.5s;
  font-size: 14 * @fontSize;
  margin-right: 5%;
  float: left;
  &:nth-child(3n) {
    margin-right: 0;
  }
  &:hover {
    box-shadow: 0px 0px 1px 2px rgba(0, 0, 0, 0.1);
  }
  img {
    width: 180 * @fontSize;
    height: 180 * @fontSize;
    display: block;
    margin: 0 auto;
    cursor: pointer;
  }
  .price {
    color: #0275d8;
    font-weight: 700;
    display: inline-block;
  }
  .desc {
    height: 50px;
    text-align: left;
    color: #333;
    font-size: 14 * @fontSize;
    cursor: pointer;
    font-weight: 600;
    margin: 0;
    margin-top: 30px;
  }
}
</style>



