<template>
    <div>
       <div class="rankTitle">
        <!-- 商品列表标题排序部分 -->
            <el-row>
                
                <el-col :span="3" :offset="13">
                    共<span class="totalAmount"> {{totalCount}} </span>件商品
                </el-col>
                <el-col :span="2">
                    <router-link :to="{path:'/searchList/searchBigImg',query:this.$route.query}">
                        <span class="iconfont icon-liebiao"></span>大图
                    </router-link>
                </el-col>
                <el-col :span="2">
                    <router-link :to="{path:'/searchList',query:this.$route.query}">
                        <span class="iconfont icon-liebiao1"></span> 列表
                    </router-link>
                </el-col>
                <el-col :span="2">
                    {{totalPage>0?listCurrpage:0}} / {{totalPage}}
                </el-col>
                <el-col :span="2">
                   <el-button type="text" class="arrow arrowPrev" @click="previousPage"><i class="el-icon-arrow-left"></i></el-button>
                   <el-button type="text" class="arrow arrowNext" @click="nextPage"><i class="el-icon-arrow-right"></i></el-button>
                </el-col>
            </el-row>
        </div>
        <!-- 列表和大图模式切换 -->
        <router-view></router-view>
        <el-button-group class="pagination clearfix" v-if="totalPage > 0">
            <el-button type="primary" icon="el-icon-arrow-left" class="arrow arrowPrev" @click="previousPage">上一页</el-button>
            <div class="pageNum">
                {{totalPage>0?listCurrpage:0}} / {{totalPage}}
                <!-- {{this.$route.query.page?this.$route.query.page-0+1:1}} / {{totalPage}}        -->
            </div>
            <el-button type="primary" class="arrow arrowNext" @click="nextPage">下一页<i class="el-icon-arrow-right el-icon--right"></i></el-button>
        </el-button-group>
    </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";
export default {
  data() {
    return {};
  },
  props: {
    limit: {
      type: Number,
      required: true
    },
    totalCount: {
      type: String,
      required: true
    }
  },
  components: {},
  created() {
      this.updatelistOffset(this.$route.query.page-1);
  },
  data() {
    return {
      
    };
  },
  methods: {
    ...mapMutations({
      updatelistOffset: "searchList/updatelistOffset"
    }),
    previousPage: function() {
      //点击上一页
        if (this.listOffset <= 0) {
          this.updatelistOffset(0);
          return;
        }
        var count = this.listOffset - 1;
        this.updatelistOffset(count);
        var param = this.getParam(
          this.listOffset
        ); //获取参数，更新父组件数据
        // this.$router.push({ query: param });
        this.$router.replace({ query: param });
        // this.$emit("updateSeriesList",param);
    },
    nextPage: function() {
      //点击下一页
        var count = this.listOffset + 1;
        this.updatelistOffset(count);
        if (this.listOffset >= this.totalPage) {
          this.updatelistOffset(this.totalPage - 1);
          return;
        }
        var param = this.getParam(
          this.listOffset,
        ); //获取参数，更新父组件数据
        // this.$router.push({ query: param });
        this.$router.replace({ query: param });
        // this.$emit("updateSeriesList",param);
    },
    getParam: function(offset) {
        var param = {};
        if(this.$route.query.brands) {
             param = { page: offset+1, brands: this.$route.query.brands,keywords: this.$route.query.keywords};
        } else {
             param = { page: offset+1, keywords: this.$route.query.keywords};
        }
      return param;
    }
  },
  computed: {
    ...mapState({
      listOffset: state => state.searchList.listOffset,

    }),
    ...mapGetters({
      listCurrpage: "searchList/listCurrpage",
    }),
    totalPage: function() {
      return Math.ceil(parseInt(this.totalCount) / this.limit);
    }
  },
  watch: {
    $route: function(newvalue, oldvalue) {// 大图和列表切换时执行
      var rankParams = this.$route.query;
      this.updatelistOffset(this.$route.query.page-1);
      this.$emit("updateSearchList",rankParams)
    }
  }
};
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.rankTitle {
  height: 40 * @fontSize;
  line-height: 40 * @fontSize;
  background-color: #3284d8;
  position: relative;
  box-shadow: 0 16px 26px -27px rgba(2, 117, 216, 0.56),
    0 4px 25px 0px rgba(0, 0, 0, 0.12), 0 8px 10px -8px rgba(2, 117, 216, 0.2);
  color: #fff;
  text-align: center;
  font-size: 14 * @fontSize;
  overflow: hidden;
  &::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: #0275d8;
    z-index: 1;
  }
  .rankPrice,
  .rankLeadTime {
    position: relative;
    i {
      position: absolute;
      right: 20 * @fontSize;
      font-size: 12 * @fontSize;
      transform: scale(0.8);
    }
    .el-icon-caret-top {
      top: 28%;
    }
    .el-icon-caret-bottom {
      bottom: 26%;
    }
  }
  .hasCount {
    vertical-align: -2px;
  }
  a {
    display: block;
    color: #fff;
    &.selectedColor {
      background-color: @baseColor;
    }
    .iconfont {
      font-size: 14 * @fontSize;
      margin-right: 3 * @fontSize;
    }
  }
  .cursorPointer {
    cursor: pointer;
    display: block;
    width: 100%;
    height: 100%;
  }
  .color_ccc {
    color: #ccc !important;
  }
  .arrow {
    color: #fff;
    width: 20px;
    font-size: 16 * @fontSize;
  }
  .router-link-exact-active {
    color: #fff;
    background-color: @baseColor;
  }
}
.pagination {
    display: block;
    position: relative;
    margin: 30*@fontSize 0;
    .pageNum {
        text-align: center;
        height: 40*@fontSize;
        line-height: 40*@fontSize;
    }
    .arrow {
        background-color: @baseColor;
        border-color:@baseColor;
        border-radius: 0;
    }
    .arrowPrev {
        float: left;
    }
    .arrowNext {
        position: absolute;
        top: 0;
        right: 0;
    }
}

</style>



