<template>
    <div class="wrap">
        <div style="position:relative; top:50%;transform: translateY(-50%)" v-if='!isSmallCountChange'>
            <el-input-number  
                v-model="num" 
                @change="handleChange" 
                :min="minCount" 
                :max="maxCount"
                >
            </el-input-number>
            <div class="warning" 
                v-if="tipDisplay">
                <span class="el-icon-warning" />{{tipMessage}}
            </div>
        </div>
        <div style="position:relative; top:50%;transform: translateY(-50%)" v-if='isSmallCountChange' class='isSmallCountChange'>
            <el-input-number  
                v-model="num" 
                @change="handleChange" 
                :min="minCount" 
                :max="maxCount" size='mini'>
            </el-input-number>
            <div class="warning" v-if="tipDisplay">
                <span class="el-icon-warning" />{{tipMessage}}
            </div>
        </div>
    </div>
</template>
<script>
  export default {
    props:{
        min:{
            type:Number,
            required: true
        },
        max:{
            type:Number,
            required: true
        },
        prices:{
            type:Array,
            required: false
        },
        index:{
            type:Number,
            required: true
        },
        quantity:{
            required: true
        },
        cartId:{
            type:Number,
            required: false
        },
        isSmallCountChange:{
            type:Boolean,
            
        }
    },
    data() {
      return {
        num:0,
        minCount:0,
        maxCount: 0,
        tipMessage:'',
        tipDisplay:false,
        firstPrice:0,
        singleAllPrice:0
      };
    },
    created(){
        this.num = this.quantity;
        this.minCount = this.min;
        this.maxCount = this.max;
    },
    mounted(){
        
    },
    methods: {
      handleChange(value) {
        this.tipDisplay = false;
        if(!value) {
            this.tipDisplay = true;
            this.tipMessage = "请输入正确数字";
        }
        if(value <= this.minCount) {
            this.tipDisplay = true;
            this.tipMessage = "起订量：" + this.minCount;
        }else if(value >= this.maxCount) {
            this.tipDisplay = true;
            this.tipMessage = "库存：" + this.maxCount;
        }
        for(var i = 0; i < this.prices.length; i++) {
            if(value >= this.prices[i].min_quantity && value <= this.prices[i].max_quantity) {
                this.firstPrice = this.prices[i].price;
            } else if (value < this.prices[0].min_quantity) {
                this.firstPrice = this.prices[0].price;
            } else if (value > this.prices[this.prices.length-1].max_quantity) {
                this.firstPrice = this.prices[this.prices.length - 1].price;
            }
        }
        this.singleAllPrice = (Math.floor(parseFloat(this.firstPrice)*value*100)/100).toFixed(2);
        this.$emit('changeCount',{price:this.firstPrice,index:this.index,singleAllPrices:this.singleAllPrice,shopcarQuantity:value,cartId:this.cartId});
      },
    },
    watch:{
        min:function (newvalue, oldvalue) {
            this.tipDisplay = false;
            this.minCount = newvalue;
       },
       quantity:function(newvalue, oldvalue) {
           this.num = newvalue;
        //    this.handleChange(this.num);//路由切换执行，改变quantity的值
        //    this.tipDisplay = false;
       },
       max:function (newvalue,oldvalue) {
           this.tipDisplay = false;
           this.maxCount = newvalue;
       }
    },
    computed:{
       
    },
  };
</script>
<style lang="less">
@fontSize: 1/16rem;
@baseColor:#0275d8;
.el-input-number {
    width: 90%;
}
.el-input-number .el-input__inner {
    padding: 0;
}
.el-input-number__decrease, .el-input-number__increase {
    width: 25%;
}
.warning {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    text-align: center;
    color: #e6a23c;
    font-size: 14px;
    margin-top: 5px;
    span {
        background-color:rgba(230,162,60,.1);
        color: #e6a23c;
        margin-right: 8px;
    }
}
.isSmallCountChange{  
    .el-input-number--mini .el-input-number__decrease, .el-input-number--mini .el-input-number__increase{
        width:20px;
        font-size: 12px;
    }
}
</style>




