<template>
  <div class="productDescription">
    <div>
        <router-link :to="'/productInfo/'+productInfo.gno">
            <img :src="productInfo.cover_url" alt="">
        </router-link>
        <p><span class="price">&yen; {{productInfo.first_price}}</span></p>
        <router-link :to="'/productInfo/'+productInfo.gno">
            <p class="desc">{{productInfo.goods_name}}</p>
        </router-link>
        <p class="clearfix productOperation" v-if="pageIndex">
           <span class="iconfont icon-mn_shoucang" @click="save" :id="productInfo.gno" v-if="!isSave"></span> 
           <span class="iconfont icon-mn_shoucang saveHeart" @click="save" :id="productInfo.gno" v-if="isSave"></span> 
            <span class="iconfont icon-caigou-xianxing" @click="addShopcar" :data-gno="productInfo.gno" :data-min="productInfo.min_buy"></span>
        </p>
        <p v-if="!pageIndex">订货号: <span>{{productInfo.gno}}</span></p>
        <p v-if="!pageIndex">库存: <span>{{productInfo.stock}}</span></p>
        <div class="clearfix operation" v-if="!pageIndex">
           <div class="save" @click="save" :id="productInfo.gno"><span class="iconfont icon-mn_shoucang"></span>收藏</div> 
            <div class="addShoppingCar" @click="addShopcar" :data-gno="productInfo.gno" :data-min="productInfo.min_buy"><span class="iconfont icon-caigou-xianxing"></span>加入购物车</div>
        </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapActions } from 'vuex';
import swal from 'sweetalert';//弹出消息提示框
import "../../../assets/sweetAlert/sweetAlert.css";
export default {
    created(){
    },
    props:["pageIndex",'productInfo'],
    data(){
        return {
            isSave:false,//为true代表已收藏
        }
    },
    methods:{
        ...mapActions({
           addToShopcar:'shopcar/addToShopcar',
           saveGoods:'shopcar/saveGoods',
       }),
       addShopcar:function(e){
           var gno = e.currentTarget.dataset.gno;
           var quantity = e.currentTarget.dataset.min;
           this.addToShopcar({
               params:{
                   gno:gno,
                   quantity:quantity
               },success:function (res){
                   let resCode = parseInt(res.data.rescode);
                   if(resCode <= 10000) {
                       if(this.$route.fullPath == "/shopcar") {//如果当前组件所在页面为购物车页面
                           swal({
                                title: "加入购物车成功",
                                icon: "success",
                                buttons: "确定",
                                // dangerMode: true,
                            })
                            .then((willDelete) => {
                                var loader = this.$loading({fullScreen:true});
                                if (willDelete) {
                                    this.$store.dispatch("shopcar/updateshopcarListAsync");
                                    loader.close();
                                } else {

                                }
                            });
                        } else {
                             swal({
                                title: "加入购物车成功",
                                icon: "success",
                                buttons: ["继续购物", "去购物车结算"],
                                // dangerMode: true,
                            })
                            .then((willDelete) => {
                                if (willDelete) {
                                    this.$router.push('/shopcar');
                                } else {

                                }
                            });
                        }
                      
                   } else {
                        var msg = res.data.msg;
                        var tipInfo;
                        if(msg.indexOf(":")!=-1) {
                             tipInfo = msg.split(":")[1]
                        } else {
                           tipInfo = "加入购物车失败,请重试"
                        }
                       swal({title: "",
                            text: tipInfo,
                            icon: "info",
                            button: "关闭",});
                   }
               }.bind(this),error:function (res){
                //    swal({title: "",
                //         text: "请求失败",
                //         icon: "info",
                //         button: "关闭",});
                    }
           })
       },
       save:function (e){//收藏商品
            var goodsGno = e.currentTarget.id;
                this.saveGoods({
                    params:goodsGno,
                    success:(res)=>{
                        // this.isSave = true;
                       var msg = res.data.msg;
                        var tipInfo;
                        if(msg.indexOf(":")!=-1) {
                             tipInfo = msg.split(":")[1]
                        } else {
                           tipInfo = "加入收藏成功"
                        }
                       swal({title: "",
                            text: tipInfo,
                            icon: "info",
                            button: "关闭",});
                    },
                    error:(error)=>{
                        console.log(error);
                    }
                })
        },
    }
}
</script>
<style lang="less" scoped>
@fontSize:1/16rem;
@baseColor:#0275d8;
.productDescription {
    margin-top: 40px;
    padding: 15px;
    box-shadow: 0px 0px 1px 2px rgba(0,0,0,0.02);
    transition: all 0.5s;
    font-size: 14*@fontSize;
    &:hover {
        .productOperation>.iconfont {
            background-color: @baseColor;
        }
        box-shadow: 0px 0px 1px 2px rgba(0,0,0,0.1);
    }
      img {
        width: 180*@fontSize;
        height: 180*@fontSize;
        display: block;
        margin: 0 auto;
        cursor: pointer;
      }
      .price {
            margin-right: 8px;
            color: #0275d8;
            font-weight: 700;
            display: inline-block;
            margin-top: 20px;
            margin-left: 5*@fontSize;
      }
      .desc {
          height: 50px;
          text-align: left;
          color: #333;
          font-size: 14*@fontSize;
          cursor: pointer;
          font-weight: 600;
      }
      .productOperation {
          >.iconfont {
            color: #fff;
            width: 30px;
            height: 30px;
            border-radius: 50%;
            background-color: #ccc;
            line-height: 30px;
            text-align: center;
            font-size: 18px;
            transition: all 0.5s;
            cursor: pointer;
          }
          >.icon-mn_shoucang {
            float: left;
        }
          >.icon-caigou-xianxing {
            float: right;
        }
      }
      .operation {
            .save, .addShoppingCar{
                float: left;
                width: 50%;
                background-color: #ddd;
                color: #333;
                text-align: center;
                height: 30px;
                line-height: 30px;
                transition: all .3s;
                box-sizing: border-box;
                cursor: pointer;
                .iconfont {
                    font-size: 14*@fontSize;
                    margin-right: 5*@fontSize;
                }
                &:hover {
                    background-color: @baseColor;
                    color: #fff;
                }
            }
            .addShoppingCar {
                border: 1px solid #ddd;
                background-color: #fff;
            }
      }
  }
  .saveHeart {
      background-color: #0275d8!important;
      color: #fff!important;
  }
</style>



