<template>
    <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item v-if="nameList.length > 0" 
            :to="{path:'/seriesList',query:{category_id:[nameList[0].id],category_name:[nameList[0].name]}}" 
            >{{nameList[0].name}}
        </el-breadcrumb-item>
        <el-breadcrumb-item  v-if="nameList.length > 1"
            :to="{path:'/seriesList',query:{category_id:[nameList[0].id,nameList[1].id],category_name:[nameList[0].name,nameList[1].name]}}" 
        >{{nameList[1].name}}</el-breadcrumb-item>
        <el-breadcrumb-item  v-if="nameList.length > 2"
            :to="{path:'/seriesList',query:{category_id:[nameList[0].id,nameList[1].id,nameList[2].id],
            category_name:[nameList[0].name,nameList[1].name,nameList[2].name]}}" 
        >{{nameList[2].name}}</el-breadcrumb-item>
        <el-breadcrumb-item  v-if="nameList.length > 3"
            :to="{path:'/seriesList',query:{category_id:[nameList[0].id,nameList[1].id,nameList[2].id,nameList[3].id],
            category_name:[nameList[0].name,nameList[1].name,nameList[2].name,nameList[3].name]}}"
        >{{nameList[3].name}}</el-breadcrumb-item>
        <el-breadcrumb-item>{{goodsName}}</el-breadcrumb-item>
    </el-breadcrumb>
</template>
<script>
import {mapState,mapActions} from 'vuex';
export default {
    props:{
        breadCrumbData:{
            type: Array,
            required: true
        }
    },
    data(){
        return {
            goodsName:'',
            nameList:[]
        }
    },
    mounted(){
        console.log(this.breadCrumbData)
        
    },
    methods:{
        initCategoryData(arr){
            var nameListArr= [];
            var newvalue = arr;
            if(newvalue.length > 0) {
                var categoryData = newvalue[0].category;
                this.goodsName = newvalue[0].goods_name || newvalue[0].product_name;
                nameListArr.push({name:categoryData.category_name,id:categoryData.id});
                if(categoryData.children.length === 0) {
                    return nameListArr;
                } else {
                    nameListArr.push({name:categoryData.children.category_name,id:categoryData.children.id});
                }
                if(categoryData.children.children.length === 0) {
                    return nameListArr;
                }else {
                    nameListArr.push({name:categoryData.children.children.category_name,id:categoryData.children.children.id});
                }
                if(categoryData.children.children.children.length === 0) {
                    return nameListArr;
                }else {
                    nameListArr.push({name:categoryData.children.children.children.category_name,id:categoryData.children.children.children.id});
                }
                if(categoryData.children.children.children.children.length === 0) {
                    return nameListArr;
                }else {
                    nameListArr.push({name:categoryData.children.children.children.children.category_name,id:categoryData.children.children.children.children.id});
                }
                
            }
            return nameListArr
        }
    },
    watch:{
        breadCrumbData(newvalue,oldvalue) {
            console.log(newvalue)
            this.nameList = [];
            this.nameList = this.initCategoryData(newvalue);
            console.log(this.nameList)
            }
        }
}
</script>
<style lang="less">
.el-breadcrumb {
    height: 40px;
    line-height: 40px;
    }
.el-breadcrumb__item .el-breadcrumb__inner {
    color: #666;
    font-weight: 400;
}
.el-breadcrumb__separator {
    color: #666;
    font-weight: 400;
    margin: 0 12px;
}
</style>



