const payment = {
    namespaced:true,
    state:{
        formData:{},
        payInfo:{}
    },
    getters:{

    },
    mutations:{
        updateformData(state,data){
            state.formData = data;
        },
        updatePayInfo(state,data) {
            state.payInfo = data;
        }
    },
    actions:{
        payMoneyNow(store,param){
            var that = this._vm;
            let url = that.$urlConfig.config.paymentUrl + '/payment/unionpay';
            that.$http({
                method:'post',
                url:url,
                data:param.params
            }).then((res)=>{
                var rescode = parseInt(res.data.rescode);
                if(rescode <= 10000) {
                    store.commit('updateformData',res.data.data);
                    param.success(res)
                } else {
                    param.fail(res)
                }
            }).catch((error) => {
                console.log(error)
            })
        },
        getPayInfo(store,param){
            var that = this._vm;
            let url = that.$urlConfig.config.paymentUrl + '/payment/unionpay/' + param.params;
            that.$http({
                method:'get',
                url:url,
            }).then((res)=>{
                var rescode = parseInt(res.data.rescode);
                if(rescode <= 10000) {
                    store.commit('updatePayInfo',res.data.data);
                    param.success(res)
                } else {
                    param.fail(res)
                }
            }).catch((error) => {
                console.log(error)
            })
        }
    }
}
export default payment;


