<template>
  <div>
        <div class="title">
            <div class="container">
                  <el-row :gutter="20">
                      <el-col :span="4"><CategoryList :styleObj="categoryStyleObj" :cateWidth="categoryWidth"/></el-col>
                      <el-col :span="20">
                      </el-col>
                  </el-row>
            </div>
        </div>
        <div class="container">
            <RankTitle :limit="limit" @updateProductList="updateProductList"/>
        </div>
        <div class="container">
            <!-- 列表和大图模式切换 -->
                <router-view></router-view>
        </div>
        <div class="container">
            <Pagination :limit = "limit" :productParam="productParam" @updateProductList="updateProductList"/>
        </div>
  </div>
</template>
<script>
import {mapState, mapMutations, mapActions, mapGetters} from 'vuex';
import CategoryList from '../components/header/main/CategoryList';//引入分类组件
import BreadCrumb from "../components/breadCrumb/main/BreadCrumb";//引入面包屑导航组件
import RankTitle from "../components/productListDetail/main/RankTitle";//引入排序标题组件
import Pagination from "../components/pagination/main/Pagination";//引入分页组件
export default {
    components:{
        CategoryList,
        BreadCrumb,
        RankTitle,
        Pagination
    },
    created(){
    },
    mounted(){
        // 获取排序参数
        var rankParams = this.$route.query
        this.updatelistOffset(this.$route.query.offset/this.limit);
        this.updateCategoryDisplay(false);
        var loader = this.$loading({fullScreen: true})
        this.updateProductListAsync({
            params:rankParams,
            success:(res)=>{
                console.log(res);
                loader.close();
            },
            fail:(res)=>{
                console.log(res);
                loader.close();
            },
            error:(error)=>{
                console.log(error);
                loader.close();
            }
        });// 初始化页面数据
    },
    destroyed(){
        this.updateCategoryDisplay(true);
    },
    data(){
        return {
            categoryStyleObj:{
                'backgroundColor':"#0275d8",
                'color':"#fff",
            },
            categoryWidth:{
                'width':'1000px',
                "top":"104%"
            },
            limit:10,
            productParam:{}
        }
    },
    methods:{
        ...mapMutations({
            updateCategoryDisplay:"updateCategoryDisplay",
            updatelistOffset:"productList/updatelistOffset"
        }),
        ...mapActions({
            updateProductListAsync:"productList/updateProductListAsync",
            
        }),
        updateProductList:function(param){
            var loader = this.$loading({fullScreen: true})
            this.updateProductListAsync({
                params:param,
                success:(res)=>{
                    // console.log(res);
                    loader.close();
                },
                fail:(res)=>{
                    console.log(res);
                    loader.close();
                },
                error:(error)=>{
                    console.log(error);
                    loader.close();
                }
                });
        },
    },
    computed:{
        ...mapState({
            productList:state=>state.productList.productList,
            totalCount:state=>state.productList.totalCount,
        }),
        ...mapGetters({
            listCurrpage:'productList/listCurrpage'
        })
    },
}
</script>
<style lang="less" scoped>
    @baseColor: #0275d8;
    @fontSize: 1/16rem;
    .title {
        height: 40px;
        line-height: 40px;
        margin: 30*@fontSize 0;
        position: relative;
        &::after {
        content: "";
         position: absolute;
         left: 0;
         bottom: 0;
         width: 100%;
         height: 2px;
         background-color: #0275d8;
         z-index: 1;
    }
    }
    
</style>



