const stepList = {
    namespaced: true,
    state: {
        metalData:[],
        weldingData:[],
        WeldingTechnologyData:[],
        guideData:[],
        selectedGuideData:[],
        selectedGuideMethods:[],
        selectedGuideDataIsEmpty:true,
    },
    getters: {
    },
    mutations: {
        // 母材数据
        updateMetalData(state,data){
            state.metalData=data;
        },
        // 专家焊接方法数据
        updateWeldingData(state,data){
            state.weldingData=Object.keys(data.method);
            // 获取导购的初始数据
            const {splash,model,speed}=data;
            state.guideData={splash,model,speed};
        },
        // 焊接工艺数据
        updateWeldingTechnologyData(state,data){
            state.WeldingTechnologyData=data;
            // console.log(state.WeldingTechnologyData)
        },
        // 导购请求焊接方法数据
        updateGuideData(state,data){
            state.selectedGuideData=data.method;
            state.selectedGuideMethods=Object.keys(data.method);
            // 判断是否有guide焊接方法数据
            if(state.selectedGuideMethods.length >0){
                state.selectedGuideDataIsEmpty=false;
            }    
        },
        changeSelectedGuideDataIsEmpty(state,data){
            state.selectedGuideDataIsEmpty=data;
        }
    },
    actions: {
        // 母材的数据
        updateMetal({commit}) {
            var that = this._vm;
            var loader = that.$loading({fullscreen: true});
            let url = that.$urlConfig.config.stepListUrl + '/solution/v1/metal';
            that.$http({
                methods: 'get',
                url: url
            }).then((res) => {
                loader.close();
                commit('stepList/updateMetalData',res.data.data,{root:true});
            }).catch((error) => {

            });
        },
        // 专家焊接方法的数据
        updateWelding({store,commit},param) {
            var that = this._vm;
            const {metal,success} = param;
            var loader = that.$loading({fullscreen: true});
            let url = that.$urlConfig.config.stepListUrl + '/solution/v1/metal/'+metal;
            that.$http({
                methods: 'get',
                url: url
            }).then((res) => {
                loader.close();
                commit('stepList/updateWeldingData',res.data.data,{root:true});
                success(Object.keys(res.data.data.method)[0]);
            }).catch((error) => {

            });
        },
        // 焊接方法选择工艺数据
        updateWeldingTechnology({commit},param) {
            var that = this._vm;
            var loader = that.$loading({fullscreen: true});
            let url = that.$urlConfig.config.stepListUrl + '/solution/v1/welding/'+param;
            that.$http({
                methods: 'get',
                url: url
            }).then((res) => {
                loader.close();
                commit('stepList/updateWeldingTechnologyData',res.data.data,{root:true});
            }).catch((error) => {

            });
        },
        // 导购的工艺效果数据
        updateGuide({state,commit},param){
            var that = this._vm;
            var loader = that.$loading({fullscreen: true});
            var qs=require('qs');
            const {splash,model,speed} = param;
            const data = {
                splash,model,speed
            };
            let url = `${that.$urlConfig.config.stepListUrl}/solution/v1/metal/${param.metal}?${qs.stringify(data)}`;
            that.$http({
                methods: 'get',
                url: url
            }).then((res) => {
                loader.close();
                commit('stepList/updateGuideData',res.data.data,{root:true});
            }).catch((error) => {
                commit('stepList/changeSelectedGuideDataIsEmpty',true,{root:true});
            });
        }
    }
};

export default stepList;


