<template>
    <div>
        <el-row class="goodsInfo" v-for="(item,index) in productList" :key="index">
            <el-col :span="9">
                <div class="goodsInfoLeft">
                    <img :src="item.goods.product.cover_url" alt="">
                </div>
                <h5> {{item.goods.product.product_name}}</h5>
            </el-col>
            <el-col :span="5" class="lineHeight_150">&yen; {{singlePrice[index]}}</el-col>
            <el-col :span="5" class="lineHeight_150">{{item.quantity}}</el-col>
            <el-col :span="5" class="lineHeight_150"><strong>&yen; {{singleAllPrice[index]}}</strong></el-col>
        </el-row>
    </div>
</template>
<script>
import {mapState} from 'vuex';
export default {
    data(){
        return {
            singlePrice:[],
            singleAllPrice:[]
        }
    },
    methods:{
        changePrice:function(count,prices){
            var currentPrice;
            prices.forEach((item, index) => {
                if(count >= item.min_quantity && count <= item.max_quantity) {
                    currentPrice = item.price;
                } else if (count < prices[0].min_quantity) {
                    currentPrice = prices[0].price;
                } else if (count > prices[prices.length-1].max_quantity) {
                    currentPrice =  prices[prices.length - 1].price;
                }
            });
            return currentPrice;
        }
    },
    computed:{
        ...mapState({
            productList:state=>state.order.productList
        })
    },
    watch:{
        productList:function(newvalue,oldvalue){
            this.singlePrice = [];
            this.singleAllPrice = [];
            var totalMoney = 0;
            var totalCount = 0;
            newvalue.forEach((item,index) => {
                this.singlePrice.push(this.changePrice(item.quantity,item.goods.prices));
                let totalPrice = Math.floor((parseFloat(this.singlePrice[index])*item.quantity)*100)/100;
                this.singleAllPrice.push(totalPrice);
                totalMoney += totalPrice;
                totalCount += item.quantity;
                this.singleAllPrice[index] = this.singleAllPrice[index].toFixed(2);
            })
            var totalCategory = newvalue.length;
            var param = {
                totalMoney,
                totalCategory,
                totalCount
            }
            this.$emit('changeBillingInfo',param)
        }
    }
}
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.goodsInfo {
    border-bottom: 1px solid #ccc;
    >div {
        position: relative;
        height: 150*@fontSize;
        text-align: center;
        font-size: 14*@fontSize;
        strong {
            color: @baseColor;
        }
    }
    .goodsInfoLeft {
        width: 120*@fontSize;
        height: 120*@fontSize;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
        img {
            width: 100%;
            height: 100%;
        }
    }
    h5 {
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 10*@fontSize;
        width: 60%;
        margin: 0;
        padding: 0;
        text-align: left;
        font-size: 14*@fontSize;
        font-weight: 700;
    }
    .lineHeight_150 {
        line-height: 150*@fontSize;
    }
}

</style>



