<template>
  <div class="productInfo">
      <div class="container">
          <div class="breadCrumbTitle">
              <BreadCrumb :breadCrumbData = "productInfoData"/>
          </div>
          <div class="clearfix">
              <div class="zoom">
                  <Zoom :zoomData = "productInfoData"/>
              </div>
              <div class="productInfoRight">
                  <div class="productDetail">
                        <div v-for="(item, index) in productInfoData" :key="index">
                            <h4 title="infoData.goods_name">{{item.goods_name}}</h4>
                            <div class="price">
                                <span>价格：<strong>&yen; {{prices[index]}}</strong></span>
                            </div>
                            <div class="diffNumPrice">
                                <ul class="differentNum">
                                    <li v-for="(itemprice, index) in item.prices" :key="index" v-if="index < 3"> 
                                        <span class="num">{{itemprice.min_quantity}} {{item.sales_unit}} - {{itemprice.max_quantity}} {{item.sales_unit}}</span>
                                        <span class="numPrice">&yen; {{itemprice.price}}</span>
                                        <span class="rect"></span>
                                    </li>
                                </ul> 
                            </div>
                            <ul class="detailAndBuy">
                                <li>订货编码 : <span class="color_red">{{item.gno}}</span></li>
                                <li>品&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;牌 : <span>{{item.brand_name}}</span></li>
                                <li>起&nbsp;&nbsp;订&nbsp;&nbsp;量 : <span>{{item.min_buy}} {{item.sales_unit}}</span></li>
                                <li>发&nbsp;&nbsp;货&nbsp;&nbsp;期 : <span>{{item.lead_time}}</span><a href="javascript">发货交期详述>></a></li> 
                                <li>当前库存 : <span>{{item.stock}}</span></li>
                            </ul>
                            <div class="numChange">
                                <CountChange 
                                :min="item.min_buy" :max="item.stock" 
                                :quantity="item.min_buy" :cartId="item.id" 
                                :prices="item.prices" @changeCount="changePrice" 
                                :index="index"/>
                            </div>
                            <div class="addShopCar">
                                <button class="addToShoppingCar" @click="addShopcar" :data-gno="item.gno">
                                    <span class="iconfont icon-caigou-xianxing"></span>加入购物车
                                </button>
                                <button class="saveGoods" @click="save" :id="item.gno"><span class="iconfont icon-mn_shoucang"></span>收藏商品</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
          <div class="productInfoSummary">
              <ProductInfoSummary />
          </div>
      </div>
  </div>
</template>
<script>
import BreadCrumb from "../components/breadCrumb/main/BreadCrumb";
import Zoom from "../components/zoom/main/Zoom";
import ProductInfoSummary from "../components/productInfoDetail/main/ProductInfoSummary";
import CountChange from "../components/countChange/main/CountChange.vue";
import {mapState,mapActions} from 'vuex';
import swal from 'sweetalert';//弹出消息提示框
import "../assets/sweetAlert/sweetAlert.css";
export default {
    props:['id'],
    components:{
        BreadCrumb,
        Zoom,
        ProductInfoSummary,
        CountChange
    },
    mounted(){
        var loader = this.$loading({fullScreen:true});
         this.updateProductInfoAsync({
             params:this.id,
             success:(res)=>{
                 loader.close();
             },
             fail:(res)=>{
                const h = this.$createElement;
                this.$message({
                  message: h('p', null, [
                    h('span', null, '请求数据失败，请重试 '),
                    h('i', { style: 'color: teal' },)
                  ])
                });
                 loader.close();
             },
             error:(error)=>{
                 const h = this.$createElement;
                this.$message({
                  message: h('p', null, [
                    h('span', null, '请求服务失败，请重试'),
                    h('i', { style: 'color: teal' },)
                  ])
                });
                loader.close();
             }
         });     
    },
    data(){
        return {
            prices:[],
            num:[]
        }
    },
    methods:{
       ...mapActions({
           updateProductInfoAsync:'productInfo/updateProductInfoAsync',
           addToShopcar:'shopcar/addToShopcar',
           saveGoods:'shopcar/saveGoods',
           
       }),
       changePrice(param){
           this.$set(this.prices,param.index, param.price);
           this.$set(this.num,param.index, param.shopcarQuantity);
       },
       addShopcar:function(e){
           var gno = e.currentTarget.dataset.gno;
           var quantity = this.num[0]
           this.addToShopcar({
               params:{
                   gno:gno,
                   quantity:quantity
               },success:function (res){
                   let resCode = parseInt(res.data.rescode);
                   console.log(res)
                   if(resCode <= 10000) {
                       swal({
                            title: "加入购物车成功",
                            icon: "success",
                            buttons: ["继续购物", "去购物车结算"],
                        })
                        .then((willDelete) => {
                          if (willDelete) {
                              this.$router.push('/shopcar');
                          } else {
                                
                          }
                        });
                   } else {
                        var msg = res.data.msg;
                        var tipInfo;
                        if(msg.indexOf(":")!=-1) {
                             tipInfo = msg.split(":")[1]
                        } else {
                           tipInfo = "加入购物车失败,请重试"
                        }
                       swal({title: "",
                            text: tipInfo,
                            icon: "info",
                            button: "关闭",});
                   }
               }.bind(this),error:function (res){
                //    swal({title: "",
                //         text: "请求失败",
                //         icon: "info",
                //         button: "关闭",});
                    }
           })
       },
       save:function (e){//收藏商品
            var goodsGno = e.currentTarget.id;
                this.saveGoods({
                    params:goodsGno,
                    success:(res)=>{
                        
                       var msg = res.data.msg;
                        var tipInfo;
                        if(msg.indexOf(":")!=-1) {
                             tipInfo = msg.split(":")[1]
                        } else {
                           tipInfo = "加入收藏成功"
                        }
                       swal({title: "",
                            text: tipInfo,
                            icon: "info",
                            button: "关闭",});
                        // console.log(res)
                    },
                    error:(error)=>{
                        console.log(error);
                    }
                })
        },
    },
    computed:{
        ...mapState({
            productInfoData:state=>state.productInfo.productInfoData
        })
    },
    watch:{
        productInfoData:function (newvalue,oldvalue) {
            this.prices.push(newvalue[0].first_price);
            this.num.push(newvalue[0].min_buy);
        }
    }
}
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.productInfo {
    margin: 30*@fontSize;
    .breadCrumbTitle {
        position: relative;
         &::after {
            content: "";
             position: absolute;
             left: 0;
             bottom: 0;
             width: 100%;
             height: 2px;
             background-color: #0275d8;
             z-index: 1;
        }
    }
    .zoom {
        float: left;
        width: 35%;
        margin-right: 5%;
    }
    .productInfoRight {
        float: right;
        width: 60%;
        position: relative;
        padding-bottom: 10px;
        &::after {
            content: "";
             position: absolute;
             left: 0;
             bottom: 0;
             width: 100%;
             height: 1px;
             background-color: #fff;
             z-index: 1;
        }
    }
    .productInfoSummary {
        margin: 30*@fontSize 0;
    }
}
.productDetail {
    margin-top: 30*@fontSize;
    h4 {
        color:#333;
        font-weight: 700;
        font-size: 16*@fontSize;
    }
    .price {
        height: 40*@fontSize;
        line-height: 40*@fontSize;
        padding-left:10*@fontSize;
        background: url("../assets/images/bgPic.png") no-repeat;
        background-size: cover;
        margin-bottom: 20*@fontSize;
        color: #666;
        font-size: 14*@fontSize;
        strong {
            color: #0275d8;
            font-weight: 700;
            font-family: Arial;
            font-size: 16*@fontSize;
            margin-left: 15*@fontSize;
        }
    }
    .diffNumPrice {
        position: relative;
        margin-bottom: 20px;
        .differentNum {
            li {
                position: relative;
                height: 26px;
                line-height: 26px;
                display: inline-block;
                position: relative;
                margin-right: 2*@fontSize;
                font-size: 14*@fontSize;
                &:last-child {
                    margin-right: 0;
                }
                .rect {
                    position: absolute;
                    left: 50%;
                    top: 0;
                    border-width: 27px 25px 0px 0px;
                    border-style: solid;
                    border-color: #0275d8 #fff #0275d8 #0275d8;
                }
                .num {
                    background-color: #0275d8;
                    color: #fff;
                    float: left;
                    width: 128px;
                    height: 28px;
                    line-height: 28px;
                    text-align: center;
                    overflow: hidden;
                }
                .numPrice {
                    color: #0275d8;
                    width: 100px;
                    float: left;
                    text-align: center;
                    border: 1px solid #0275d8;
                    border-left: none;
                    overflow: hidden;
                }
            }
        }
    }
    .detailAndBuy {
        margin-bottom: 15*@fontSize;
        padding-bottom: 20*@fontSize;
        li {
            padding: 6px 0;
            font-size: 14*@fontSize;
            span {
                margin-left: 5px;
            }
            a {
                margin-left: 10px;
                color: @baseColor;
            }
        }
    }
    .numChange {
        width: 150*@fontSize;
    }
}
.addShopCar button {
    margin-top: 6px;
    width: 110*@fontSize;
    height: 40*@fontSize;
    font-size:14*@fontSize;
    transition:all 0.3s;
    border: none;
    background-color: #0275d8;
    border: 1px solid #0275d8;
    color: #fff;
    margin-right: 30*@fontSize;
    cursor: pointer;
    .iconfont {
        margin-right: 5px;
        font-size: 15*@fontSize;
    }
}
.addShopCar .saveGoods {
    background-color: rgba(2, 117, 216, 0.03);
    border: 1px solid #0275d8;
    color: #0275d8;
}
.swal-button {
    background-color: @baseColor!important;
}
</style>



