const productInfo = {
    namespaced: true,
    state:{
        productInfoData:[]
    },
    getters:{

    },
    mutations:{
        updateproductInfo(state,data){//更新详情页数据
            state.productInfoData = data;
        },
    },
    actions:{
        updateProductInfoAsync(store,param){//获取商品详情页数据
            var that = this._vm;
            let url = that.$urlConfig.config.productUrl + "/api/goods/" + param.params;
            that.$http({
                methods:'get',
                url:url
            }).then((res)=>{
                var rescode = parseInt(res.data.rescode);
                if(rescode <= 10000) {
                    var arr = [];
                    arr.push(res.data.data);
                    store.commit('updatePrice',arr,{root:true});
                    store.commit('updateproductInfo',arr);
                    param.success(res)
                } else {
                    param.fail(res)
                }
            }).catch((error) => {
                param.error(error);
            }) 
        },
    }
}
export default productInfo;


