<template>
    <div class="indexPage">
        <div class="industryApplication">
            <div class="container">
                <el-row :gutter="30">
                    <el-col :span="18">
                        <IndustryApplication />
                    </el-col>
                    <el-col :span="6">
                        <IndustryUser />
                    </el-col>
                </el-row>
            </div>
        </div>
        <div class="productType">
            <div class="container">
                <el-row>
                    <ProductType />
                </el-row>
            </div>
        </div>
        <div class="productDetail">
            <div class="container">
                <el-row :gutter = "40">
                    <el-col :span="18">
                        <ProductDetail :indexProduct = "indexProduct"/>
                    </el-col>
                    <el-col :span="6">
                        <ProductSalesRank :indexProduct = "indexProduct"/>
                        <GuessLike :indexProduct = "indexProduct"/>
                    </el-col>
                </el-row>
            </div>
        </div>
        <div class="brands">
            <div class="container">
                <el-row>
                    <Brands />
                </el-row>
            </div>
        </div>
        <div class="teachInfo">
            <div class="container">
                <TeachInfo />
            </div>
        </div>
    </div>
    
</template>
<script>
import IndustryApplication from "../components/IndustryApplication/IndustryApplication";
import IndustryUser from "../components/IndustryApplication/IndustryUser";
import Brands from "../components/brands/main/Brand";
import ProductType from "../components/ProductType/main/ProductType";
import ProductDetail from "../components/ProductDetail/main/ProductDetail";
import ProductSalesRank from "../components/ProductDetail/main/ProductSalesRank";
import GuessLike from "../components/ProductDetail/main/GuessLike";
import TeachInfo from "../components/TeachInfo/main/TeachInfo";
import {mapState,mapActions} from 'vuex';
export default {
  name:"indexPage",
  components:{
      IndustryApplication,
      IndustryUser,
      Brands,
      ProductType,
      ProductDetail,
      ProductSalesRank,
      GuessLike,
      TeachInfo
  },
  mounted(){
      var loader = this.$loading({fullScreen: true});
      this.updateIndexProductAsync({
          success:(res)=>{
            //   console.log(res);
              loader.close();
          },
          fail:(res)=>{
              const h = this.$createElement;
                this.$message({
                  message: h('p', null, [
                    h('span', null, '请求数据失败，请重试 '),
                    h('i', { style: 'color: teal' },)
                  ])
                });
                 loader.close();
          },
          error:(error)=>{
            //   const h = this.$createElement;
            //     this.$message({
            //       message: h('p', null, [
            //         h('span', null, '请求服务失败，请重试 '),
            //         h('i', { style: 'color: teal' },)
            //       ])
            //     });
                loader.close();
          }
      })
  },
  data(){
      return {

      }
  },
  methods:{
      ...mapActions({
          updateIndexProductAsync:"indexPage/updateIndexProductAsync"
      }),
  },
  computed:{
      ...mapState({
          indexProduct:state=>state.indexPage.indexProduct
      }),
  },
}
</script>
<style lang="less" scoped>
    @baseColor: #0275d8;
    @fontSize: 1/16rem;
  .industryApplication {
      background-color: #f9f9f9;
      padding: 30*@fontSize 0;
      position: relative;
      &::after {
        content: "";
         position: absolute;
         left: 0;
         bottom: 0;
         width: 100%;
         height: 1px;
         background-color: #f9f9f9;
         z-index: 1;
    }
  }
  .brands {
      margin: 30*@fontSize 0;
  }
  .productDetail {
      margin: 30*@fontSize 0;
  }
  .teachInfo {
      margin: 30*@fontSize 0;
  }
</style>



