<template>
  <div class="productBigImg">
        <el-row :gutter="30">
            <el-col :span=num v-for="(item, index) in productList" :key="index">
                <ProductDescription :pageIndex="productDetailShow" :productInfo="item"/>
            </el-col>
        </el-row>
    </div>
</template>
<script>
import ProductDescription from "../../productDescription/main/ProductDescription";
import {mapState, mapActions} from 'vuex'
export default {
  components:{
      ProductDescription
  },
  data(){
      return {
          productDetailShow:false,
          num:6
      }
  },
  mounted(){
      if(this.$route.path=='/SearchList/bigImgList'){
          this.num=8;
      }else{
          this.num=6;
      }
  },
  computed:{
      ...mapState({
          productList:state=>state.productList.productList
      })
  }
}
</script>
<style lang="less" scoped>

</style>



