<template>
  <div class="container">
        <div class="orderTip">
			    <p>订单已经提交，请尽快完成支付！</p>
			    <p>请您在提交订单7天内完成支付,否则订单会自动取消</p>
        </div>
        <div class="orderinfo">
            <Title title="订单信息" englishTitle="" iconClass="icon-caidan"/>
            <ul class="orderInfoDetail">
              <li><span class="orderNum">订&nbsp;&nbsp;单&nbsp;&nbsp;号：</span> <span>{{orderInfo.order_sn}}</span>
                  <router-link :to="'/orderDetail/'+orderInfo.id">查看订单详情 >></router-link> 
              </li> 
              <li><span class="orderTime">下单时间：</span> <span>{{orderTime}}</span></li> 
              <li><span class="invoiceAddress">收货地址：</span> <span>{{orderInfo.address}}</span></li> 
              <li><span class="invoiceType">发票类型：</span> <span>{{receiptType}}</span> </li> 
              <li><span class="invoiceTitle">发票抬头：</span> <span>{{orderInfo.receipt_title||"您选择不开发票"}}</span></li></ul>
        </div>
        <div class="payMethod">
            <Title title="支付方式" englishTitle="" iconClass="icon--fufeiyuedu"/>
            <div>
              <img src="../assets/images/inlinePayment.jpg" alt="">
          </div>
        </div>
        <div class="totalMoney">
            <span>应付金额: <strong>&yen; {{orderInfo.total_money}}</strong></span>
            <el-button type="primary" class="payNow" @click="payNow">立即支付</el-button>
        </div>
        <SurePayMoney :surePay="surePay" @changeSurePay="changeSurePay"/>
  </div>
</template>
<script>
import Title from "../components/title/title.vue";
import SurePayMoney from '../components/SurePayMoney/SurePayMoney.vue';
import { mapState , mapActions} from 'vuex';
import moment from 'moment';
export default {
    components:{
      Title,
      SurePayMoney
    },
    props:{
        id:{
            type:String,
            required:true
        }
    },
    data(){
        return {
            orderTime:"",
            receiptType:'',
            surePay:false
        }
    },
    mounted(){
        var loader = this.$loading({ fullscreen: true });
        this.updateorderInfoAsync({
            orderId: this.id,
            success:function(res){
                this.orderTime = this.formatTime(res.data.data.add_time*1000);
                if(res.data.data.receipt_type === 2) {
                    this.receiptType = "增值税发票";
                } else if (res.data.data.receipt_type === 3) {
                    this.receiptType = "您选择不开发票"
                }
                loader.close();
            }.bind(this),
            fail:function (res){
                const h = this.$createElement;
                this.$message({
                    message: h('p', null, [
                        h('span', null, '获取订单信息失败,请重试'),
                        h('i', { style: 'color: teal' },)
                    ]),
                    center: true
                })
                loader.close();
            }.bind(this),
            error:function (error) {
                // const h = this.$createElement;
                // this.$message({
                //     message: h('p', null, [
                //         h('span', null, '请求服务失败,请重试'),
                //         h('i', { style: 'color: teal' },)
                //     ]),
                //     center: true
                // })
                loader.close();
            }.bind(this)
        })
    },
    methods:{
        ...mapActions({
            updateorderInfoAsync:"order/updateorderInfoAsync"
        }),
        formatTime:function (time) {
			return moment(time).format("YYYY/MM/DD HH:mm:ss"); 
        },
        payNow(){
            this.surePay = true;
        },
        changeSurePay(param){
            this.surePay = false;
        }
    },
    computed:{
        ...mapState({
            orderSuccessCode:state=>state.order.orderSuccessCode,
            orderInfo:state=>state.order.orderInfo
        })
    },
    watch:{
        orderSuccessCode:function(newvalue,oldvalue){

        }
    }
}
</script>
<style lang="less" scoped>
@fontSize: 1/16rem;
@baseColor: #0275d8;
.orderTip {
    padding: 15*@fontSize;
    border: 1px solid #ddd;
    border-bottom:none;
    margin-top: 30*@fontSize;
    font-size: 14*@fontSize;
    color: #666;
    position: relative;
    &::after {
        content: "";
         position: absolute;
         left: 0;
         bottom: 0;
         width: 100%;
         height: 1px;
         background-color: #ddd;
         z-index: 1;
    }
}
.orderinfo {
  margin: 30*@fontSize 0;
  .orderInfoDetail {
      margin-top: 30*@fontSize;
      li {
         height: 50*@fontSize;
        line-height: 50*@fontSize;
        font-size: 14*@fontSize;
         color: #333;
         a {
           color: @baseColor;
           margin-left: 20*@fontSize;
         }
      }
  }
}
.payMethod {
    text-align: left;
    img {
        width: 150px;
        height: 76px;
        margin: 25px 0;
    }
}
.totalMoney {
  text-align: right;
  margin-bottom: 50*@fontSize;
  strong {
    color: @baseColor;
    font-weight: 600;
    margin-left: 10*@fontSize;
  }
  .payNow {
    background-color: @baseColor;
    border-color: @baseColor;
    margin-left: 20*@fontSize;
  }
}
</style>



