const seriesInfo = {
    namespaced: true,
    state:{
        seriesInfoData:[], // 产品详情
        seriesModalList:[], //产品型号列表
        productInfo:[],//系列商品详情
    },
    getters:{

    },
    mutations:{
        updateSeriesInfoData(state,data){
            state.seriesInfoData = data;
        },
        updateSeriesModalList(state,data){
            state.seriesModalList = data;
        },
        updateProductInfo(state,data){
            state.productInfo = data;
        }
    },
    actions:{
        updateSeriesInfoDataAsync(store, params) {
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + "/products/"+params.param;
            that.$http({
                method:'get',
                url:url,
            }).then((res)=>{
                var resCode = parseInt(res.data.rescode);
                if(resCode <= 10000) {
                    let arr = [];
                    arr.push(res.data.data);
                    store.commit('updateSeriesInfoData',arr);
                    params.success(res);
                } else {
                    params.fail(res);
                }

            }).catch((error)=>{
                params.error(error);
            })
        },
        updateSeriesModalList(store, params) {
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + "/products/"+params.param1+"/models?" + params.param2;
            that.$http({
                method:'get',
                url:url,
            }).then((res)=>{
                var resCode = parseInt(res.data.rescode);
                if(resCode <= 10000) {
                    store.commit('updateSeriesModalList',res.data.data);
                    params.success(res);
                } else {
                    params.fail(res);
                }

            }).catch((error)=>{
                params.error(error);
            })
        },
        updateProductInfoAsync(store, params) {
            var that = this._vm;
            let url = that.$urlConfig.config.seriesUrl + "/goods/"+params.param;
            that.$http({
                method:'get',
                url:url,
            }).then((res)=>{
                var resCode = parseInt(res.data.rescode);
                var arr = [];
                if(resCode <= 10000) {
                    arr.push(res.data.data);
                    store.commit('updatePrice', arr, { root: true });
                    store.commit('updateProductInfo',arr);
                    params.success(res);
                } else {
                    params.fail(res);
                }

            }).catch((error)=>{
                params.error(error);
            })
        }
    }

}

export default seriesInfo;


