<template>
    <div class="step-list container">
        <div class="step-title">
            <h3>工业魔方</h3>
        </div>
        <StepTable />
    </div>
</template>
<script>
import StepTable from "../components/StepTable/main/StepTable.vue";
import { mapState, mapMutations, mapActions, mapGetters } from "vuex";
export default {
  name: "SteplList",
  components: {
    StepTable
  },
};
</script>

<style lang="less" scoped>
@baseColor: #0275d8;
@fontSize: 1/16rem;
.step-list {
  padding: 0;
  min-height: 650px;
  margin: 50px auto 300px;
  border: 1px solid rgba(0, 0, 0, 0.125);
  border-bottom: none;
  box-shadow: 0 16px 24px 2px rgba(0, 0, 0, 0.14),
    0 6px 30px 5px rgba(0, 0, 0, 0.12), 0 8px 10px -5px rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  position: relative;
  &::after {
        content: "";
         position: absolute;
         left: 0;
         bottom: 0;
         width: 100%;
         height: 1px;
         background-color: rgba(0, 0, 0, 0.125);
         z-index: 1;
    }
  > .step-title {
    text-align: center;
    padding: 35px 0 35px;
    h3 {
      font-size: 1.825em;
      line-height: 1.4em;
      font-weight: 700;
      padding: 0 0 15px 0;
      margin: 0;
    }
    h5 {
      font-size: 1.25em;
      font-family: "Roboto", "Helvetica", "Arial", sans-serif;
      font-weight: 300;
      padding: 5px 0 15px 0;
      margin: 0;
    }
  }
}
</style>




